import { useState, useEffect } from 'react'
import { Link, useLocation, useNavigate } from 'react-router-dom'
import { motion, AnimatePresence } from 'framer-motion'
import { useTranslation } from 'react-i18next'
import {
  Bars3Icon,
  XMarkIcon,
  SparklesIcon,
  UserCircleIcon,
  ChevronDownIcon
} from '@heroicons/react/24/outline'
import { useAuthStore } from '../../store/authStore'
import LanguageSwitcher from '../ui/LanguageSwitcher'
import NotificationBell from '../notifications/NotificationBell'

export default function Navbar() {
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false)
  const [isUserMenuOpen, setIsUserMenuOpen] = useState(false)
  const [navbarOpacity, setNavbarOpacity] = useState(0)
  const { user, isAuthenticated, logout } = useAuthStore()
  const location = useLocation()
  const navigate = useNavigate()
  const { t } = useTranslation()

  // Scroll-based opacity control - only on homepage
  useEffect(() => {
    if (location.pathname !== '/') {
      setNavbarOpacity(1)
      return
    }

    const handleScroll = () => {
      // Find the "Why Choose BeautyCita" section (Features section)
      const featuresSection = document.querySelector('section:nth-of-type(2)')

      if (!featuresSection) {
        setNavbarOpacity(0)
        return
      }

      const rect = featuresSection.getBoundingClientRect()
      const navbarHeight = 64 // h-16 = 64px

      // Calculate opacity based on how close the section is to the top
      // Start fading in when section is 200px from top, fully opaque when it touches navbar
      const fadeDistance = 200
      const distanceFromTop = rect.top - navbarHeight

      if (distanceFromTop <= 0) {
        // Section has reached or passed the navbar
        setNavbarOpacity(1)
      } else if (distanceFromTop >= fadeDistance) {
        // Section is far away
        setNavbarOpacity(0)
      } else {
        // Section is in the fade zone - calculate smooth opacity
        const opacity = 1 - (distanceFromTop / fadeDistance)
        setNavbarOpacity(opacity)
      }
    }

    // Initial check
    handleScroll()

    // Listen to scroll events
    window.addEventListener('scroll', handleScroll, { passive: true })

    return () => window.removeEventListener('scroll', handleScroll)
  }, [location.pathname])

  const navigation = [
    { name: t('nav.home'), href: '/' },
    { name: t('nav.services'), href: '/services' },
    { name: t('nav.stylists'), href: '/stylists' },
    { name: t('nav.blog'), href: '/blog' },
  ]

  // Add dashboard link to main navigation for authenticated users
  const getNavigationWithDashboard = () => {
    const baseNav = [...navigation]

    if (isAuthenticated && user) {
      // Insert dashboard link after services but before stylists
      const dashboardLink = {
        name: 'Dashboard',
        href: user.role === 'STYLIST' ? '/dashboard' : user.role === 'ADMIN' ? '/admin/dashboard' : '/profile'
      }
      baseNav.splice(2, 0, dashboardLink) // Insert at index 2 (after services)
    }

    return baseNav
  }

  const userNavigation = [
    { name: t('nav.profile'), href: '/profile' },
    { name: t('nav.bookings'), href: '/bookings' },
    ...(user?.role === 'STYLIST' ? [{ name: t('nav.dashboard'), href: '/dashboard' }] : []),
    ...(user?.role === 'ADMIN' ? [{ name: 'Admin', href: '/admin' }] : []),
  ]

  const handleLogout = () => {
    logout()
    setIsUserMenuOpen(false)
    navigate('/', { replace: true })
  }

  const isActivePage = (href: string) => {
    if (href === '/') {
      return location.pathname === '/'
    }
    return location.pathname.startsWith(href)
  }

  return (
    <nav
      className="sticky top-0 z-50 transition-all duration-300"
      style={{
        backgroundColor: `rgba(255, 255, 255, ${navbarOpacity})`,
        borderBottom: navbarOpacity > 0 ? `1px solid rgba(229, 231, 235, ${navbarOpacity})` : 'none',
        boxShadow: navbarOpacity > 0 ? '0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06)' : 'none',
        backdropFilter: navbarOpacity > 0 ? 'blur(8px)' : 'none'
      }}
    >
      <div className="container-responsive">
        <div className="flex justify-between h-16">
          {/* Logo */}
          <div className="flex items-center">
            <Link to="/" className="flex items-center space-x-2 group">
              <div className="relative">
                <SparklesIcon
                  className="h-8 w-8 group-hover:text-primary-700 transition-colors duration-200"
                  style={{
                    color: navbarOpacity < 0.5 ? 'rgba(255, 255, 255, 0.95)' : 'rgb(147, 51, 234)'
                  }}
                />
                <div className="absolute inset-0 animate-pulse-soft">
                  <SparklesIcon
                    className="h-8 w-8 opacity-50"
                    style={{
                      color: navbarOpacity < 0.5 ? 'rgba(255, 255, 255, 0.3)' : 'rgb(216, 180, 254)'
                    }}
                  />
                </div>
              </div>
              <span
                className="text-xl font-serif font-bold gradient-text transition-all duration-300"
                style={{
                  color: navbarOpacity < 0.5 ? 'rgba(255, 255, 255, 0.95)' : undefined,
                  textShadow: navbarOpacity < 0.5 ? '0 2px 4px rgba(0,0,0,0.3)' : 'none'
                }}
              >
                BeautyCita
              </span>
            </Link>
          </div>

          {/* Desktop Navigation */}
          <div className="hidden md:flex items-center space-x-8">
            {getNavigationWithDashboard().map((item) => (
              <Link
                key={item.name}
                to={item.href}
                className={`px-3 py-2 text-sm font-medium transition-all duration-300 ${
                  item.name === 'Dashboard'
                    ? 'bg-primary-50 text-primary-700 rounded-md hover:bg-primary-100'
                    : ''
                }`}
                style={{
                  color: navbarOpacity < 0.5
                    ? isActivePage(item.href)
                      ? 'rgba(255, 255, 255, 1)'
                      : 'rgba(255, 255, 255, 0.9)'
                    : isActivePage(item.href)
                      ? 'rgb(147, 51, 234)'
                      : 'rgb(55, 65, 81)',
                  borderBottom: isActivePage(item.href)
                    ? `2px solid ${navbarOpacity < 0.5 ? 'rgba(255, 255, 255, 0.9)' : 'rgb(147, 51, 234)'}`
                    : 'none',
                  textShadow: navbarOpacity < 0.5 ? '0 1px 2px rgba(0,0,0,0.3)' : 'none'
                }}
              >
                {item.name}
              </Link>
            ))}
          </div>

          {/* User Menu / Auth Buttons */}
          <div className="hidden md:flex items-center space-x-4">
            <LanguageSwitcher />
            {isAuthenticated && user && <NotificationBell />}
            {isAuthenticated && user ? (
              <div className="relative">
                <button
                  onClick={() => setIsUserMenuOpen(!isUserMenuOpen)}
                  className="flex items-center space-x-2 text-gray-700 hover:text-primary-600 transition-colors duration-200"
                >
                  <div className="h-8 w-8 rounded-full bg-primary-100 flex items-center justify-center">
                    {user.profilePictureUrl ? (
                      <img
                        src={user.profilePictureUrl}
                        alt={user.firstName}
                        className="h-8 w-8 rounded-full object-cover"
                      />
                    ) : (
                      <UserCircleIcon className="h-5 w-5 text-primary-600" />
                    )}
                  </div>
                  <span className="text-sm font-medium">
                    {user.firstName}
                  </span>
                  <ChevronDownIcon className="h-4 w-4" />
                </button>

                <AnimatePresence>
                  {isUserMenuOpen && (
                    <motion.div
                      initial={{ opacity: 0, scale: 0.95 }}
                      animate={{ opacity: 1, scale: 1 }}
                      exit={{ opacity: 0, scale: 0.95 }}
                      transition={{ duration: 0.2 }}
                      className="absolute right-0 mt-2 w-48 bg-white rounded-lg shadow-lg border border-gray-100 py-1"
                    >
                      {userNavigation.map((item) => (
                        <Link
                          key={item.name}
                          to={item.href}
                          onClick={() => setIsUserMenuOpen(false)}
                          className="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-50 transition-colors duration-200"
                        >
                          {item.name}
                        </Link>
                      ))}
                      <hr className="my-1" />
                      <button
                        onClick={handleLogout}
                        className="block w-full text-left px-4 py-2 text-sm text-red-600 hover:bg-red-50 transition-colors duration-200"
                      >
                        {t('nav.logout')}
                      </button>
                    </motion.div>
                  )}
                </AnimatePresence>
              </div>
            ) : (
              <div className="flex items-center space-x-3">
                <Link
                  to="/login"
                  className="font-medium transition-all duration-300"
                  style={{
                    color: navbarOpacity < 0.5 ? 'rgba(255, 255, 255, 0.95)' : 'rgb(55, 65, 81)',
                    textShadow: navbarOpacity < 0.5 ? '0 1px 2px rgba(0,0,0,0.3)' : 'none'
                  }}
                >
                  {t('nav.login')}
                </Link>
                <Link
                  to="/register"
                  className="btn btn-primary"
                >
                  {t('nav.register')}
                </Link>
              </div>
            )}
          </div>

          {/* Mobile menu button */}
          <div className="md:hidden flex items-center space-x-2">
            <LanguageSwitcher />
            {isAuthenticated && user && <NotificationBell />}
            <button
              onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
              className="p-2 transition-all duration-300"
              style={{
                color: navbarOpacity < 0.5 ? 'rgba(255, 255, 255, 0.95)' : 'rgb(55, 65, 81)'
              }}
            >
              {isMobileMenuOpen ? (
                <XMarkIcon className="h-6 w-6" />
              ) : (
                <Bars3Icon className="h-6 w-6" />
              )}
            </button>
          </div>
        </div>
      </div>

      {/* Mobile Menu */}
      <AnimatePresence>
        {isMobileMenuOpen && (
          <motion.div
            initial={{ opacity: 0, height: 0 }}
            animate={{ opacity: 1, height: 'auto' }}
            exit={{ opacity: 0, height: 0 }}
            transition={{ duration: 0.3 }}
            className="md:hidden bg-white border-t border-gray-100"
          >
            <div className="px-4 py-4 space-y-2">
              {getNavigationWithDashboard().map((item) => (
                <Link
                  key={item.name}
                  to={item.href}
                  onClick={() => setIsMobileMenuOpen(false)}
                  className={`block px-3 py-2 text-base font-medium rounded-lg transition-colors duration-200 ${
                    isActivePage(item.href)
                      ? 'text-primary-600 bg-primary-50'
                      : 'text-gray-700 hover:text-primary-600 hover:bg-gray-50'
                  } ${
                    item.name === 'Dashboard'
                      ? 'bg-primary-50 text-primary-700 font-semibold border border-primary-200'
                      : ''
                  }`}
                >
                  {item.name}
                </Link>
              ))}

              {isAuthenticated && user ? (
                <>
                  <hr className="my-4" />
                  <div className="flex items-center space-x-3 px-3 py-2">
                    <div className="h-10 w-10 rounded-full bg-primary-100 flex items-center justify-center">
                      {user.profilePictureUrl ? (
                        <img
                          src={user.profilePictureUrl}
                          alt={user.firstName}
                          className="h-10 w-10 rounded-full object-cover"
                        />
                      ) : (
                        <UserCircleIcon className="h-6 w-6 text-primary-600" />
                      )}
                    </div>
                    <div>
                      <div className="text-sm font-medium text-gray-900">
                        {user.firstName} {user.lastName}
                      </div>
                      <div className="text-xs text-gray-500">
                        {user.email}
                      </div>
                    </div>
                  </div>

                  {userNavigation.map((item) => (
                    <Link
                      key={item.name}
                      to={item.href}
                      onClick={() => setIsMobileMenuOpen(false)}
                      className="block px-3 py-2 text-base font-medium text-gray-700 hover:text-primary-600 hover:bg-gray-50 rounded-lg transition-colors duration-200"
                    >
                      {item.name}
                    </Link>
                  ))}

                  <button
                    onClick={() => {
                      handleLogout()
                      setIsMobileMenuOpen(false)
                    }}
                    className="block w-full text-left px-3 py-2 text-base font-medium text-red-600 hover:bg-red-50 rounded-lg transition-colors duration-200"
                  >
                    {t('nav.logout')}
                  </button>
                </>
              ) : (
                <>
                  <hr className="my-4" />
                  <Link
                    to="/login"
                    onClick={() => setIsMobileMenuOpen(false)}
                    className="block px-3 py-2 text-base font-medium text-gray-700 hover:text-primary-600 hover:bg-gray-50 rounded-lg transition-colors duration-200"
                  >
                    {t('nav.login')}
                  </Link>
                  <Link
                    to="/register"
                    onClick={() => setIsMobileMenuOpen(false)}
                    className="block px-3 py-2 text-base font-medium text-white bg-primary-600 hover:bg-primary-700 rounded-lg transition-colors duration-200 text-center"
                  >
                    {t('nav.register')}
                  </Link>
                </>
              )}
            </div>
          </motion.div>
        )}
      </AnimatePresence>

      {/* Click outside to close user menu */}
      {isUserMenuOpen && (
        <div
          className="fixed inset-0 z-40"
          onClick={() => setIsUserMenuOpen(false)}
        />
      )}
    </nav>
  )
}