import { useState, useEffect } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { Link } from 'react-router-dom'
import {
  CalendarDaysIcon,
  ClockIcon,
  MapPinIcon,
  UserCircleIcon,
  CheckCircleIcon,
  ExclamationTriangleIcon,
  XCircleIcon,
  EyeIcon,
  ChatBubbleLeftIcon,
  StarIcon
} from '@heroicons/react/24/outline'
import { bookingService, type Booking } from '../services/bookingService'
import type { BookingStatus } from '../types'
import { useAuthStore } from '../store/authStore'
import toast from 'react-hot-toast'
import ReviewForm from '../components/reviews/ReviewForm'
import { apiClient } from '../services/api'

const statusConfig: Record<BookingStatus, { color: string; icon: any; text: string }> = {
  PENDING_PAYMENT: {
    color: 'bg-orange-100 text-orange-800',
    icon: ClockIcon,
    text: 'Pendiente de Pago'
  },
  PENDING_STYLIST_APPROVAL: {
    color: 'bg-yellow-100 text-yellow-800',
    icon: ClockIcon,
    text: 'Esperando Estilista'
  },
  STYLIST_ACCEPTED: {
    color: 'bg-blue-100 text-blue-800',
    icon: CheckCircleIcon,
    text: 'Acepta por Estilista'
  },
  CLIENT_CONFIRMED: {
    color: 'bg-green-100 text-green-800',
    icon: CheckCircleIcon,
    text: 'Confirmada'
  },
  IN_PROGRESS: {
    color: 'bg-indigo-100 text-indigo-800',
    icon: ClockIcon,
    text: 'En Progreso'
  },
  COMPLETED: {
    color: 'bg-emerald-100 text-emerald-800',
    icon: CheckCircleIcon,
    text: 'Completada'
  },
  STYLIST_DECLINED: {
    color: 'bg-red-100 text-red-800',
    icon: XCircleIcon,
    text: 'Rechazada por Estilista'
  },
  STYLIST_NO_RESPONSE: {
    color: 'bg-gray-100 text-gray-800',
    icon: XCircleIcon,
    text: 'Sin Respuesta del Estilista'
  },
  CLIENT_NO_CONFIRM: {
    color: 'bg-gray-100 text-gray-800',
    icon: XCircleIcon,
    text: 'Cliente No Confirm'
  },
  CLIENT_NO_SHOW: {
    color: 'bg-orange-100 text-orange-800',
    icon: XCircleIcon,
    text: 'Cliente No Asisti'
  },
  STYLIST_NO_SHOW: {
    color: 'bg-red-100 text-red-800',
    icon: XCircleIcon,
    text: 'Estilista No Asisti'
  },
  CANCELLED: {
    color: 'bg-red-100 text-red-800',
    icon: XCircleIcon,
    text: 'Cancelada'
  },
  EXPIRED: {
    color: 'bg-gray-100 text-gray-800',
    icon: XCircleIcon,
    text: 'Expirada'
  }
}

const filterOptions = [
  { value: 'all', label: 'Todas las citas' },
  { value: 'upcoming', label: 'Prximas' },
  { value: 'past', label: 'Pasadas' },
  { value: 'cancelled', label: 'Canceladas' }
]

export default function BookingsPage() {
  const { user, isAuthenticated } = useAuthStore()
  const [bookings, setBookings] = useState<Booking[]>([])
  const [loading, setLoading] = useState(true)
  const [filter, setFilter] = useState('all')
  const [reviewingBookingId, setReviewingBookingId] = useState<string | null>(null)
  const [bookingHasReview, setBookingHasReview] = useState<Record<string, boolean>>({})

  useEffect(() => {
    if (isAuthenticated) {
      loadBookings()
    }
  }, [isAuthenticated])

  const loadBookings = async () => {
    try {
      setLoading(true)
      const response = await bookingService.getBookings()

      if (response.success && response.data) {
        setBookings(response.data)
        // Check which bookings already have reviews
        checkExistingReviews(response.data)
      } else {
        toast.error('Error al cargar las citas')
      }
    } catch (error) {
      console.error('Error loading bookings:', error)
      toast.error('Error al cargar las citas')
    } finally {
      setLoading(false)
    }
  }

  const checkExistingReviews = async (bookingsList: Booking[]) => {
    try {
      const response = await apiClient.get('/reviews/my-reviews')
      if (response.success) {
        const reviewedBookingIds = new Set(
          response.data.map((review: any) => review.booking_id.toString())
        )
        const hasReviewMap: Record<string, boolean> = {}
        bookingsList.forEach(booking => {
          hasReviewMap[booking.id] = reviewedBookingIds.has(booking.id)
        })
        setBookingHasReview(hasReviewMap)
      }
    } catch (error) {
      console.error('Error checking reviews:', error)
    }
  }

  const handleReviewSuccess = () => {
    setReviewingBookingId(null)
    loadBookings() // Reload to update review status
    toast.success('Resea publicada con xito!')
  }

  const filteredBookings = bookings.filter(booking => {
    const bookingDate = new Date(booking.appointment_date)
    const today = new Date()
    today.setHours(0, 0, 0, 0)

    switch (filter) {
      case 'upcoming':
        return bookingDate >= today && ![
          'CANCELLED', 'EXPIRED', 'COMPLETED', 'STYLIST_DECLINED',
          'STYLIST_NO_RESPONSE', 'CLIENT_NO_CONFIRM', 'CLIENT_NO_SHOW', 'STYLIST_NO_SHOW'
        ].includes(booking.status)
      case 'past':
        return bookingDate < today || ['COMPLETED', 'CLIENT_NO_SHOW', 'STYLIST_NO_SHOW'].includes(booking.status)
      case 'cancelled':
        return ['CANCELLED', 'EXPIRED', 'STYLIST_DECLINED', 'STYLIST_NO_RESPONSE', 'CLIENT_NO_CONFIRM'].includes(booking.status)
      default:
        return true
    }
  })

  const handleCancelBooking = async (bookingId: string) => {
    if (!confirm('Ests seguro de que quieres cancelar esta cita?')) {
      return
    }

    try {
      const response = await bookingService.cancelBooking(bookingId)

      if (response.success) {
        toast.success('Cita cancelada correctamente')
        loadBookings() // Reload bookings
      } else {
        toast.error('Error al cancelar la cita')
      }
    } catch (error) {
      console.error('Error cancelling booking:', error)
      toast.error('Error al cancelar la cita')
    }
  }

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('es-MX', {
      weekday: 'long',
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    })
  }

  const formatTime = (timeString: string) => {
    return new Date(`2000-01-01T${timeString}`).toLocaleTimeString('es-MX', {
      hour: '2-digit',
      minute: '2-digit',
      hour12: true
    })
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-600"></div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="container-responsive py-8">
        {/* Header */}
        <div className="mb-8">
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5 }}
          >
            <h1 className="text-3xl font-serif font-bold text-gray-900 mb-2">
              Mis Citas
            </h1>
            <p className="text-gray-600">
              Administra y revisa todas tus citas de belleza
            </p>
          </motion.div>
        </div>

        {/* Filter Tabs */}
        <div className="mb-6">
          <div className="border-b border-gray-200">
            <nav className="-mb-px flex space-x-8">
              {filterOptions.map((option) => {
                const isActive = filter === option.value
                const count = bookings.filter(booking => {
                  const bookingDate = new Date(booking.appointment_date)
                  const today = new Date()
                  today.setHours(0, 0, 0, 0)

                  switch (option.value) {
                    case 'upcoming':
                      return bookingDate >= today && booking.status !== 'CANCELLED'
                    case 'past':
                      return bookingDate < today || booking.status === 'COMPLETED'
                    case 'cancelled':
                      return booking.status === 'CANCELLED'
                    default:
                      return true
                  }
                }).length

                return (
                  <button
                    key={option.value}
                    onClick={() => setFilter(option.value)}
                    className={`py-2 px-1 border-b-2 font-medium text-sm whitespace-nowrap ${
                      isActive
                        ? 'border-primary-500 text-primary-600'
                        : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                    }`}
                  >
                    {option.label}
                    <span className={`ml-2 py-0.5 px-2 rounded-full text-xs ${
                      isActive ? 'bg-primary-100 text-primary-600' : 'bg-gray-100 text-gray-600'
                    }`}>
                      {count}
                    </span>
                  </button>
                )
              })}
            </nav>
          </div>
        </div>

        {/* Bookings List */}
        <div className="space-y-4">
          {filteredBookings.length > 0 ? (
            filteredBookings.map((booking) => {
              const statusInfo = statusConfig[booking.status] || statusConfig.PENDING_PAYMENT
              const StatusIcon = statusInfo.icon
              const canCancel = [
                'PENDING_STYLIST_APPROVAL', 'STYLIST_ACCEPTED', 'CLIENT_CONFIRMED'
              ].includes(booking.status)
              const canReview = booking.status === 'COMPLETED' && !bookingHasReview[booking.id]
              const hasReview = bookingHasReview[booking.id]

              return (
                <motion.div
                  key={booking.id}
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  className="card hover:shadow-md transition-shadow duration-200"
                >
                  <div className="card-body">
                    <div className="flex items-start justify-between">
                      <div className="flex-1">
                        {/* Service and Stylist Info */}
                        <div className="flex items-start space-x-4 mb-4">
                          <div className="flex-shrink-0">
                            <div className="w-12 h-12 bg-primary-100 rounded-full flex items-center justify-center">
                              <UserCircleIcon className="w-8 h-8 text-primary-600" />
                            </div>
                          </div>
                          <div className="flex-1">
                            <h3 className="font-semibold text-gray-900 text-lg">
                              {booking.service_name}
                            </h3>
                            <p className="text-gray-600 font-medium">
                              {booking.stylist_business_name}
                            </p>
                            <p className="text-sm text-gray-500">
                              con {booking.stylist_name}
                            </p>
                          </div>
                        </div>

                        {/* Date and Time */}
                        <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-4">
                          <div className="flex items-center text-sm text-gray-600">
                            <CalendarDaysIcon className="w-4 h-4 mr-2" />
                            <span>{formatDate(booking.appointment_date)}</span>
                          </div>
                          <div className="flex items-center text-sm text-gray-600">
                            <ClockIcon className="w-4 h-4 mr-2" />
                            <span>{formatTime(booking.appointment_time)}</span>
                          </div>
                          <div className="flex items-center text-sm font-medium text-gray-900">
                            <span className="text-gray-500 mr-1">Total:</span>
                            ${booking.total_price}
                          </div>
                        </div>

                        {/* Notes */}
                        {booking.notes && (
                          <div className="mb-4">
                            <p className="text-sm text-gray-600">
                              <span className="font-medium">Notas:</span> {booking.notes}
                            </p>
                          </div>
                        )}
                      </div>

                      {/* Status and Actions */}
                      <div className="flex flex-col items-end space-y-3">
                        <div className={`inline-flex items-center px-2.5 py-1.5 rounded-full text-xs font-medium ${statusInfo.color}`}>
                          <StatusIcon className="w-3 h-3 mr-1" />
                          {statusInfo.text}
                        </div>

                        <div className="flex flex-col space-y-2">
                          <div className="flex space-x-2">
                            <button className="btn btn-outline-primary btn-sm">
                              <EyeIcon className="w-4 h-4 mr-1" />
                              Ver
                            </button>
                            {canCancel && (
                              <button
                                onClick={() => handleCancelBooking(booking.id)}
                                className="btn btn-outline-danger btn-sm"
                              >
                                Cancelar
                              </button>
                            )}
                          </div>
                          {canReview && (
                            <button
                              onClick={() => setReviewingBookingId(booking.id)}
                              className="btn btn-primary btn-sm w-full"
                            >
                              <StarIcon className="w-4 h-4 mr-1" />
                              Escribir Resea
                            </button>
                          )}
                          {hasReview && (
                            <div className="text-xs text-green-600 flex items-center justify-center">
                              <CheckCircleIcon className="w-4 h-4 mr-1" />
                              Resea publicada
                            </div>
                          )}
                        </div>
                      </div>
                    </div>
                  </div>
                </motion.div>
              )
            })
          ) : (
            <div className="text-center py-12">
              <CalendarDaysIcon className="w-12 h-12 text-gray-400 mx-auto mb-3" />
              <p className="text-gray-500 mb-2">
                {filter === 'all' ? 'No tienes citas an' : `No tienes citas ${filterOptions.find(f => f.value === filter)?.label.toLowerCase()}`}
              </p>
              <p className="text-sm text-gray-400 mb-4">
                {filter === 'all' ? 'Reserva tu primera cita con un estilista profesional!' : 'Cambia el filtro para ver otras citas'}
              </p>
              {filter === 'all' && (
                <Link to="/stylists" className="btn btn-primary">
                  Explorar Estilistas
                </Link>
              )}
            </div>
          )}
        </div>

        {/* Review Modal */}
        <AnimatePresence>
          {reviewingBookingId && (() => {
            const booking = bookings.find(b => b.id === reviewingBookingId)
            if (!booking) return null

            return (
              <motion.div
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                exit={{ opacity: 0 }}
                className="fixed inset-0 bg-black bg-opacity-50 z-50 flex items-center justify-center p-4"
                onClick={() => setReviewingBookingId(null)}
              >
                <motion.div
                  initial={{ scale: 0.95, opacity: 0 }}
                  animate={{ scale: 1, opacity: 1 }}
                  exit={{ scale: 0.95, opacity: 0 }}
                  className="max-w-2xl w-full max-h-[90vh] overflow-y-auto"
                  onClick={(e) => e.stopPropagation()}
                >
                  <ReviewForm
                    bookingId={parseInt(booking.id)}
                    stylistName={booking.stylist_business_name}
                    serviceType={booking.service_name}
                    onSuccess={handleReviewSuccess}
                    onCancel={() => setReviewingBookingId(null)}
                  />
                </motion.div>
              </motion.div>
            )
          })()}
        </AnimatePresence>
      </div>
    </div>
  )
}