import { Routes, Route, Navigate, useLocation } from 'react-router-dom'
import { useAuthStore } from './store/authStore'
import { useEffect, useState } from 'react'
import AphroditeChat, { AphroditeFloatingButton } from './components/ai/AphroditeChat'

// Layout components
import Navbar from './components/layout/Navbar'
import Footer from './components/layout/Footer'
import ScrollToTop from './components/layout/ScrollToTop'

// Pages
import HomePage from './pages/HomePage'
import LoginPage from './pages/auth/LoginPage'
import RegisterPage from './pages/auth/RegisterPage'
import ClientRegisterPage from './pages/auth/ClientRegisterPage'
import ForgotPasswordPage from './pages/auth/ForgotPasswordPage'
import ResetPasswordPage from './pages/auth/ResetPasswordPage'
import StylistRegisterPage from './pages/auth/StylistRegisterPage'
import VerifyPhonePage from './pages/auth/VerifyPhonePage'
import AuthCallback from './components/AuthCallback'
import ServicesPage from './pages/ServicesPage'

// Stylist registration components for both modal and direct page access
import StylistRegistrationLayout from './pages/auth/stylist/StylistRegistrationLayout'
import StylistStep1Personal from './pages/auth/stylist/StylistStep1Personal'
import StylistStep2Professional from './pages/auth/stylist/StylistStep2Professional'
import StylistStep3Location from './pages/auth/stylist/StylistStep3Location'
import StylistStep4Portfolio from './pages/auth/stylist/StylistStep4Portfolio'
import StylistStep5Review from './pages/auth/stylist/StylistStep5Review'
import StylistsPage from './pages/StylistsPage'
import SyncfusionDemo from './pages/SyncfusionDemo'
import StylistProfilePage from './pages/StylistProfilePage'
import StylistPortfolioSlideshow from './pages/public/StylistPortfolioSlideshow'
import BookingPage from './pages/BookingPage'
import ProfilePage from './pages/ProfilePage'
import ProfileEditPage from './pages/profile/ProfileEditPage'
import DashboardPage from './pages/DashboardPage'
import BookingsPage from './pages/BookingsPage'
import PortfolioPage from './pages/dashboard/PortfolioPage'
import RevenuePage from './pages/dashboard/RevenuePage'
import ReminderStatsPage from './pages/dashboard/ReminderStatsPage'
import NotFoundPage from './pages/NotFoundPage'
import UnauthorizedPage from './pages/UnauthorizedPage'

// Admin - redirects to admin2 (separate React app)

// Footer link pages
import AboutPage from './pages/AboutPage'
import CareersPage from './pages/CareersPage'
import PressPage from './pages/PressPage'
import BlogPage from './pages/BlogPage'
import BlogPostPage from './pages/BlogPostPage'
import HelpPage from './pages/HelpPage'
import ContactPage from './pages/ContactPage'
import StatusPage from './pages/StatusPage'
import ReportPage from './pages/ReportPage'
import PrivacyPage from './pages/PrivacyPage'
import TermsPage from './pages/TermsPage'
import CookiesPage from './pages/CookiesPage'
import LicensesPage from './pages/LicensesPage'
import ResourcesPage from './pages/ResourcesPage'
import PoliciesPage from './pages/PoliciesPage'
import CommissionsPage from './pages/CommissionsPage'
import TeamCommentsPage from './pages/TeamCommentsPage'

// Client pages
import BitcoinPage from './pages/client/BitcoinPage'

// Protected Route component
import ProtectedRoute from './components/auth/ProtectedRoute'

function AppLayout() {
  const location = useLocation()
  const [isAphroditeOpen, setIsAphroditeOpen] = useState(false)

  // Don't show navbar/footer on homepage (it has its own)
  const hideLayout = location.pathname === '/'

  return (
    <>
      {!hideLayout && <Navbar />}
      <main className="flex-grow">
        <Routes>
          <Route path="/" element={<HomePage />} />
                <Route path="/login" element={<LoginPage />} />
                <Route path="/register" element={<ClientRegisterPage />} />
                <Route path="/register/basic" element={<RegisterPage />} />
                <Route path="/forgot-password" element={<ForgotPasswordPage />} />
                <Route path="/reset-password" element={<ResetPasswordPage />} />
                <Route path="/verify-phone" element={<VerifyPhonePage />} />
                <Route path="/auth/callback" element={<AuthCallback />} />
                {/* New single-page stylist registration */}
                <Route path="/register/stylist" element={<StylistRegisterPage />} />

                {/* Old multi-step stylist registration - moved to different URL */}
                <Route path="/register/stylist-multi" element={<Navigate to="/register/stylist-multi/step/1" replace />} />
                <Route path="/register/stylist-multi/step/*" element={<StylistRegistrationLayout />}>
                  <Route path="1" element={<StylistStep1Personal />} />
                  <Route path="2" element={<StylistStep2Professional />} />
                  <Route path="3" element={<StylistStep3Location />} />
                  <Route path="4" element={<StylistStep4Portfolio />} />
                  <Route path="5" element={<StylistStep5Review />} />
                  <Route index element={<Navigate to="/register/stylist-multi/step/1" replace />} />
                </Route>

                <Route path="/services" element={<ServicesPage />} />
                <Route path="/stylists" element={<StylistsPage />} />
                <Route path="/stylist/:id" element={<StylistProfilePage />} />
                <Route path="/syncfusion-demo" element={<SyncfusionDemo />} />

                {/* Footer link pages */}
                <Route path="/about" element={<AboutPage />} />
                <Route path="/careers" element={<CareersPage />} />
                <Route path="/press" element={<PressPage />} />
                <Route path="/blog" element={<BlogPage />} />
                <Route path="/blog/:slug" element={<BlogPostPage />} />
                <Route path="/help" element={<HelpPage />} />
                <Route path="/contact" element={<ContactPage />} />
                <Route path="/status" element={<StatusPage />} />
                <Route path="/report" element={<ReportPage />} />
                <Route path="/privacy" element={<PrivacyPage />} />
                <Route path="/terms" element={<TermsPage />} />
                <Route path="/cookies" element={<CookiesPage />} />
                <Route path="/licenses" element={<LicensesPage />} />
                <Route path="/resources" element={<ResourcesPage />} />
                <Route path="/policies" element={<PoliciesPage />} />
                <Route path="/commissions" element={<CommissionsPage />} />
                <Route path="/team/comments" element={<TeamCommentsPage />} />

                {/* Protected routes */}
                <Route
                  path="/book/:stylistId/:serviceId"
                  element={
                    <ProtectedRoute>
                      <BookingPage />
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/profile"
                  element={
                    <ProtectedRoute>
                      <ProfilePage />
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/profile/edit"
                  element={
                    <ProtectedRoute>
                      <ProfileEditPage />
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/dashboard"
                  element={
                    <ProtectedRoute>
                      <DashboardPage />
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/client/bitcoin"
                  element={
                    <ProtectedRoute>
                      <BitcoinPage />
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/bookings"
                  element={
                    <ProtectedRoute>
                      <BookingsPage />
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/dashboard/portfolio"
                  element={
                    <ProtectedRoute>
                      <PortfolioPage />
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/dashboard/revenue"
                  element={
                    <ProtectedRoute>
                      <RevenuePage />
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/dashboard/reminders"
                  element={
                    <ProtectedRoute>
                      <ReminderStatsPage />
                    </ProtectedRoute>
                  }
                />

                {/* Error pages */}
                <Route path="/unauthorized" element={<UnauthorizedPage />} />
                <Route path="*" element={<NotFoundPage />} />
        </Routes>
      </main>
      {!hideLayout && <Footer />}

      {/* Aphrodite AI Chatbot */}
      <AphroditeFloatingButton onClick={() => setIsAphroditeOpen(true)} />
      <AphroditeChat
        isOpen={isAphroditeOpen}
        onClose={() => setIsAphroditeOpen(false)}
      />
    </>
  )
}

function App() {
  const { user, checkAuth } = useAuthStore()

  useEffect(() => {
    checkAuth()
  }, [checkAuth])

  return (
    <div className="flex flex-col min-h-screen bg-gray-50">
      <ScrollToTop />
      <Routes>
        {/* Portfolio route - NO layout (must come before other routes to avoid navbar/footer) */}
        <Route path="/p/:username" element={<StylistPortfolioSlideshow />} />

        {/* All other routes with conditional layout */}
        <Route path="/*" element={<AppLayout />} />
      </Routes>
    </div>
  )
}

export default App