import { useState, useEffect } from 'react'
import { useNavigate, useSearchParams } from 'react-router-dom'
import { useAuthStore } from '../../store/authStore'
import axios from 'axios'

const VerifyPhonePage = () => {
  const [searchParams] = useSearchParams()
  const navigate = useNavigate()
  const { checkAuth } = useAuthStore()

  const [phone, setPhone] = useState('')
  const [verificationCode, setVerificationCode] = useState('')
  const [codeSent, setCodeSent] = useState(false)
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')
  const [success, setSuccess] = useState('')

  const email = searchParams.get('email')
  const role = searchParams.get('role')
  const token = searchParams.get('token')

  useEffect(() => {
    if (!email) {
      navigate('/login')
    }
    // Store the token from OAuth if present
    if (token) {
      // Update the store directly with token
      const storageKey = 'auth-storage'
      const existingData = localStorage.getItem(storageKey)
      if (existingData) {
        const parsed = JSON.parse(existingData)
        parsed.state.token = token
        localStorage.setItem(storageKey, JSON.stringify(parsed))
      } else {
        localStorage.setItem(storageKey, JSON.stringify({
          state: { token, isAuthenticated: false, isLoading: false, user: null, client: null, stylist: null },
          version: 0
        }))
      }
    }
  }, [email, token, navigate])

  const handleSendCode = async (e: React.FormEvent) => {
    e.preventDefault()
    setError('')
    setSuccess('')
    setLoading(true)

    try {
      await axios.post('/api/auth/send-phone-verification', {
        phone,
        email
      })
      setCodeSent(true)
      setSuccess('Verification code sent to your phone')
    } catch (err: any) {
      setError(err.response?.data?.message || 'Error sending code')
    } finally {
      setLoading(false)
    }
  }

  const handleVerifyCode = async (e: React.FormEvent) => {
    e.preventDefault()
    setError('')
    setLoading(true)

    try {
      await axios.post('/api/auth/verify-phone', {
        phone,
        code: verificationCode,
        email
      })

      setSuccess('Phone verified successfully!')

      // Refresh auth state and redirect
      await checkAuth()

      setTimeout(() => {
        if (role === 'stylist') {
          navigate('/dashboard')
        } else {
          navigate('/profile')
        }
      }, 1500)
    } catch (err: any) {
      setError(err.response?.data?.message || 'Invalid code')
    } finally {
      setLoading(false)
    }
  }

  const handleSkip = () => {
    if (role === 'stylist') {
      navigate('/dashboard')
    } else {
      navigate('/profile')
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-pink-50 via-purple-50 to-indigo-50 py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-md mx-auto">
        <div className="text-center mb-8">
          <div className="inline-flex items-center justify-center w-16 h-16 rounded-full bg-gradient-to-r from-pink-500 to-purple-600 mb-4">
            <i className="bi bi-phone text-white text-2xl"></i>
          </div>
          <h2 className="text-3xl font-bold text-gray-900">
            Verify Your Phone
          </h2>
          <p className="mt-2 text-gray-600">
            Add your number to receive appointment notifications
          </p>
        </div>

        <div className="bg-white rounded-2xl shadow-xl p-8">
          {error && (
            <div className="mb-4 p-4 bg-red-50 border border-red-200 rounded-lg">
              <p className="text-red-800 text-sm">{error}</p>
            </div>
          )}

          {success && (
            <div className="mb-4 p-4 bg-green-50 border border-green-200 rounded-lg">
              <p className="text-green-800 text-sm">{success}</p>
            </div>
          )}

          {!codeSent ? (
            <form onSubmit={handleSendCode}>
              <div className="mb-6">
                <label htmlFor="phone" className="block text-sm font-medium text-gray-700 mb-2">
                  Phone Number
                </label>
                <input
                  type="tel"
                  id="phone"
                  value={phone}
                  onChange={(e) => setPhone(e.target.value)}
                  placeholder="+52 123 456 7890"
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent"
                  required
                />
                <p className="mt-2 text-xs text-gray-500">
                  International format (e.g. +52 for Mexico)
                </p>
              </div>

              <button
                type="submit"
                disabled={loading}
                className="w-full bg-gradient-to-r from-pink-500 to-purple-600 text-white py-3 px-4 rounded-lg font-semibold hover:from-pink-600 hover:to-purple-700 transition-all disabled:opacity-50 disabled:cursor-not-allowed"
              >
                {loading ? 'Sending...' : 'Send Code'}
              </button>
            </form>
          ) : (
            <form onSubmit={handleVerifyCode}>
              <div className="mb-6">
                <label htmlFor="code" className="block text-sm font-medium text-gray-700 mb-2">
                  Verification Code
                </label>
                <input
                  type="text"
                  id="code"
                  value={verificationCode}
                  onChange={(e) => setVerificationCode(e.target.value)}
                  placeholder="123456"
                  maxLength={6}
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent text-center text-2xl tracking-widest"
                  required
                />
                <p className="mt-2 text-xs text-gray-500 text-center">
                  Enter the 6-digit code sent to {phone}
                </p>
              </div>

              <button
                type="submit"
                disabled={loading}
                className="w-full bg-gradient-to-r from-pink-500 to-purple-600 text-white py-3 px-4 rounded-lg font-semibold hover:from-pink-600 hover:to-purple-700 transition-all disabled:opacity-50 disabled:cursor-not-allowed mb-3"
              >
                {loading ? 'Verifying...' : 'Verify Code'}
              </button>

              <button
                type="button"
                onClick={() => setCodeSent(false)}
                className="w-full text-purple-600 py-2 text-sm hover:text-purple-700"
              >
                Change phone number
              </button>
            </form>
          )}

          <div className="mt-6 pt-6 border-t border-gray-200">
            <button
              onClick={handleSkip}
              className="w-full text-gray-600 text-sm hover:text-gray-800"
            >
              Skip for now
            </button>
          </div>
        </div>
      </div>
    </div>
  )
}

export default VerifyPhonePage