import React, { useState, useEffect, useRef, useCallback } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { useTranslation } from 'react-i18next';
import {
  HeartIcon,
  ChatBubbleLeftIcon,
  ShareIcon,
  BookmarkIcon,
  PlayIcon,
  ArrowPathIcon
} from '@heroicons/react/24/outline';
import {
  HeartIcon as HeartIconSolid,
  BookmarkIcon as BookmarkIconSolid
} from '@heroicons/react/24/solid';
import { useAuthStore } from '../store/authStore';
import { apiClient } from '../services/api';

interface BlogPost {
  id: number;
  title: string;
  slug: string;
  content: string;
  excerpt: string;
  author: string;
  author_avatar?: string;
  published_at: string;
  media_url?: string;
  media_type?: 'image' | 'video';
  likes_count: number;
  comments_count: number;
  shares_count: number;
  is_liked?: boolean;
  is_bookmarked?: boolean;
  user_has_interacted?: boolean;
}

interface Comment {
  id: number;
  post_id: number;
  author: string;
  author_avatar?: string;
  content: string;
  created_at: string;
  likes_count: number;
  is_liked?: boolean;
}

const BlogPage: React.FC = () => {
  const { t } = useTranslation();
  const { user, isAuthenticated } = useAuthStore();
  const [posts, setPosts] = useState<BlogPost[]>([]);
  const [loading, setLoading] = useState(false);
  const [hasMore, setHasMore] = useState(true);
  const [page, setPage] = useState(1);
  const [refreshing, setRefreshing] = useState(false);
  const [expandedPost, setExpandedPost] = useState<number | null>(null);
  const [comments, setComments] = useState<Record<number, Comment[]>>({});
  const [commentText, setCommentText] = useState<Record<number, string>>({});
  const observerRef = useRef<IntersectionObserver | null>(null);
  const lastPostRef = useRef<HTMLDivElement | null>(null);
  const scrollContainerRef = useRef<HTMLDivElement | null>(null);
  const pullStartY = useRef<number>(0);
  const isPulling = useRef<boolean>(false);

  // Fetch posts with engagement-based sorting
  const fetchPosts = async (pageNum: number, isRefresh = false) => {
    if (loading) return;

    try {
      setLoading(true);

      const response = await fetch(
        `/api/blog/timeline?page=${pageNum}&limit=10&user_id=${user?.id || ''}`
      );
      const data = await response.json();

      if (data.success) {
        const newPosts = data.posts;

        if (isRefresh) {
          setPosts(newPosts);
        } else {
          setPosts(prev => [...prev, ...newPosts]);
        }

        setHasMore(data.pagination.page < data.pagination.pages);
      } else {
        console.error('Error fetching posts:', data.message);
      }
    } catch (err) {
      console.error('Error fetching posts:', err);
    } finally {
      setLoading(false);
      setRefreshing(false);
    }
  };

  // Initial load
  useEffect(() => {
    fetchPosts(1);
  }, []);

  // Infinite scroll observer
  useEffect(() => {
    if (observerRef.current) observerRef.current.disconnect();

    observerRef.current = new IntersectionObserver(
      entries => {
        if (entries[0].isIntersecting && hasMore && !loading) {
          setPage(prev => prev + 1);
        }
      },
      { threshold: 0.5 }
    );

    if (lastPostRef.current) {
      observerRef.current.observe(lastPostRef.current);
    }

    return () => observerRef.current?.disconnect();
  }, [hasMore, loading]);

  // Load more when page changes
  useEffect(() => {
    if (page > 1) {
      fetchPosts(page);
    }
  }, [page]);

  // Pull to refresh
  const handleTouchStart = (e: React.TouchEvent) => {
    if (scrollContainerRef.current?.scrollTop === 0) {
      pullStartY.current = e.touches[0].clientY;
      isPulling.current = true;
    }
  };

  const handleTouchMove = (e: React.TouchEvent) => {
    if (!isPulling.current) return;

    const touchY = e.touches[0].clientY;
    const pullDistance = touchY - pullStartY.current;

    if (pullDistance > 80) {
      setRefreshing(true);
    }
  };

  const handleTouchEnd = () => {
    if (refreshing) {
      setPage(1);
      fetchPosts(1, true);
    }
    isPulling.current = false;
  };

  // Post interactions
  const handleLike = async (postId: number) => {
    if (!isAuthenticated) return;

    // Optimistic update
    setPosts(prev => prev.map(post =>
      post.id === postId
        ? {
            ...post,
            is_liked: !post.is_liked,
            likes_count: post.is_liked ? post.likes_count - 1 : post.likes_count + 1
          }
        : post
    ));

    try {
      await apiClient.post(`/blog/${postId}/like`);
    } catch (error) {
      console.error('Error liking post:', error);
      // Revert on error
      setPosts(prev => prev.map(post =>
        post.id === postId
          ? {
              ...post,
              is_liked: !post.is_liked,
              likes_count: post.is_liked ? post.likes_count + 1 : post.likes_count - 1
            }
          : post
      ));
    }
  };

  const handleBookmark = async (postId: number) => {
    if (!isAuthenticated) return;

    // Optimistic update
    setPosts(prev => prev.map(post =>
      post.id === postId
        ? { ...post, is_bookmarked: !post.is_bookmarked }
        : post
    ));

    try {
      await apiClient.post(`/blog/${postId}/bookmark`);
    } catch (error) {
      console.error('Error bookmarking post:', error);
      // Revert on error
      setPosts(prev => prev.map(post =>
        post.id === postId
          ? { ...post, is_bookmarked: !post.is_bookmarked }
          : post
      ));
    }
  };

  const handleShare = async (post: BlogPost) => {
    try {
      // Track the share
      if (isAuthenticated) {
        await apiClient.post(`/blog/${post.id}/share`);
      }

      // Update UI
      setPosts(prev => prev.map(p =>
        p.id === post.id
          ? { ...p, shares_count: p.shares_count + 1 }
          : p
      ));

      // Share or copy to clipboard
      if (navigator.share) {
        await navigator.share({
          title: post.title,
          text: post.excerpt,
          url: `${window.location.origin}/blog/${post.slug}`
        });
      } else {
        await navigator.clipboard.writeText(`${window.location.origin}/blog/${post.slug}`);
      }
    } catch (error) {
      console.error('Error sharing post:', error);
    }
  };

  const handleComment = async (postId: number) => {
    if (!commentText[postId]?.trim() || !isAuthenticated) return;

    const commentContent = commentText[postId].trim();
    setCommentText(prev => ({ ...prev, [postId]: '' }));

    try {
      const response = await apiClient.post(`/blog/${postId}/comments`, {
        content: commentContent
      });

      if (response.success) {
        setComments(prev => ({
          ...prev,
          [postId]: [...(prev[postId] || []), response.comment]
        }));

        setPosts(prev => prev.map(post =>
          post.id === postId
            ? { ...post, comments_count: post.comments_count + 1 }
            : post
        ));
      }
    } catch (error) {
      console.error('Error adding comment:', error);
      setCommentText(prev => ({ ...prev, [postId]: commentContent }));
    }
  };

  const loadComments = async (postId: number) => {
    if (comments[postId]) return; // Already loaded

    try {
      const response = await fetch(`/api/blog/${postId}/comments`);
      const data = await response.json();

      if (data.success) {
        setComments(prev => ({ ...prev, [postId]: data.comments }));
      }
    } catch (error) {
      console.error('Error loading comments:', error);
    }
  };

  const toggleComments = (postId: number) => {
    if (expandedPost === postId) {
      setExpandedPost(null);
    } else {
      setExpandedPost(postId);
      loadComments(postId);
    }
  };

  const formatTimeAgo = (dateString: string) => {
    const date = new Date(dateString);
    const now = new Date();
    const seconds = Math.floor((now.getTime() - date.getTime()) / 1000);

    if (seconds < 60) return `${seconds}s`;
    if (seconds < 3600) return `${Math.floor(seconds / 60)}m`;
    if (seconds < 86400) return `${Math.floor(seconds / 3600)}h`;
    return `${Math.floor(seconds / 86400)}d`;
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-pink-50 via-white to-purple-50">
      {/* Header */}
      <div className="sticky top-16 z-40 bg-white/80 backdrop-blur-md border-b border-gray-200">
        <div className="max-w-2xl mx-auto px-4 py-4">
          <h1 className="text-2xl font-bold bg-gradient-to-r from-pink-500 to-purple-600 bg-clip-text text-transparent">
            For You 
          </h1>
        </div>
      </div>

      {/* Pull to refresh indicator */}
      <AnimatePresence>
        {refreshing && (
          <motion.div
            initial={{ opacity: 0, y: -20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
            className="flex justify-center py-4"
          >
            <ArrowPathIcon className="w-6 h-6 text-purple-600 animate-spin" />
          </motion.div>
        )}
      </AnimatePresence>

      {/* Timeline Feed */}
      <div
        ref={scrollContainerRef}
        onTouchStart={handleTouchStart}
        onTouchMove={handleTouchMove}
        onTouchEnd={handleTouchEnd}
        className="max-w-2xl mx-auto"
      >
        {posts.map((post, index) => (
          <motion.article
            key={post.id}
            ref={index === posts.length - 1 ? lastPostRef : null}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.3 }}
            className="bg-white border-b border-gray-200 hover:bg-gray-50/50 transition-colors"
          >
            <div className="p-4">
              {/* Author header */}
              <div className="flex items-start space-x-3 mb-3">
                <img
                  src={post.author_avatar || 'https://i.pravatar.cc/150?img=1'}
                  alt={post.author}
                  className="w-12 h-12 rounded-full object-cover ring-2 ring-pink-200"
                />
                <div className="flex-1 min-w-0">
                  <div className="flex items-center space-x-2">
                    <h3 className="font-bold text-gray-900 truncate">{post.author}</h3>
                    <span className="text-gray-500"></span>
                    <span className="text-gray-500 text-sm">{formatTimeAgo(post.published_at)}</span>
                  </div>
                  <p className="text-gray-600 text-sm">Beauty Expert</p>
                </div>
              </div>

              {/* Post content */}
              <div className="mb-3">
                <h2 className="text-xl font-bold text-gray-900 mb-2">{post.title}</h2>
                <p className="text-gray-700 leading-relaxed">{post.excerpt}</p>
              </div>

              {/* Media */}
              {post.media_url && (
                <div className="mb-3 rounded-2xl overflow-hidden border border-gray-200">
                  {post.media_type === 'video' ? (
                    <div className="relative bg-black aspect-video">
                      <video
                        src={post.media_url}
                        controls
                        className="w-full h-full"
                        poster={`https://picsum.photos/seed/${post.id}/800/600`}
                      >
                        <source src={post.media_url} type="video/mp4" />
                      </video>
                      <div className="absolute inset-0 flex items-center justify-center pointer-events-none">
                        <div className="bg-white/20 backdrop-blur-sm rounded-full p-4">
                          <PlayIcon className="w-12 h-12 text-white" />
                        </div>
                      </div>
                    </div>
                  ) : (
                    <img
                      src={post.media_url}
                      alt={post.title}
                      className="w-full object-cover"
                      loading="lazy"
                    />
                  )}
                </div>
              )}

              {/* Action buttons */}
              <div className="flex items-center justify-between text-gray-600">
                <button
                  onClick={() => handleLike(post.id)}
                  className="flex items-center space-x-2 hover:text-pink-600 transition-colors group"
                >
                  {post.is_liked ? (
                    <HeartIconSolid className="w-5 h-5 text-pink-600" />
                  ) : (
                    <HeartIcon className="w-5 h-5 group-hover:scale-110 transition-transform" />
                  )}
                  <span className={post.is_liked ? 'text-pink-600 font-medium' : ''}>
                    {post.likes_count}
                  </span>
                </button>

                <button
                  onClick={() => toggleComments(post.id)}
                  className="flex items-center space-x-2 hover:text-purple-600 transition-colors group"
                >
                  <ChatBubbleLeftIcon className="w-5 h-5 group-hover:scale-110 transition-transform" />
                  <span>{post.comments_count}</span>
                </button>

                <button
                  onClick={() => handleShare(post)}
                  className="flex items-center space-x-2 hover:text-blue-600 transition-colors group"
                >
                  <ShareIcon className="w-5 h-5 group-hover:scale-110 transition-transform" />
                  <span>{post.shares_count}</span>
                </button>

                <button
                  onClick={() => handleBookmark(post.id)}
                  className="hover:text-yellow-600 transition-colors"
                >
                  {post.is_bookmarked ? (
                    <BookmarkIconSolid className="w-5 h-5 text-yellow-600" />
                  ) : (
                    <BookmarkIcon className="w-5 h-5" />
                  )}
                </button>
              </div>
            </div>

            {/* Comments section */}
            <AnimatePresence>
              {expandedPost === post.id && (
                <motion.div
                  initial={{ height: 0, opacity: 0 }}
                  animate={{ height: 'auto', opacity: 1 }}
                  exit={{ height: 0, opacity: 0 }}
                  transition={{ duration: 0.3 }}
                  className="border-t border-gray-200 bg-gray-50/50"
                >
                  {/* Comments list */}
                  <div className="px-4 py-3 space-y-3 max-h-96 overflow-y-auto">
                    {comments[post.id]?.map(comment => (
                      <div key={comment.id} className="flex space-x-3">
                        <img
                          src={comment.author_avatar || 'https://i.pravatar.cc/150?img=1'}
                          alt={comment.author}
                          className="w-8 h-8 rounded-full object-cover"
                        />
                        <div className="flex-1 bg-white rounded-2xl px-4 py-2 border border-gray-200">
                          <div className="flex items-center space-x-2 mb-1">
                            <span className="font-semibold text-sm text-gray-900">{comment.author}</span>
                            <span className="text-gray-500 text-xs">{formatTimeAgo(comment.created_at)}</span>
                          </div>
                          <p className="text-gray-700 text-sm">{comment.content}</p>
                        </div>
                      </div>
                    ))}
                  </div>

                  {/* Comment input */}
                  {isAuthenticated ? (
                    <div className="px-4 py-3 border-t border-gray-200 flex space-x-3">
                      <img
                        src={user?.profile_picture_url || 'https://i.pravatar.cc/150?img=1'}
                        alt={user?.name || 'You'}
                        className="w-8 h-8 rounded-full object-cover"
                      />
                      <div className="flex-1 flex space-x-2">
                        <input
                          type="text"
                          value={commentText[post.id] || ''}
                          onChange={(e) => setCommentText(prev => ({ ...prev, [post.id]: e.target.value }))}
                          onKeyPress={(e) => e.key === 'Enter' && handleComment(post.id)}
                          placeholder="Add a comment..."
                          className="flex-1 bg-white border border-gray-300 rounded-full px-4 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-purple-500"
                        />
                        <button
                          onClick={() => handleComment(post.id)}
                          disabled={!commentText[post.id]?.trim()}
                          className="px-4 py-2 bg-gradient-to-r from-pink-500 to-purple-600 text-white rounded-full text-sm font-medium disabled:opacity-50 disabled:cursor-not-allowed hover:shadow-lg transition-shadow"
                        >
                          Post
                        </button>
                      </div>
                    </div>
                  ) : (
                    <div className="px-4 py-3 border-t border-gray-200 text-center">
                      <p className="text-gray-600 text-sm">
                        <a href="/login" className="text-purple-600 hover:underline">Sign in</a> to comment
                      </p>
                    </div>
                  )}
                </motion.div>
              )}
            </AnimatePresence>
          </motion.article>
        ))}

        {/* Loading indicator */}
        {loading && (
          <div className="flex justify-center py-8">
            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-purple-600"></div>
          </div>
        )}

        {/* End of feed */}
        {!hasMore && posts.length > 0 && (
          <div className="text-center py-8 text-gray-500">
            <p className="text-sm">You're all caught up! </p>
          </div>
        )}
      </div>
    </div>
  );
};

export default BlogPage;
