import React, { useState } from 'react';
import {
  ScheduleComponent,
  Day,
  Week,
  WorkWeek,
  Month,
  Agenda,
  Inject,
  ViewsDirective,
  ViewDirective,
  ResourcesDirective,
  ResourceDirective,
  EventSettingsModel,
  DragAndDrop,
  Resize,
} from '@syncfusion/ej2-react-schedule';
import { useTranslation } from 'react-i18next';
import toast from 'react-hot-toast';

interface Appointment {
  Id: number;
  Subject: string;
  StartTime: Date;
  EndTime: Date;
  IsAllDay?: boolean;
  Status?: string;
  CategoryColor?: string;
  ClientName?: string;
  ClientPhone?: string;
  ServiceName?: string;
  Notes?: string;
}

interface SyncfusionSchedulerProps {
  appointments?: Appointment[];
  onAppointmentChange?: (data: any) => void;
  onAppointmentAdd?: (data: any) => void;
  onAppointmentDelete?: (data: any) => void;
  readOnly?: boolean;
}

const SyncfusionScheduler: React.FC<SyncfusionSchedulerProps> = ({
  appointments = [],
  onAppointmentChange,
  onAppointmentAdd,
  onAppointmentDelete,
  readOnly = false,
}) => {
  const { t } = useTranslation();
  const [scheduleData, setScheduleData] = useState<Appointment[]>(appointments);

  // Event settings for the scheduler
  const eventSettings: EventSettingsModel = {
    dataSource: scheduleData,
    fields: {
      id: 'Id',
      subject: { name: 'Subject', title: 'Service' },
      startTime: { name: 'StartTime', title: 'Start Time' },
      endTime: { name: 'EndTime', title: 'End Time' },
      isAllDay: { name: 'IsAllDay' },
      description: { name: 'Notes' },
    },
  };

  // Resource data for categorizing appointments
  const statusData = [
    { StatusText: 'Confirmed', StatusId: 1, StatusColor: '#10b981' },
    { StatusText: 'Pending', StatusId: 2, StatusColor: '#f59e0b' },
    { StatusText: 'Cancelled', StatusId: 3, StatusColor: '#ef4444' },
    { StatusText: 'Completed', StatusId: 4, StatusColor: '#6366f1' },
  ];

  // Handle event creation
  const onActionBegin = (args: any): void => {
    if (readOnly) {
      args.cancel = true;
      toast.error(t('scheduler.readOnlyMode') || 'Scheduler is in read-only mode');
      return;
    }

    if (args.requestType === 'eventCreate' && onAppointmentAdd) {
      onAppointmentAdd(args.data);
      toast.success(t('scheduler.appointmentCreated') || 'Appointment created successfully');
    }

    if (args.requestType === 'eventChange' && onAppointmentChange) {
      onAppointmentChange(args.data);
      toast.success(t('scheduler.appointmentUpdated') || 'Appointment updated successfully');
    }

    if (args.requestType === 'eventRemove' && onAppointmentDelete) {
      onAppointmentDelete(args.data);
      toast.success(t('scheduler.appointmentDeleted') || 'Appointment deleted successfully');
    }
  };

  // Handle event click
  const onEventClick = (args: any): void => {
    console.log('Event clicked:', args.event);
  };

  // Custom event template
  const eventTemplate = (props: any): JSX.Element => {
    return (
      <div className="template-wrap" style={{ background: props.CategoryColor }}>
        <div className="subject">{props.Subject}</div>
        {props.ClientName && (
          <div className="client-name" style={{ fontSize: '11px', opacity: 0.9 }}>
            {props.ClientName}
          </div>
        )}
      </div>
    );
  };

  // Working hours configuration
  const workHours = {
    highlight: true,
    start: '09:00',
    end: '18:00',
  };

  return (
    <div className="syncfusion-scheduler-container">
      <ScheduleComponent
        height="650px"
        selectedDate={new Date()}
        eventSettings={eventSettings}
        actionBegin={onActionBegin}
        eventClick={onEventClick}
        workHours={workHours}
        timeScale={{ enable: true, interval: 30, slotCount: 1 }}
        eventTemplate={eventTemplate}
        readonly={readOnly}
        showQuickInfo={true}
        showTimeIndicator={true}
        enableAdaptiveUI={true}
        timeFormat="HH:mm"
        dateFormat="dd/MM/yyyy"
      >
        <ResourcesDirective>
          <ResourceDirective
            field="Status"
            title="Status"
            name="Status"
            allowMultiple={false}
            dataSource={statusData}
            textField="StatusText"
            idField="StatusId"
            colorField="StatusColor"
          />
        </ResourcesDirective>

        <ViewsDirective>
          <ViewDirective option="Day" />
          <ViewDirective option="Week" />
          <ViewDirective option="WorkWeek" />
          <ViewDirective option="Month" />
          <ViewDirective option="Agenda" />
        </ViewsDirective>

        <Inject services={[Day, Week, WorkWeek, Month, Agenda, DragAndDrop, Resize]} />
      </ScheduleComponent>

      <style jsx>{`
        .syncfusion-scheduler-container {
          width: 100%;
          margin: 0 auto;
          background: white;
          border-radius: 12px;
          padding: 20px;
          box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
        }

        .template-wrap {
          width: 100%;
          height: 100%;
          padding: 5px;
          border-radius: 4px;
          color: white;
          font-weight: 500;
        }

        .subject {
          font-size: 12px;
          font-weight: 600;
        }

        .client-name {
          margin-top: 2px;
        }
      `}</style>
    </div>
  );
};

export default SyncfusionScheduler;
