import { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { motion } from 'framer-motion'
import { useTranslation } from 'react-i18next'
import {
  UserCircleIcon,
  CalendarDaysIcon,
  HeartIcon,
  CreditCardIcon,
  BellIcon,
  MapPinIcon,
  StarIcon,
  ClockIcon,
  PlusIcon
} from '@heroicons/react/24/outline'
import { useAuthStore } from '../../store/authStore'
import DashboardService from '../../services/dashboardService'

interface ClientStats {
  totalBookings: number
  upcomingBookings: number
  favoriteStylists: number
  totalSpent: number
}

interface UpcomingBooking {
  id: string
  stylistName: string
  serviceName: string
  date: string
  time: string
  price: number
  status: 'confirmed' | 'pending' | 'completed'
}

interface RecentBooking {
  id: string
  stylistName: string
  serviceName: string
  date: string
  rating?: number
  canReview: boolean
}

export default function ClientProfile() {
  const { user, client } = useAuthStore()
  const { t } = useTranslation()
  const [stats, setStats] = useState<ClientStats>({
    totalBookings: 0,
    upcomingBookings: 0,
    favoriteStylists: 0,
    totalSpent: 0
  })
  const [upcomingBookings, setUpcomingBookings] = useState<UpcomingBooking[]>([])
  const [recentBookings, setRecentBookings] = useState<RecentBooking[]>([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const fetchProfileData = async () => {
      try {
        // Fetch client stats and activity data in parallel
        const [statsData, activityData] = await Promise.all([
          DashboardService.getClientStats(),
          DashboardService.getClientActivity()
        ])

        setStats(statsData)
        setUpcomingBookings(activityData.upcomingBookings || [])
        setRecentBookings(activityData.recentBookings.map(booking => ({
          id: booking.id,
          stylistName: booking.stylist_name,
          serviceName: booking.service_name,
          date: booking.appointment_date,
          rating: booking.rating,
          canReview: !booking.review_id
        })) || [])
      } catch (error) {
        console.error('Error fetching profile data:', error)
        // Keep default empty state on error
      } finally {
        setLoading(false)
      }
    }

    fetchProfileData()
  }, [client])

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-600"></div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="container-responsive py-8">
        {/* Header */}
        <div className="mb-8">
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5 }}
          >
            <h1 className="text-3xl font-serif font-bold text-gray-900 mb-2">
              Mi Perfil
            </h1>
            <p className="text-gray-600">
              Gestiona tu informacin y reservas
            </p>
          </motion.div>
        </div>

        {/* Quick Actions */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5, delay: 0.1 }}
          className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-8"
        >
          <Link
            to="/stylists"
            className="card hover:shadow-md transition-shadow duration-200"
          >
            <div className="card-body text-center">
              <PlusIcon className="h-8 w-8 text-primary-600 mx-auto mb-3" />
              <h3 className="font-medium text-gray-900">Nueva Cita</h3>
              <p className="text-sm text-gray-600">Reservar servicio</p>
            </div>
          </Link>

          <Link
            to="/bookings"
            className="card hover:shadow-md transition-shadow duration-200"
          >
            <div className="card-body text-center">
              <CalendarDaysIcon className="h-8 w-8 text-primary-600 mx-auto mb-3" />
              <h3 className="font-medium text-gray-900">Mis Citas</h3>
              <p className="text-sm text-gray-600">Ver reservas</p>
            </div>
          </Link>

          <Link
            to="/profile/favorites"
            className="card hover:shadow-md transition-shadow duration-200"
          >
            <div className="card-body text-center">
              <HeartIcon className="h-8 w-8 text-primary-600 mx-auto mb-3" />
              <h3 className="font-medium text-gray-900">Favoritos</h3>
              <p className="text-sm text-gray-600">Estilistas favoritos</p>
            </div>
          </Link>

          <Link
            to="/profile/settings"
            className="card hover:shadow-md transition-shadow duration-200"
          >
            <div className="card-body text-center">
              <UserCircleIcon className="h-8 w-8 text-primary-600 mx-auto mb-3" />
              <h3 className="font-medium text-gray-900">Configuracin</h3>
              <p className="text-sm text-gray-600">Editar perfil</p>
            </div>
          </Link>
        </motion.div>

        {/* Stats Grid */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5, delay: 0.2 }}
          className="grid grid-cols-2 md:grid-cols-4 gap-6 mb-8"
        >
          <div className="card">
            <div className="card-body">
              <div className="flex items-center">
                <div className="flex-shrink-0">
                  <CalendarDaysIcon className="h-8 w-8 text-primary-600" />
                </div>
                <div className="ml-3">
                  <p className="text-sm font-medium text-gray-500">Prximas Citas</p>
                  <p className="text-2xl font-bold text-gray-900">{stats.upcomingBookings}</p>
                </div>
              </div>
            </div>
          </div>

          <div className="card">
            <div className="card-body">
              <div className="flex items-center">
                <div className="flex-shrink-0">
                  <ClockIcon className="h-8 w-8 text-blue-600" />
                </div>
                <div className="ml-3">
                  <p className="text-sm font-medium text-gray-500">Total Citas</p>
                  <p className="text-2xl font-bold text-gray-900">{stats.totalBookings}</p>
                </div>
              </div>
            </div>
          </div>

          <div className="card">
            <div className="card-body">
              <div className="flex items-center">
                <div className="flex-shrink-0">
                  <HeartIcon className="h-8 w-8 text-red-500" />
                </div>
                <div className="ml-3">
                  <p className="text-sm font-medium text-gray-500">Favoritos</p>
                  <p className="text-2xl font-bold text-gray-900">{stats.favoriteStylists}</p>
                </div>
              </div>
            </div>
          </div>

          <div className="card">
            <div className="card-body">
              <div className="flex items-center">
                <div className="flex-shrink-0">
                  <CreditCardIcon className="h-8 w-8 text-green-600" />
                </div>
                <div className="ml-3">
                  <p className="text-sm font-medium text-gray-500">Gastado</p>
                  <p className="text-2xl font-bold text-gray-900">${stats.totalSpent}</p>
                </div>
              </div>
            </div>
          </div>
        </motion.div>

        {/* Main Content Grid */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
          {/* Upcoming Bookings */}
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5, delay: 0.3 }}
            className="card"
          >
            <div className="card-header">
              <h3 className="card-title">Prximas Citas</h3>
              <Link
                to="/bookings"
                className="text-sm text-primary-600 hover:text-primary-700"
              >
                Ver todas
              </Link>
            </div>
            <div className="card-body">
              {upcomingBookings.length > 0 ? (
                <div className="space-y-4">
                  {upcomingBookings.map((booking) => (
                    <div key={booking.id} className="flex items-center space-x-4 p-4 bg-gray-50 rounded-lg">
                      <div className="flex-shrink-0">
                        <CalendarDaysIcon className="h-8 w-8 text-primary-600" />
                      </div>
                      <div className="flex-1 min-w-0">
                        <p className="text-sm font-medium text-gray-900">{booking.serviceName}</p>
                        <p className="text-sm text-gray-500">Con {booking.stylistName}</p>
                        <p className="text-sm text-gray-500">{booking.date} a las {booking.time}</p>
                      </div>
                      <div className="text-sm font-medium text-gray-900">
                        ${booking.price}
                      </div>
                    </div>
                  ))}
                </div>
              ) : (
                <div className="text-center py-8">
                  <CalendarDaysIcon className="h-12 w-12 text-gray-400 mx-auto mb-4" />
                  <p className="text-gray-500 mb-2">No tienes citas programadas</p>
                  <p className="text-sm text-gray-400 mb-4">
                    Reserva tu prximo servicio de belleza!
                  </p>
                  <Link
                    to="/stylists"
                    className="btn btn-primary btn-sm"
                  >
                    Explorar Estilistas
                  </Link>
                </div>
              )}
            </div>
          </motion.div>

          {/* Recent Activity */}
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5, delay: 0.4 }}
            className="card"
          >
            <div className="card-header">
              <h3 className="card-title">Historial Reciente</h3>
              <Link
                to="/bookings?tab=history"
                className="text-sm text-primary-600 hover:text-primary-700"
              >
                Ver historial
              </Link>
            </div>
            <div className="card-body">
              {recentBookings.length > 0 ? (
                <div className="space-y-4">
                  {recentBookings.map((booking) => (
                    <div key={booking.id} className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
                      <div className="flex items-center space-x-4">
                        <div className="flex-shrink-0">
                          <ClockIcon className="h-8 w-8 text-gray-400" />
                        </div>
                        <div>
                          <p className="text-sm font-medium text-gray-900">{booking.serviceName}</p>
                          <p className="text-sm text-gray-500">Con {booking.stylistName}</p>
                          <p className="text-sm text-gray-500">{booking.date}</p>
                        </div>
                      </div>
                      <div className="text-right">
                        {booking.rating ? (
                          <div className="flex items-center">
                            <StarIcon className="h-4 w-4 text-yellow-400 mr-1" />
                            <span className="text-sm text-gray-600">{booking.rating}</span>
                          </div>
                        ) : booking.canReview ? (
                          <button className="text-sm text-primary-600 hover:text-primary-700">
                            Calificar
                          </button>
                        ) : null}
                      </div>
                    </div>
                  ))}
                </div>
              ) : (
                <div className="text-center py-8">
                  <ClockIcon className="h-12 w-12 text-gray-400 mx-auto mb-4" />
                  <p className="text-gray-500">No tienes historial de citas</p>
                  <p className="text-sm text-gray-400">
                    Tus servicios completados aparecern aqu
                  </p>
                </div>
              )}
            </div>
          </motion.div>
        </div>

        {/* Profile Information */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5, delay: 0.5 }}
          className="mt-8"
        >
          <div className="card">
            <div className="card-header">
              <h3 className="card-title">Informacin Personal</h3>
              <Link
                to="/profile/edit"
                className="text-sm text-primary-600 hover:text-primary-700"
              >
                Editar
              </Link>
            </div>
            <div className="card-body">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Nombre completo
                  </label>
                  <p className="text-gray-900">
                    {user?.first_name && user?.last_name
                      ? `${user.first_name} ${user.last_name}`
                      : user?.name || 'No especificado'
                    }
                  </p>
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Correo electrnico
                  </label>
                  <p className="text-gray-900">{user?.email}</p>
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Telfono
                  </label>
                  <p className="text-gray-900">{user?.phone || 'No especificado'}</p>
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Ubicacin
                  </label>
                  <p className="text-gray-900">
                    {client?.preferred_location || 'No especificada'}
                  </p>
                </div>
              </div>
            </div>
          </div>
        </motion.div>
      </div>
    </div>
  )
}