import React, { useState } from 'react'
import { motion } from 'framer-motion'
import { useTranslation } from 'react-i18next'
import {
  CheckIcon,
  ExclamationTriangleIcon,
  ArrowRightIcon,
  MapPinIcon
} from '@heroicons/react/24/outline'
import { useStylistRegistration } from '../../../hooks/useStylistRegistration'
import LocationPicker from '../../../components/ui/LocationPicker'

export default function StylistStep3Location() {
  const { t } = useTranslation()
  const {
    formData,
    fieldErrors,
    updateField,
    completeCurrentStepAndNext,
    validateStep
  } = useStylistRegistration()

  const [touchedFields, setTouchedFields] = useState<Record<string, boolean>>({})

  // Real-time validation for current step
  const currentStepValidation = validateStep(3)
  const canProceed = currentStepValidation.isValid

  const handleLocationChange = (location: {
    address: string
    latitude: number
    longitude: number
    city?: string
    state?: string
  }) => {
    updateField('locationAddress', location.address)
    updateField('latitude', location.latitude)
    updateField('longitude', location.longitude)

    if (location.city) {
      updateField('locationCity', location.city)
    }
    if (location.state) {
      updateField('locationState', location.state)
    }

    // Mark location as touched when user selects a location
    setTouchedFields(prev => ({ ...prev, locationAddress: true }))
  }

  const handleNext = () => {
    // Mark fields as touched to show validation errors
    setTouchedFields({
      locationAddress: true,
    })

    if (canProceed) {
      completeCurrentStepAndNext()
    }
  }

  const getFieldError = (field: string): string => {
    return fieldErrors[field] || ''
  }

  const hasLocationData = formData.locationAddress && formData.latitude && formData.longitude

  // Get API key from environment
  const apiKey = import.meta.env.VITE_GOOGLE_MAPS_API_KEY || ''

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
      className="space-y-6"
    >
      {/* Introduction */}
      <div className="text-center mb-8">
        <div className="flex justify-center mb-4">
          <MapPinIcon className="h-12 w-12 text-primary-500" />
        </div>
        <h3 className="text-2xl font-semibold text-gray-900 mb-2">
          {t('stylistRegistration.location.title')}
        </h3>
        <p className="text-gray-600">
          {t('stylistRegistration.location.subtitle')}
        </p>
      </div>


      {/* Location Picker */}
      <div className="space-y-4">
        <label className="block text-sm font-medium text-gray-700">
          {t('stylistRegistration.location.label')} <span className="text-red-500">*</span>
        </label>

        {/* Custom Location Picker Component */}
        <div className="border-2 border-dashed border-gray-300 rounded-lg p-6 bg-gray-50">
          <LocationPicker
            value={hasLocationData ? {
              address: formData.locationAddress,
              latitude: formData.latitude,
              longitude: formData.longitude,
              city: formData.locationCity,
              state: formData.locationState
            } : undefined}
            onChange={handleLocationChange}
            error={touchedFields.locationAddress ? getFieldError('locationAddress') : undefined}
            apiKey={apiKey}
          />
        </div>

        {touchedFields.locationAddress && getFieldError('locationAddress') && (
          <motion.p
            initial={{ opacity: 0, y: -5 }}
            animate={{ opacity: 1, y: 0 }}
            className="text-red-600 text-sm flex items-center space-x-1"
          >
            <ExclamationTriangleIcon className="h-4 w-4" />
            <span>{getFieldError('locationAddress')}</span>
          </motion.p>
        )}

        {/* Location Summary */}
        {hasLocationData && (
          <motion.div
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            className="bg-green-50 border border-green-200 rounded-lg p-4"
          >
            <div className="flex items-center space-x-2 mb-2">
              <CheckIcon className="h-5 w-5 text-green-600" />
              <h4 className="text-sm font-medium text-green-800">
                {t('auth.stylistSteps.step3.locationSetSuccess')}
              </h4>
            </div>
            <div className="text-sm text-green-700">
              <p><strong>{t('auth.review.address')}:</strong> {formData.locationAddress}</p>
              {formData.locationCity && (
                <p><strong>{t('auth.review.city')}:</strong> {formData.locationCity}</p>
              )}
              {formData.locationState && (
                <p><strong>{t('auth.review.state')}:</strong> {formData.locationState}</p>
              )}
              <p className="text-xs text-green-600 mt-2">
                {t('auth.stylistSteps.step3.coordinates')}: {formData.latitude.toFixed(6)}, {formData.longitude.toFixed(6)}
              </p>
            </div>
          </motion.div>
        )}
      </div>


      {/* Next Button */}
      <div className="flex justify-end pt-6 border-t border-gray-200">
        <motion.button
          onClick={handleNext}
          disabled={!canProceed}
          className={`flex items-center space-x-2 px-6 py-3 rounded-lg font-medium transition-all duration-200 ${
            canProceed
              ? 'bg-gradient-to-r from-primary-500 to-secondary-500 text-white hover:shadow-lg transform hover:scale-105'
              : 'bg-gray-300 text-gray-500 cursor-not-allowed'
          }`}
          whileHover={canProceed ? { scale: 1.05 } : {}}
          whileTap={canProceed ? { scale: 0.95 } : {}}
        >
          <span>{t('stylistRegistration.location.nextButton')}</span>
          <ArrowRightIcon className="h-4 w-4" />
        </motion.button>
      </div>

      {/* Validation Summary */}
      {!canProceed && Object.keys(fieldErrors).length > 0 && (
        <motion.div
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          className="mt-4 p-4 bg-yellow-50 border border-yellow-200 rounded-lg"
        >
          <div className="flex items-center space-x-2 mb-2">
            <ExclamationTriangleIcon className="h-5 w-5 text-yellow-600" />
            <h4 className="text-sm font-medium text-yellow-800">
              {t('auth.stylistSteps.step3.validationMessage')}
            </h4>
          </div>
          <ul className="text-sm text-yellow-700 space-y-1">
            {Object.entries(fieldErrors).map(([field, error]) => (
              <li key={field}> {error}</li>
            ))}
          </ul>
        </motion.div>
      )}

      {/* Help Text */}
      <div className="text-center text-sm text-gray-500">
        <p>
          {t('auth.stylistSteps.step3.helpText')}{' '}
          <button className="text-primary-600 hover:text-primary-700 underline">
            {t('auth.stylistSteps.step3.contactSupport')}
          </button>
        </p>
      </div>
    </motion.div>
  )
}