import { useState, useEffect } from 'react'
import { Link, useNavigate, useSearchParams } from 'react-router-dom'
import { motion } from 'framer-motion'
import { useForm } from 'react-hook-form'
import { useTranslation } from 'react-i18next'
import {
  EyeIcon,
  EyeSlashIcon,
  SparklesIcon,
  ArrowRightIcon,
  CheckCircleIcon
} from '@heroicons/react/24/outline'
import axios from 'axios'
import toast from 'react-hot-toast'

interface ResetPasswordForm {
  password: string
  confirmPassword: string
}

export default function ResetPasswordPage() {
  const [showPassword, setShowPassword] = useState(false)
  const [showConfirmPassword, setShowConfirmPassword] = useState(false)
  const [isLoading, setIsLoading] = useState(false)
  const [isSuccess, setIsSuccess] = useState(false)
  const [isValidToken, setIsValidToken] = useState(false)
  const [isCheckingToken, setIsCheckingToken] = useState(true)
  const [userEmail, setUserEmail] = useState('')
  const [searchParams] = useSearchParams()
  const navigate = useNavigate()
  const { t } = useTranslation()

  const token = searchParams.get('token')

  const {
    register,
    handleSubmit,
    watch,
    formState: { errors },
  } = useForm<ResetPasswordForm>()

  const password = watch('password')

  useEffect(() => {
    if (!token) {
      navigate('/forgot-password')
      return
    }

    // Verify token
    const verifyToken = async () => {
      try {
        const response = await axios.get(`/api/auth/reset-password/${token}`)
        if (response.data.success) {
          setIsValidToken(true)
          setUserEmail(response.data.data.email)
        } else {
          toast.error(t('auth.resetPassword.invalidToken'))
          navigate('/forgot-password')
        }
      } catch (error) {
        toast.error(t('auth.resetPassword.invalidToken'))
        navigate('/forgot-password')
      } finally {
        setIsCheckingToken(false)
      }
    }

    verifyToken()
  }, [token, navigate])

  const onSubmit = async (data: ResetPasswordForm) => {
    setIsLoading(true)

    try {
      const response = await axios.post('/api/auth/reset-password', {
        token,
        password: data.password
      })

      if (response.data.success) {
        setIsSuccess(true)
        toast.success(t('auth.resetPassword.resetSuccess'))
        setTimeout(() => {
          navigate('/login')
        }, 2000)
      }
    } catch (error: any) {
      toast.error(error.response?.data?.message || t('auth.resetPassword.resetError'))
    } finally {
      setIsLoading(false)
    }
  }

  if (isCheckingToken) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-primary-50 to-secondary-50 flex items-center justify-center">
        <div className="loading-spinner-lg" />
      </div>
    )
  }

  if (isSuccess) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-primary-50 to-secondary-50 flex items-center justify-center py-12 px-4 sm:px-6 lg:px-8">
        <div className="max-w-md w-full">
          <motion.div
            initial={{ opacity: 0, scale: 0.95 }}
            animate={{ opacity: 1, scale: 1 }}
            transition={{ duration: 0.6 }}
            className="text-center"
          >
            <div className="mx-auto w-20 h-20 bg-green-100 rounded-full flex items-center justify-center mb-6">
              <CheckCircleIcon className="h-12 w-12 text-green-600" />
            </div>

            <h2 className="text-3xl font-serif font-bold text-gray-900 mb-4">
              {t('auth.resetPassword.successTitle')}
            </h2>

            <p className="text-gray-600 mb-8">
              {t('auth.resetPassword.successMessage')}
            </p>

            <Link
              to="/login"
              className="btn btn-primary inline-flex items-center space-x-2"
            >
              <span>{t('auth.resetPassword.goToLogin')}</span>
              <ArrowRightIcon className="h-4 w-4" />
            </Link>
          </motion.div>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-primary-50 to-secondary-50 flex items-center justify-center py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-md w-full space-y-8">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
          className="text-center"
        >
          {/* Logo */}
          <Link to="/" className="inline-flex items-center space-x-2 mb-8">
            <SparklesIcon className="h-10 w-10 text-primary-600" />
            <span className="text-2xl font-serif font-bold gradient-text">
              BeautyCita
            </span>
          </Link>

          {/* Title */}
          <h2 className="text-3xl font-serif font-bold text-gray-900 mb-2">
            {t('auth.resetPassword.title')}
          </h2>
          <p className="text-gray-600">
            {t('auth.resetPassword.subtitle')}
          </p>
        </motion.div>

        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, delay: 0.1 }}
          className="card"
        >
          <div className="card-body">
            <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
              {/* New Password */}
              <div className="form-group">
                <label htmlFor="password" className="label">
                  {t('auth.resetPassword.newPasswordLabel')}
                </label>
                <div className="relative">
                  <input
                    {...register('password', {
                      required: t('auth.validation.passwordRequired'),
                      minLength: {
                        value: 8,
                        message: t('auth.validation.passwordMinLength')
                      },
                      pattern: {
                        value: /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)/,
                        message: t('auth.validation.passwordComplexity')
                      }
                    })}
                    type={showPassword ? 'text' : 'password'}
                    id="password"
                    className={`input pr-10 ${errors.password ? 'input-error' : ''}`}
                    placeholder={t('auth.placeholders.password')}
                    autoComplete="new-password"
                  />
                  <button
                    type="button"
                    onClick={() => setShowPassword(!showPassword)}
                    className="absolute inset-y-0 right-0 pr-3 flex items-center text-gray-400 hover:text-gray-600"
                  >
                    {showPassword ? (
                      <EyeSlashIcon className="h-5 w-5" />
                    ) : (
                      <EyeIcon className="h-5 w-5" />
                    )}
                  </button>
                </div>
                {errors.password && (
                  <p className="form-error">{errors.password.message}</p>
                )}
              </div>

              {/* Confirm Password */}
              <div className="form-group">
                <label htmlFor="confirmPassword" className="label">
                  {t('auth.resetPassword.confirmPasswordLabel')}
                </label>
                <div className="relative">
                  <input
                    {...register('confirmPassword', {
                      required: t('auth.validation.confirmPasswordRequired'),
                      validate: (value) =>
                        value === password || t('auth.validation.passwordMismatch')
                    })}
                    type={showConfirmPassword ? 'text' : 'password'}
                    id="confirmPassword"
                    className={`input pr-10 ${errors.confirmPassword ? 'input-error' : ''}`}
                    placeholder={t('auth.placeholders.password')}
                    autoComplete="new-password"
                  />
                  <button
                    type="button"
                    onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                    className="absolute inset-y-0 right-0 pr-3 flex items-center text-gray-400 hover:text-gray-600"
                  >
                    {showConfirmPassword ? (
                      <EyeSlashIcon className="h-5 w-5" />
                    ) : (
                      <EyeIcon className="h-5 w-5" />
                    )}
                  </button>
                </div>
                {errors.confirmPassword && (
                  <p className="form-error">{errors.confirmPassword.message}</p>
                )}
              </div>

              {/* Submit button */}
              <button
                type="submit"
                disabled={isLoading}
                className="btn btn-primary btn-lg w-full flex items-center justify-center space-x-2"
              >
                {isLoading ? (
                  <div className="loading-spinner" />
                ) : (
                  <>
                    <span>{t('auth.resetPassword.resetPasswordButton')}</span>
                    <ArrowRightIcon className="h-4 w-4" />
                  </>
                )}
              </button>
            </form>

            {/* Back to login */}
            <div className="mt-6 text-center">
              <Link
                to="/login"
                className="text-sm text-gray-600 hover:text-primary-700"
              >
                {t('auth.links.backToLogin')}
              </Link>
            </div>
          </div>
        </motion.div>
      </div>
    </div>
  )
}