import { useState } from 'react'
import { Link } from 'react-router-dom'
import { motion } from 'framer-motion'
import { useForm } from 'react-hook-form'
import { useTranslation } from 'react-i18next'
import {
  SparklesIcon,
  ArrowRightIcon,
  EnvelopeIcon,
  CheckCircleIcon
} from '@heroicons/react/24/outline'
import axios from 'axios'
import toast from 'react-hot-toast'

interface ForgotPasswordForm {
  email: string
}

export default function ForgotPasswordPage() {
  const [isLoading, setIsLoading] = useState(false)
  const [isSuccess, setIsSuccess] = useState(false)
  const { t } = useTranslation()

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm<ForgotPasswordForm>()

  const onSubmit = async (data: ForgotPasswordForm) => {
    setIsLoading(true)

    try {
      const response = await axios.post('/api/auth/forgot-password', data)

      if (response.data.success) {
        setIsSuccess(true)
        toast.success(t('auth.forgotPassword.resetEmailSent'))

        // In development, show the link
        if (response.data.resetLink) {
          console.log('Password reset link:', response.data.resetLink)
        }
      }
    } catch (error: any) {
      toast.error(error.response?.data?.message || t('auth.forgotPassword.resetEmailError'))
    } finally {
      setIsLoading(false)
    }
  }

  if (isSuccess) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-primary-50 to-secondary-50 flex items-center justify-center py-12 px-4 sm:px-6 lg:px-8">
        <div className="max-w-md w-full">
          <motion.div
            initial={{ opacity: 0, scale: 0.95 }}
            animate={{ opacity: 1, scale: 1 }}
            transition={{ duration: 0.6 }}
            className="text-center"
          >
            <div className="mx-auto w-20 h-20 bg-green-100 rounded-full flex items-center justify-center mb-6">
              <CheckCircleIcon className="h-12 w-12 text-green-600" />
            </div>

            <h2 className="text-3xl font-serif font-bold text-gray-900 mb-4">
              {t('auth.forgotPassword.checkEmailTitle')}
            </h2>

            <p className="text-gray-600 mb-8">
              {t('auth.forgotPassword.checkEmailMessage')}
            </p>

            <Link
              to="/login"
              className="btn btn-primary inline-flex items-center space-x-2"
            >
              <span>{t('auth.resetPassword.goToLogin')}</span>
              <ArrowRightIcon className="h-4 w-4" />
            </Link>
          </motion.div>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-primary-50 to-secondary-50 flex items-center justify-center py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-md w-full space-y-8">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
          className="text-center"
        >
          {/* Logo */}
          <Link to="/" className="inline-flex items-center space-x-2 mb-8">
            <SparklesIcon className="h-10 w-10 text-primary-600" />
            <span className="text-2xl font-serif font-bold gradient-text">
              BeautyCita
            </span>
          </Link>

          {/* Title */}
          <h2 className="text-3xl font-serif font-bold text-gray-900 mb-2">
            {t('auth.forgotPassword.title')}
          </h2>
          <p className="text-gray-600">
            {t('auth.forgotPassword.subtitle')}
          </p>
        </motion.div>

        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, delay: 0.1 }}
          className="card"
        >
          <div className="card-body">
            <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
              {/* Email */}
              <div className="form-group">
                <label htmlFor="email" className="label">
                  {t('auth.forgotPassword.emailLabel')}
                </label>
                <div className="relative">
                  <input
                    {...register('email', {
                      required: t('auth.forgotPassword.emailRequired'),
                      pattern: {
                        value: /^\S+@\S+$/i,
                        message: t('auth.forgotPassword.emailInvalid')
                      }
                    })}
                    type="email"
                    id="email"
                    className={`input pl-10 ${errors.email ? 'input-error' : ''}`}
                    placeholder={t('auth.forgotPassword.emailPlaceholder')}
                    autoComplete="email"
                  />
                  <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                    <EnvelopeIcon className="h-5 w-5 text-gray-400" />
                  </div>
                </div>
                {errors.email && (
                  <p className="form-error">{errors.email.message}</p>
                )}
              </div>

              {/* Submit button */}
              <button
                type="submit"
                disabled={isLoading}
                className="btn btn-primary btn-lg w-full flex items-center justify-center space-x-2"
              >
                {isLoading ? (
                  <div className="loading-spinner" />
                ) : (
                  <>
                    <span>{t('auth.forgotPassword.sendResetLink')}</span>
                    <ArrowRightIcon className="h-4 w-4" />
                  </>
                )}
              </button>
            </form>

            {/* Back to login */}
            <div className="mt-6 text-center">
              <Link
                to="/login"
                className="text-sm text-gray-600 hover:text-primary-700"
              >
                {t('auth.links.backToLogin')}
              </Link>
            </div>
          </div>
        </motion.div>
      </div>
    </div>
  )
}