import React, { useState } from 'react'
import { motion } from 'framer-motion'
import { useTranslation } from 'react-i18next'
import {
  CheckIcon,
  ExclamationTriangleIcon,
  ArrowRightIcon,
  BriefcaseIcon,
  SparklesIcon
} from '@heroicons/react/24/outline'
import { useStylistRegistration } from '../../../hooks/useStylistRegistration'

// Available specialties for selection
const availableSpecialties = [
  'Hair Cutting',
  'Hair Coloring',
  'Hair Styling',
  'Hair Extensions',
  'Highlights & Lowlights',
  'Makeup Application',
  'Bridal Makeup',
  'Special Event Makeup',
  'Eyebrow Shaping',
  'Eyebrow Tinting',
  'Eyelash Extensions',
  'Eyelash Lift & Tint',
  'Facial Treatments',
  'Skincare Consultation',
  'Manicure',
  'Pedicure',
  'Nail Art',
  'Gel Nails',
  'Acrylic Nails',
  'Bridal Styling',
  'Special Event Styling'
]

const experienceOptions = [
  { value: '1', label: '1 year' },
  { value: '2', label: '2 years' },
  { value: '3', label: '3 years' },
  { value: '4', label: '4 years' },
  { value: '5', label: '5 years' },
  { value: '7', label: '7+ years' },
  { value: '10', label: '10+ years' }
]

export default function StylistStep2Professional() {
  const { t } = useTranslation()
  const {
    formData,
    fieldErrors,
    updateField,
    completeCurrentStepAndNext,
    validateStep
  } = useStylistRegistration()

  const [touchedFields, setTouchedFields] = useState<Record<string, boolean>>({})

  // Real-time validation for current step
  const currentStepValidation = validateStep(2)
  const canProceed = currentStepValidation.isValid

  const handleFieldChange = (field: string, value: any) => {
    updateField(field as any, value)
  }

  const handleFieldBlur = (field: string) => {
    setTouchedFields(prev => ({ ...prev, [field]: true }))
  }

  const handleSpecialtyToggle = (specialty: string) => {
    const currentSpecialties = formData.specialties || []
    const updatedSpecialties = currentSpecialties.includes(specialty)
      ? currentSpecialties.filter(s => s !== specialty)
      : [...currentSpecialties, specialty]

    handleFieldChange('specialties', updatedSpecialties)
  }

  const handleNext = () => {
    // Mark all fields as touched to show validation errors
    setTouchedFields({
      businessName: true,
      experience: true,
      specialties: true,
      bio: true,
    })

    if (canProceed) {
      completeCurrentStepAndNext()
    }
  }

  const getFieldError = (field: string): string => {
    return fieldErrors[field] || ''
  }

  const getFieldStatus = (field: string, value: any): 'error' | 'success' | 'default' => {
    if (!touchedFields[field]) return 'default'
    if (getFieldError(field)) return 'error'
    if (field === 'specialties') {
      return Array.isArray(value) && value.length > 0 ? 'success' : 'default'
    }
    if (value && (typeof value === 'string' ? value.length > 0 : true)) return 'success'
    return 'default'
  }

  const getFieldClassName = (field: string, value: any) => {
    const status = getFieldStatus(field, value)
    const baseClass = 'w-full p-3 border rounded-lg focus:ring-2 transition-all duration-200'

    switch (status) {
      case 'error':
        return `${baseClass} border-red-300 bg-red-50 focus:ring-red-500 focus:border-red-500`
      case 'success':
        return `${baseClass} border-green-300 bg-green-50 focus:ring-green-500 focus:border-green-500`
      default:
        return `${baseClass} border-gray-300 focus:ring-primary-500 focus:border-primary-500`
    }
  }

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
      className="space-y-6"
    >
      {/* Introduction */}
      <div className="text-center mb-8">
        <div className="flex justify-center mb-4">
          <BriefcaseIcon className="h-12 w-12 text-primary-500" />
        </div>
        <h3 className="text-2xl font-semibold text-gray-900 mb-2">
          {t('auth.stylistSteps.step2.heading')}
        </h3>
        <p className="text-gray-600">
          {t('auth.stylistSteps.step2.subheading')}
        </p>
      </div>

      {/* Form Fields */}
      <div className="space-y-6">
        {/* Business Name */}
        <div className="space-y-2">
          <label className="block text-sm font-medium text-gray-700">
            {t('auth.stylistSteps.step2.businessNameLabel')} <span className="text-red-500">*</span>
          </label>
          <div className="relative">
            <input
              type="text"
              value={formData.businessName}
              onChange={(e) => handleFieldChange('businessName', e.target.value)}
              onBlur={() => handleFieldBlur('businessName')}
              className={getFieldClassName('businessName', formData.businessName)}
              placeholder={t('auth.stylistSteps.step2.businessNamePlaceholder')}
            />
            {getFieldStatus('businessName', formData.businessName) === 'success' && (
              <CheckIcon className="absolute right-3 top-3 h-5 w-5 text-green-500" />
            )}
          </div>
          {touchedFields.businessName && getFieldError('businessName') && (
            <motion.p
              initial={{ opacity: 0, y: -5 }}
              animate={{ opacity: 1, y: 0 }}
              className="text-red-600 text-sm flex items-center space-x-1"
            >
              <ExclamationTriangleIcon className="h-4 w-4" />
              <span>{getFieldError('businessName')}</span>
            </motion.p>
          )}
          <p className="text-xs text-gray-500">
            {t('auth.stylistSteps.step2.businessNameHint')}
          </p>
        </div>

        {/* Years of Experience */}
        <div className="space-y-2">
          <label className="block text-sm font-medium text-gray-700">
            {t('auth.stylistSteps.step2.experienceLabel')} <span className="text-red-500">*</span>
          </label>
          <div className="relative">
            <select
              value={formData.experience}
              onChange={(e) => handleFieldChange('experience', e.target.value)}
              onBlur={() => handleFieldBlur('experience')}
              className={getFieldClassName('experience', formData.experience)}
            >
              <option value="">{t('auth.stylistSteps.step2.experiencePlaceholder')}</option>
              {experienceOptions.map(option => (
                <option key={option.value} value={option.value}>
                  {option.label}
                </option>
              ))}
            </select>
            {getFieldStatus('experience', formData.experience) === 'success' && (
              <CheckIcon className="absolute right-8 top-3 h-5 w-5 text-green-500" />
            )}
          </div>
          {touchedFields.experience && getFieldError('experience') && (
            <motion.p
              initial={{ opacity: 0, y: -5 }}
              animate={{ opacity: 1, y: 0 }}
              className="text-red-600 text-sm flex items-center space-x-1"
            >
              <ExclamationTriangleIcon className="h-4 w-4" />
              <span>{getFieldError('experience')}</span>
            </motion.p>
          )}
        </div>

        {/* Specialties */}
        <div className="space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              {t('auth.stylistSteps.step2.specialtiesLabel')} <span className="text-red-500">*</span>
            </label>
            <p className="text-sm text-gray-600 mb-3">
              {t('auth.stylistSteps.step2.specialtiesDescription')}
            </p>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
            {availableSpecialties.map((specialty) => {
              const isSelected = formData.specialties?.includes(specialty) || false
              return (
                <motion.label
                  key={specialty}
                  className={`flex items-center p-3 border-2 rounded-lg cursor-pointer transition-all duration-200 ${
                    isSelected
                      ? 'border-primary-300 bg-primary-50 ring-2 ring-primary-200'
                      : 'border-gray-200 hover:border-gray-300 hover:bg-gray-50'
                  }`}
                  whileHover={{ scale: 1.02 }}
                  whileTap={{ scale: 0.98 }}
                >
                  <input
                    type="checkbox"
                    checked={isSelected}
                    onChange={() => handleSpecialtyToggle(specialty)}
                    className="sr-only"
                  />
                  <div className={`flex-shrink-0 w-5 h-5 rounded border-2 flex items-center justify-center mr-3 ${
                    isSelected
                      ? 'bg-primary-500 border-primary-500 text-white'
                      : 'border-gray-300'
                  }`}>
                    {isSelected && <CheckIcon className="w-3 h-3" />}
                  </div>
                  <span className={`text-sm font-medium ${
                    isSelected ? 'text-primary-700' : 'text-gray-700'
                  }`}>
                    {specialty}
                  </span>
                  {specialty.includes('Bridal') && (
                    <SparklesIcon className="w-4 h-4 text-yellow-500 ml-1" />
                  )}
                </motion.label>
              )
            })}
          </div>

          {touchedFields.specialties && getFieldError('specialties') && (
            <motion.p
              initial={{ opacity: 0, y: -5 }}
              animate={{ opacity: 1, y: 0 }}
              className="text-red-600 text-sm flex items-center space-x-1"
            >
              <ExclamationTriangleIcon className="h-4 w-4" />
              <span>{getFieldError('specialties')}</span>
            </motion.p>
          )}

          {formData.specialties && formData.specialties.length > 0 && (
            <div className="p-3 bg-green-50 border border-green-200 rounded-lg">
              <p className="text-sm text-green-700">
                 {formData.specialties.length} {t('auth.stylistSteps.step2.servicesSelected')}
              </p>
            </div>
          )}
        </div>

        {/* Biography */}
        <div className="space-y-2">
          <label className="block text-sm font-medium text-gray-700">
            {t('auth.stylistSteps.step2.bioLabel')}
          </label>
          <div className="relative">
            <textarea
              value={formData.bio}
              onChange={(e) => handleFieldChange('bio', e.target.value)}
              onBlur={() => handleFieldBlur('bio')}
              rows={4}
              className={getFieldClassName('bio', formData.bio)}
              placeholder={t('auth.stylistSteps.step2.bioPlaceholder')}
            />
          </div>
          <div className="flex justify-between items-center">
            <p className="text-xs text-gray-500">
              {t('auth.stylistSteps.step2.bioHint')}
            </p>
            <span className="text-xs text-gray-400">
              {formData.bio?.length || 0}/500
            </span>
          </div>
        </div>
      </div>

      {/* Next Button */}
      <div className="flex justify-end pt-6 border-t border-gray-200">
        <motion.button
          onClick={handleNext}
          disabled={!canProceed}
          className={`flex items-center space-x-2 px-6 py-3 rounded-lg font-medium transition-all duration-200 ${
            canProceed
              ? 'bg-gradient-to-r from-primary-500 to-secondary-500 text-white hover:shadow-lg transform hover:scale-105'
              : 'bg-gray-300 text-gray-500 cursor-not-allowed'
          }`}
          whileHover={canProceed ? { scale: 1.05 } : {}}
          whileTap={canProceed ? { scale: 0.95 } : {}}
        >
          <span>Continue to Location Setup</span>
          <ArrowRightIcon className="h-4 w-4" />
        </motion.button>
      </div>

      {/* Validation Summary */}
      {!canProceed && Object.keys(fieldErrors).length > 0 && (
        <motion.div
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          className="mt-4 p-4 bg-yellow-50 border border-yellow-200 rounded-lg"
        >
          <div className="flex items-center space-x-2 mb-2">
            <ExclamationTriangleIcon className="h-5 w-5 text-yellow-600" />
            <h4 className="text-sm font-medium text-yellow-800">
              Please complete the following to continue:
            </h4>
          </div>
          <ul className="text-sm text-yellow-700 space-y-1">
            {Object.entries(fieldErrors).map(([field, error]) => (
              <li key={field}> {error}</li>
            ))}
          </ul>
        </motion.div>
      )}

      {/* Success Message */}
      {canProceed && (
        <motion.div
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          className="mt-4 p-4 bg-green-50 border border-green-200 rounded-lg"
        >
          <div className="flex items-center space-x-2">
            <CheckIcon className="h-5 w-5 text-green-600" />
            <h4 className="text-sm font-medium text-green-800">
              Great! Your professional information looks complete.
            </h4>
          </div>
          <p className="text-sm text-green-700 mt-1">
            You're ready to set up your service location.
          </p>
        </motion.div>
      )}
    </motion.div>
  )
}