import React, { useState, useRef } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { useTranslation } from 'react-i18next'
import {
  PhotoIcon,
  XMarkIcon,
  ArrowRightIcon,
  PlusIcon,
  InformationCircleIcon,
  LinkIcon,
  CheckIcon
} from '@heroicons/react/24/outline'
import { useStylistRegistration } from '../../../hooks/useStylistRegistration'

export default function StylistStep4Portfolio() {
  const { t } = useTranslation()
  const {
    formData,
    updateField,
    completeCurrentStepAndNext
  } = useStylistRegistration()

  const [portfolioPreviews, setPortfolioPreviews] = useState<string[]>([])
  const [dragActive, setDragActive] = useState(false)
  const [uploadError, setUploadError] = useState<string>('')
  const fileInputRef = useRef<HTMLInputElement>(null)

  const maxFiles = 6
  const maxFileSize = 5 * 1024 * 1024 // 5MB
  const allowedTypes = ['image/jpeg', 'image/jpg', 'image/png', 'image/webp']

  const handleInstagramChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    updateField('instagramUrl', e.target.value)
  }

  const validateFile = (file: File): string | null => {
    if (!allowedTypes.includes(file.type)) {
      return `${file.name}: Only JPEG, PNG, and WebP images are allowed`
    }
    if (file.size > maxFileSize) {
      return `${file.name}: File size must be less than 5MB`
    }
    return null
  }

  const handleFileUpload = (files: FileList | null) => {
    if (!files) return

    const fileArray = Array.from(files)
    const currentFiles = formData.portfolioImages || []

    // Check total file limit
    if (currentFiles.length + fileArray.length > maxFiles) {
      setUploadError(`You can only upload up to ${maxFiles} images`)
      return
    }

    // Validate each file
    const validFiles: File[] = []
    const errors: string[] = []

    fileArray.forEach(file => {
      const error = validateFile(file)
      if (error) {
        errors.push(error)
      } else {
        validFiles.push(file)
      }
    })

    if (errors.length > 0) {
      setUploadError(errors.join('; '))
      return
    }

    // Clear any previous errors
    setUploadError('')

    // Add new files
    const updatedFiles = [...currentFiles, ...validFiles]
    updateField('portfolioImages', updatedFiles)

    // Create previews for new files
    validFiles.forEach(file => {
      const reader = new FileReader()
      reader.onload = (e) => {
        if (e.target?.result) {
          setPortfolioPreviews(prev => [...prev, e.target!.result as string])
        }
      }
      reader.readAsDataURL(file)
    })
  }

  const removeImage = (index: number) => {
    const currentFiles = formData.portfolioImages || []
    const updatedFiles = currentFiles.filter((_, i) => i !== index)
    updateField('portfolioImages', updatedFiles)

    setPortfolioPreviews(prev => prev.filter((_, i) => i !== index))
  }

  const handleDrag = (e: React.DragEvent) => {
    e.preventDefault()
    e.stopPropagation()
    if (e.type === 'dragenter' || e.type === 'dragover') {
      setDragActive(true)
    } else if (e.type === 'dragleave') {
      setDragActive(false)
    }
  }

  const handleDrop = (e: React.DragEvent) => {
    e.preventDefault()
    e.stopPropagation()
    setDragActive(false)

    const files = e.dataTransfer.files
    handleFileUpload(files)
  }

  const openFileDialog = () => {
    fileInputRef.current?.click()
  }

  const handleNext = () => {
    // Step 4 is optional, so we can always proceed
    completeCurrentStepAndNext()
  }

  const hasPortfolioContent = (formData.portfolioImages && formData.portfolioImages.length > 0) || formData.instagramUrl

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
      className="space-y-6"
    >
      {/* Introduction */}
      <div className="text-center mb-8">
        <div className="flex justify-center mb-4">
          <PhotoIcon className="h-12 w-12 text-primary-500" />
        </div>
        <h3 className="text-2xl font-semibold text-gray-900 mb-2">
          Showcase your best work
        </h3>
        <p className="text-gray-600 mb-4">
          Upload photos of your work to attract clients
        </p>
        <div className="bg-blue-50 border border-blue-200 rounded-lg p-3 max-w-md mx-auto">
          <p className="text-sm text-blue-700">
             <strong>{t('common.optional')}</strong> - {t('auth.stylistSteps.step4.optionalNote')}
          </p>
        </div>
      </div>

      {/* Instagram URL */}
      <div className="space-y-3">
        <label className="block text-sm font-medium text-gray-700">
          <LinkIcon className="h-4 w-4 inline mr-1" />
          Instagram Profile (Optional)
        </label>
        <div className="relative">
          <input
            type="url"
            value={formData.instagramUrl || ''}
            onChange={handleInstagramChange}
            className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-primary-500 pl-10"
            placeholder="https://instagram.com/yourhandle"
          />
          <LinkIcon className="absolute left-3 top-3 h-5 w-5 text-gray-400" />
        </div>
        <p className="text-xs text-gray-500">
          Link your Instagram to showcase more of your work and build trust with clients
        </p>
      </div>

      {/* File Upload Section */}
      <div className="space-y-4">
        <label className="block text-sm font-medium text-gray-700">
          <PhotoIcon className="h-4 w-4 inline mr-1" />
          Portfolio Images (Optional)
        </label>

        {/* Upload Area */}
        <div
          className={`border-2 border-dashed rounded-lg p-8 text-center transition-all duration-200 ${
            dragActive
              ? 'border-primary-400 bg-primary-50'
              : 'border-gray-300 hover:border-gray-400 hover:bg-gray-50'
          }`}
          onDragEnter={handleDrag}
          onDragLeave={handleDrag}
          onDragOver={handleDrag}
          onDrop={handleDrop}
          onClick={openFileDialog}
        >
          <input
            ref={fileInputRef}
            type="file"
            multiple
            accept="image/*"
            onChange={(e) => handleFileUpload(e.target.files)}
            className="hidden"
          />

          <motion.div
            className="cursor-pointer"
            whileHover={{ scale: 1.02 }}
            whileTap={{ scale: 0.98 }}
          >
            <div className="flex justify-center mb-4">
              <div className={`p-3 rounded-full ${
                dragActive ? 'bg-primary-100' : 'bg-gray-100'
              }`}>
                <PhotoIcon className={`h-8 w-8 ${
                  dragActive ? 'text-primary-600' : 'text-gray-400'
                }`} />
              </div>
            </div>

            <h4 className="text-lg font-medium text-gray-900 mb-2">
              {dragActive ? t('auth.stylistSteps.step4.dropHere') : t('auth.stylistSteps.step4.uploadButton')}
            </h4>
            <p className="text-gray-600 mb-4">
              Drag and drop images here, or click to browse
            </p>

            <div className="text-sm text-gray-500 space-y-1">
              <p> Up to {maxFiles} images</p>
              <p> JPEG, PNG, WebP formats</p>
              <p> Max 5MB per image</p>
            </div>

            {formData.portfolioImages && formData.portfolioImages.length > 0 && (
              <div className="mt-4 inline-flex items-center px-3 py-1 bg-green-100 text-green-700 rounded-full text-sm">
                <CheckIcon className="h-4 w-4 mr-1" />
                {formData.portfolioImages.length} image{formData.portfolioImages.length !== 1 ? 's' : ''} selected
              </div>
            )}
          </motion.div>
        </div>

        {/* Upload Error */}
        {uploadError && (
          <motion.div
            initial={{ opacity: 0, y: -10 }}
            animate={{ opacity: 1, y: 0 }}
            className="p-3 bg-red-50 border border-red-200 rounded-lg text-red-700 text-sm"
          >
            {uploadError}
          </motion.div>
        )}

        {/* Image Previews */}
        <AnimatePresence>
          {portfolioPreviews.length > 0 && (
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -20 }}
              className="grid grid-cols-2 md:grid-cols-3 gap-4"
            >
              {portfolioPreviews.map((preview, index) => (
                <motion.div
                  key={index}
                  initial={{ opacity: 0, scale: 0.8 }}
                  animate={{ opacity: 1, scale: 1 }}
                  exit={{ opacity: 0, scale: 0.8 }}
                  className="relative group"
                >
                  <img
                    src={preview}
                    alt={`Portfolio ${index + 1}`}
                    className="w-full h-32 object-cover rounded-lg border"
                  />
                  <button
                    type="button"
                    onClick={(e) => {
                      e.stopPropagation()
                      removeImage(index)
                    }}
                    className="absolute -top-2 -right-2 bg-red-500 text-white rounded-full p-1 opacity-0 group-hover:opacity-100 transition-opacity shadow-md"
                  >
                    <XMarkIcon className="h-4 w-4" />
                  </button>
                  <div className="absolute inset-0 bg-black bg-opacity-0 group-hover:bg-opacity-10 rounded-lg transition-all duration-200" />
                </motion.div>
              ))}

              {/* Add More Button */}
              {portfolioPreviews.length < maxFiles && (
                <motion.button
                  onClick={openFileDialog}
                  className="h-32 border-2 border-dashed border-gray-300 rounded-lg flex flex-col items-center justify-center hover:border-gray-400 hover:bg-gray-50 transition-colors"
                  whileHover={{ scale: 1.02 }}
                  whileTap={{ scale: 0.98 }}
                >
                  <PlusIcon className="h-8 w-8 text-gray-400 mb-1" />
                  <span className="text-sm text-gray-500">Add More</span>
                </motion.button>
              )}
            </motion.div>
          )}
        </AnimatePresence>
      </div>

      {/* Tips Section */}
      <div className="bg-purple-50 border border-purple-200 rounded-lg p-4">
        <div className="flex items-start space-x-3">
          <InformationCircleIcon className="h-5 w-5 text-purple-500 flex-shrink-0 mt-0.5" />
          <div>
            <h4 className="text-sm font-medium text-purple-800 mb-2">
              {t('auth.stylistSteps.step4.tipsTitle')}
            </h4>
            <ul className="text-sm text-purple-700 space-y-1">
              <li> Show before and after transformations</li>
              <li> Include different styles and techniques</li>
              <li> Use good lighting and clear, sharp images</li>
              <li> Feature diverse hair types and skin tones</li>
              <li> Get permission from clients before using their photos</li>
            </ul>
          </div>
        </div>
      </div>

      {/* Next Button */}
      <div className="flex justify-end pt-6 border-t border-gray-200">
        <motion.button
          onClick={handleNext}
          className="flex items-center space-x-2 px-6 py-3 rounded-lg font-medium bg-gradient-to-r from-primary-500 to-secondary-500 text-white hover:shadow-lg transform hover:scale-105 transition-all duration-200"
          whileHover={{ scale: 1.05 }}
          whileTap={{ scale: 0.95 }}
        >
          <span>
            {hasPortfolioContent ? t('auth.stylistSteps.step4.continueButton') : t('auth.stylistSteps.step4.skipButton')}
          </span>
          <ArrowRightIcon className="h-4 w-4" />
        </motion.button>
      </div>

      {/* Success Message */}
      {hasPortfolioContent && (
        <motion.div
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          className="mt-4 p-4 bg-green-50 border border-green-200 rounded-lg"
        >
          <div className="flex items-center space-x-2">
            <CheckIcon className="h-5 w-5 text-green-600" />
            <h4 className="text-sm font-medium text-green-800">
              Great! Your portfolio will help attract more clients.
            </h4>
          </div>
          <p className="text-sm text-green-700 mt-1">
            You can always add more images later from your stylist dashboard.
          </p>
        </motion.div>
      )}

      {/* Skip Message */}
      {!hasPortfolioContent && (
        <motion.div
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          className="mt-4 p-4 bg-blue-50 border border-blue-200 rounded-lg"
        >
          <div className="flex items-center space-x-2">
            <InformationCircleIcon className="h-5 w-5 text-blue-600" />
            <h4 className="text-sm font-medium text-blue-800">
              No problem! You can add portfolio images later.
            </h4>
          </div>
          <p className="text-sm text-blue-700 mt-1">
            Focus on getting bookings first, then build your portfolio as you complete services.
          </p>
        </motion.div>
      )}
    </motion.div>
  )
}