import { motion, HTMLMotionProps } from 'framer-motion'
import { ReactNode } from 'react'
import { Link } from 'react-router-dom'

interface AnimatedButtonProps extends Omit<HTMLMotionProps<"button">, 'children'> {
  children: ReactNode
  variant?: 'primary' | 'secondary' | 'ghost'
  size?: 'sm' | 'md' | 'lg' | 'xl'
  to?: string
  onClick?: () => void
  className?: string
  isDarkMode?: boolean
}

export default function AnimatedButton({
  children,
  variant = 'primary',
  size = 'md',
  to,
  onClick,
  className = '',
  isDarkMode = false,
  ...motionProps
}: AnimatedButtonProps) {
  const sizeClasses = {
    sm: 'px-4 py-2 text-sm',
    md: 'px-6 py-3 text-base',
    lg: 'px-8 py-4 text-lg',
    xl: 'px-12 py-5 text-xl'
  }

  const variantClasses = {
    primary: 'bg-gradient-to-r from-pink-500 to-purple-600 text-white shadow-lg hover:shadow-xl',
    secondary: isDarkMode
      ? 'bg-white/10 backdrop-blur-md text-white border-2 border-white/30 hover:bg-white/20'
      : 'bg-gray-100 text-gray-900 border-2 border-gray-200 hover:bg-gray-200',
    ghost: isDarkMode
      ? 'bg-transparent text-white border-2 border-white/50 hover:bg-white/10'
      : 'bg-transparent text-gray-900 border-2 border-gray-300 hover:bg-gray-50'
  }

  const baseClasses = `
    ${sizeClasses[size]}
    ${variantClasses[variant]}
    font-semibold rounded-full
    transition-all duration-200
    transform-gpu
    will-change-transform
    ${className}
  `

  const motionVariants = {
    rest: { scale: 1 },
    hover: { scale: 1.05 },
    tap: { scale: 0.95 }
  }

  const Component = motion.create(to ? Link : 'button')

  return (
    <Component
      to={to}
      onClick={onClick}
      className={baseClasses}
      variants={motionVariants}
      initial="rest"
      whileHover="hover"
      whileTap="tap"
      {...motionProps}
    >
      {children}
    </Component>
  )
}