import React, { useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { motion } from 'framer-motion';
import {
  CheckCircleIcon,
  ExclamationTriangleIcon,
  XCircleIcon,
  ClockIcon,
  ArrowPathIcon
} from '@heroicons/react/24/outline';

interface ServiceStatus {
  status: 'operational' | 'degraded' | 'outage' | 'maintenance';
  message: string;
  value?: number;
  unit?: string;
  uptime?: number;
  error?: string;
  modelCount?: number;
  pid?: number;
  nodeVersion?: string;
}

interface StatusData {
  timestamp: string;
  overall: 'operational' | 'degraded' | 'outage' | 'maintenance';
  services: Record<string, ServiceStatus>;
}

const StatusPage: React.FC = () => {
  const { t } = useTranslation();
  const [statusData, setStatusData] = useState<StatusData | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [lastUpdated, setLastUpdated] = useState<Date>(new Date());

  // Fetch status data from API
  const fetchStatus = async () => {
    try {
      setError(null);
      const response = await fetch('/api/status');
      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }
      const data: StatusData = await response.json();
      setStatusData(data);
      setLastUpdated(new Date());
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to fetch status');
      console.error('Status fetch error:', err);
    } finally {
      setIsLoading(false);
    }
  };

  // Auto-refresh every 30 seconds
  useEffect(() => {
    fetchStatus();
    const interval = setInterval(fetchStatus, 30000);
    return () => clearInterval(interval);
  }, []);

  const getStatusIcon = (status: string) => {
    switch (status) {
      case 'operational':
        return <CheckCircleIcon className="h-6 w-6 text-green-500" />;
      case 'degraded':
        return <ExclamationTriangleIcon className="h-6 w-6 text-yellow-500" />;
      case 'outage':
        return <XCircleIcon className="h-6 w-6 text-red-500" />;
      case 'maintenance':
        return <ClockIcon className="h-6 w-6 text-blue-500" />;
      default:
        return <CheckCircleIcon className="h-6 w-6 text-green-500" />;
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'operational':
        return 'text-green-600';
      case 'degraded':
        return 'text-yellow-600';
      case 'outage':
        return 'text-red-600';
      case 'maintenance':
        return 'text-blue-600';
      default:
        return 'text-green-600';
    }
  };

  // Get overall status for banner
  const getOverallStatusBanner = () => {
    if (error || !statusData) {
      return (
        <div className="bg-red-100 border border-red-200 rounded-2xl p-6 mb-8">
          <div className="flex items-center justify-center space-x-3">
            <XCircleIcon className="h-8 w-8 text-red-600" />
            <span className="text-xl font-semibold text-red-800">
              {error ? 'Status Check Failed' : 'Loading...'}
            </span>
          </div>
        </div>
      );
    }

    const overall = statusData.overall;
    const colors = {
      operational: { bg: 'bg-green-100', border: 'border-green-200', text: 'text-green-800', icon: 'text-green-600' },
      degraded: { bg: 'bg-yellow-100', border: 'border-yellow-200', text: 'text-yellow-800', icon: 'text-yellow-600' },
      outage: { bg: 'bg-red-100', border: 'border-red-200', text: 'text-red-800', icon: 'text-red-600' },
      maintenance: { bg: 'bg-blue-100', border: 'border-blue-200', text: 'text-blue-800', icon: 'text-blue-600' }
    };

    const style = colors[overall];
    const messages = {
      operational: 'All Systems Operational',
      degraded: 'Some Systems Degraded',
      outage: 'Service Outage',
      maintenance: 'Maintenance Mode'
    };

    return (
      <div className={`${style.bg} ${style.border} rounded-2xl p-6 mb-8`}>
        <div className="flex items-center justify-center space-x-3">
          <div className={style.icon}>
            {getStatusIcon(overall)}
          </div>
          <span className={`text-xl font-semibold ${style.text}`}>
            {messages[overall]}
          </span>
        </div>
      </div>
    );
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-green-50 via-white to-emerald-50">
      <section className="py-20 px-4">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          className="container mx-auto max-w-4xl text-center"
        >
          <h1 className="text-4xl md:text-5xl font-bold mb-6 bg-gradient-to-r from-green-600 to-emerald-600 bg-clip-text text-transparent">
            BeautyCita Status
          </h1>
          <p className="text-xl text-gray-600 mb-8">Real-time service monitoring</p>

          {getOverallStatusBanner()}

          <div className="bg-white rounded-2xl shadow-lg p-8">
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-2xl font-bold text-gray-900">System Status</h2>
              <button
                onClick={fetchStatus}
                disabled={isLoading}
                className="flex items-center space-x-2 px-4 py-2 bg-gray-100 hover:bg-gray-200 rounded-lg transition-colors"
              >
                <ArrowPathIcon className={`h-4 w-4 ${isLoading ? 'animate-spin' : ''}`} />
                <span className="text-sm">Refresh</span>
              </button>
            </div>

            {isLoading && !statusData ? (
              <div className="flex items-center justify-center py-12">
                <div className="loading-spinner"></div>
                <span className="ml-3 text-gray-600">Loading status...</span>
              </div>
            ) : statusData ? (
              <div className="space-y-4">
                {Object.entries(statusData.services).map(([serviceName, service]) => (
                  <div key={serviceName} className="flex items-center justify-between p-4 border border-gray-100 rounded-lg hover:bg-gray-50 transition-colors">
                    <span className="font-medium text-gray-900">{serviceName}</span>
                    <div className="flex items-center space-x-2">
                      {getStatusIcon(service.status)}
                      <span className={`font-medium ${getStatusColor(service.status)}`}>
                        {service.message}
                      </span>
                    </div>
                  </div>
                ))}
              </div>
            ) : (
              <div className="text-center py-12">
                <XCircleIcon className="h-12 w-12 text-red-500 mx-auto mb-4" />
                <p className="text-red-600 mb-2">Failed to load status</p>
                <p className="text-gray-500 text-sm">{error}</p>
              </div>
            )}

            <div className="flex items-center justify-between mt-6 pt-6 border-t border-gray-100">
              <p className="text-sm text-gray-500">
                Last updated: {lastUpdated.toLocaleString()}
              </p>
              <p className="text-xs text-gray-400">
                Auto-refreshes every 30 seconds
              </p>
            </div>
          </div>
        </motion.div>
      </section>
    </div>
  );
};

export default StatusPage;