import React, { useState } from 'react'
import { motion } from 'framer-motion'
import {
  CheckCircleIcon,
  ExclamationTriangleIcon,
  SparklesIcon,
  UserIcon,
  BriefcaseIcon,
  MapPinIcon,
  PhotoIcon,
  PencilIcon,
  EyeIcon
} from '@heroicons/react/24/outline'
import { useStylistRegistration } from '../../../hooks/useStylistRegistration'
import { useNavigate } from 'react-router-dom'
import { useTranslation } from 'react-i18next'
import toast from 'react-hot-toast'

export default function StylistStep5Review() {
  const navigate = useNavigate()
  const { t } = useTranslation()
  const {
    formData,
    updateField,
    submitRegistration,
    isLoading,
    goToStep
  } = useStylistRegistration()

  const [agreeToTerms, setAgreeToTerms] = useState(false)
  const [isSubmitting, setIsSubmitting] = useState(false)

  const handleSubmit = async () => {
    if (!agreeToTerms) {
      toast.error(t('auth.review.termsError'))
      return
    }

    setIsSubmitting(true)

    try {
      const result = await submitRegistration()

      if (result.success) {
        toast.success(t('auth.review.successMessage'))
        navigate('/login', {
          state: {
            message: t('auth.review.successMessage'),
            email: formData.email
          }
        })
      } else {
        toast.error(result.error || t('auth.review.errorMessage'))
      }
    } catch (error: any) {
      toast.error(error.message || t('auth.review.errorMessage'))
    } finally {
      setIsSubmitting(false)
    }
  }

  const handleEdit = (step: number) => {
    goToStep(step)
  }

  const getSummaryData = () => {
    return {
      personal: {
        name: `${formData.firstName} ${formData.lastName}`,
        email: formData.email,
        phone: formData.phone,
      },
      professional: {
        businessName: formData.businessName,
        experience: formData.experience,
        specialties: formData.specialties || [],
        bio: formData.bio,
      },
      location: {
        address: formData.locationAddress,
        city: formData.locationCity,
        state: formData.locationState,
      },
      portfolio: {
        instagramUrl: formData.instagramUrl,
        portfolioImages: formData.portfolioImages || [],
      }
    }
  }

  const summary = getSummaryData()
  const canSubmit = agreeToTerms && !isSubmitting && !isLoading

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
      className="space-y-6"
    >
      {/* Introduction */}
      <div className="text-center mb-8">
        <div className="flex justify-center mb-4">
          <SparklesIcon className="h-12 w-12 text-primary-500" />
        </div>
        <h3 className="text-2xl font-semibold text-gray-900 mb-2">
          {t('auth.review.title')}
        </h3>
        <p className="text-gray-600">
          {t('auth.review.subtitle')}
        </p>
      </div>

      {/* Review Sections */}
      <div className="space-y-6">

        {/* Personal Information */}
        <motion.div
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.1 }}
          className="bg-white border border-gray-200 rounded-lg p-6"
        >
          <div className="flex items-center justify-between mb-4">
            <div className="flex items-center space-x-3">
              <div className="p-2 bg-primary-100 rounded-lg">
                <UserIcon className="h-5 w-5 text-primary-600" />
              </div>
              <div>
                <h4 className="font-semibold text-gray-900">{t('auth.review.personalInfo')}</h4>
                <p className="text-sm text-gray-600">{t('auth.review.step')} 1</p>
              </div>
            </div>
            <button
              onClick={() => handleEdit(1)}
              className="flex items-center space-x-1 text-primary-600 hover:text-primary-700 text-sm"
            >
              <PencilIcon className="h-4 w-4" />
              <span>{t('auth.review.edit')}</span>
            </button>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm">
            <div>
              <span className="font-medium text-gray-700">{t('auth.review.name')}:</span>
              <span className="ml-2 text-gray-900">{summary.personal.name}</span>
            </div>
            <div>
              <span className="font-medium text-gray-700">{t('auth.review.email')}:</span>
              <span className="ml-2 text-gray-900">{summary.personal.email}</span>
            </div>
            <div className="md:col-span-2">
              <span className="font-medium text-gray-700">{t('auth.review.phone')}:</span>
              <span className="ml-2 text-gray-900">{summary.personal.phone}</span>
            </div>
          </div>
        </motion.div>

        {/* Professional Information */}
        <motion.div
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.2 }}
          className="bg-white border border-gray-200 rounded-lg p-6"
        >
          <div className="flex items-center justify-between mb-4">
            <div className="flex items-center space-x-3">
              <div className="p-2 bg-secondary-100 rounded-lg">
                <BriefcaseIcon className="h-5 w-5 text-secondary-600" />
              </div>
              <div>
                <h4 className="font-semibold text-gray-900">{t('auth.review.professionalInfo')}</h4>
                <p className="text-sm text-gray-600">{t('auth.review.step')} 2</p>
              </div>
            </div>
            <button
              onClick={() => handleEdit(2)}
              className="flex items-center space-x-1 text-primary-600 hover:text-primary-700 text-sm"
            >
              <PencilIcon className="h-4 w-4" />
              <span>{t('auth.review.edit')}</span>
            </button>
          </div>

          <div className="space-y-3 text-sm">
            <div>
              <span className="font-medium text-gray-700">{t('auth.review.businessName')}:</span>
              <span className="ml-2 text-gray-900">{summary.professional.businessName}</span>
            </div>
            <div>
              <span className="font-medium text-gray-700">{t('auth.review.experience')}:</span>
              <span className="ml-2 text-gray-900">{summary.professional.experience} {summary.professional.experience !== '1' ? t('auth.review.years') : t('auth.review.year')}</span>
            </div>
            <div>
              <span className="font-medium text-gray-700">{t('auth.review.specialties')}:</span>
              <div className="mt-2 flex flex-wrap gap-2">
                {summary.professional.specialties.map((specialty, index) => (
                  <span
                    key={index}
                    className="inline-flex items-center px-3 py-1 rounded-full text-xs font-medium bg-primary-100 text-primary-700"
                  >
                    {specialty}
                  </span>
                ))}
              </div>
            </div>
            {summary.professional.bio && (
              <div>
                <span className="font-medium text-gray-700">{t('auth.review.bio')}:</span>
                <p className="mt-1 text-gray-900 bg-gray-50 p-3 rounded-lg">
                  {summary.professional.bio}
                </p>
              </div>
            )}
          </div>
        </motion.div>

        {/* Location Information */}
        <motion.div
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.3 }}
          className="bg-white border border-gray-200 rounded-lg p-6"
        >
          <div className="flex items-center justify-between mb-4">
            <div className="flex items-center space-x-3">
              <div className="p-2 bg-green-100 rounded-lg">
                <MapPinIcon className="h-5 w-5 text-green-600" />
              </div>
              <div>
                <h4 className="font-semibold text-gray-900">{t('auth.review.location')}</h4>
                <p className="text-sm text-gray-600">{t('auth.review.step')} 3</p>
              </div>
            </div>
            <button
              onClick={() => handleEdit(3)}
              className="flex items-center space-x-1 text-primary-600 hover:text-primary-700 text-sm"
            >
              <PencilIcon className="h-4 w-4" />
              <span>{t('auth.review.edit')}</span>
            </button>
          </div>

          <div className="text-sm">
            <span className="font-medium text-gray-700">{t('auth.review.address')}:</span>
            <span className="ml-2 text-gray-900">{summary.location.address}</span>
            {summary.location.city && summary.location.state && (
              <>
                <br />
                <span className="font-medium text-gray-700">{t('auth.review.city')}/{t('auth.review.state')}:</span>
                <span className="ml-2 text-gray-900">{summary.location.city}, {summary.location.state}</span>
              </>
            )}
          </div>
        </motion.div>

        {/* Portfolio Information */}
        <motion.div
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.4 }}
          className="bg-white border border-gray-200 rounded-lg p-6"
        >
          <div className="flex items-center justify-between mb-4">
            <div className="flex items-center space-x-3">
              <div className="p-2 bg-purple-100 rounded-lg">
                <PhotoIcon className="h-5 w-5 text-purple-600" />
              </div>
              <div>
                <h4 className="font-semibold text-gray-900">{t('auth.review.portfolioImages')}</h4>
                <p className="text-sm text-gray-600">{t('auth.review.step')} 4 ({t('common.optional')})</p>
              </div>
            </div>
            <button
              onClick={() => handleEdit(4)}
              className="flex items-center space-x-1 text-primary-600 hover:text-primary-700 text-sm"
            >
              <PencilIcon className="h-4 w-4" />
              <span>{t('auth.review.edit')}</span>
            </button>
          </div>

          <div className="space-y-3 text-sm">
            {summary.portfolio.instagramUrl ? (
              <div>
                <span className="font-medium text-gray-700">{t('auth.review.instagramProfile')}:</span>
                <a
                  href={summary.portfolio.instagramUrl}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="ml-2 text-primary-600 hover:text-primary-700 underline"
                >
                  {summary.portfolio.instagramUrl}
                </a>
              </div>
            ) : (
              <p className="text-gray-500">{t('auth.review.instagramProfile')}: -</p>
            )}

            <div>
              <span className="font-medium text-gray-700">{t('auth.review.portfolioImagesLabel')}:</span>
              <span className="ml-2 text-gray-900">
                {summary.portfolio.portfolioImages.length} {summary.portfolio.portfolioImages.length !== 1 ? t('auth.review.images') : t('auth.review.image')}
              </span>
            </div>

            {summary.portfolio.portfolioImages.length === 0 && (
              <div className="bg-blue-50 border border-blue-200 rounded-lg p-3">
                <p className="text-blue-700 text-sm">
                  {t('auth.review.noPortfolio')}
                </p>
              </div>
            )}
          </div>
        </motion.div>

      </div>

      {/* Terms Agreement */}
      <motion.div
        initial={{ opacity: 0, y: 10 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.5 }}
        className="bg-gray-50 border border-gray-200 rounded-lg p-6"
      >
        <div className="flex items-start space-x-3">
          <input
            type="checkbox"
            id="finalTermsAgreement"
            checked={agreeToTerms}
            onChange={(e) => setAgreeToTerms(e.target.checked)}
            className="mt-1 h-4 w-4 text-primary-600 focus:ring-primary-500 border-gray-300 rounded"
          />
          <div className="text-sm">
            <label htmlFor="finalTermsAgreement" className="text-gray-700">
              {t('auth.review.termsCheckbox')}
            </label>
          </div>
        </div>
      </motion.div>

      {/* Submit Button */}
      <motion.div
        initial={{ opacity: 0, y: 10 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.6 }}
        className="flex justify-center pt-6 border-t border-gray-200"
      >
        <motion.button
          onClick={handleSubmit}
          disabled={!canSubmit}
          className={`flex items-center space-x-3 px-8 py-4 rounded-lg font-semibold text-lg transition-all duration-200 ${
            canSubmit
              ? 'bg-gradient-to-r from-primary-500 to-secondary-500 text-white hover:shadow-xl transform hover:scale-105'
              : 'bg-gray-300 text-gray-500 cursor-not-allowed'
          }`}
          whileHover={canSubmit ? { scale: 1.05 } : {}}
          whileTap={canSubmit ? { scale: 0.95 } : {}}
        >
          {isSubmitting ? (
            <>
              <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white"></div>
              <span>{t('auth.review.submitting')}</span>
            </>
          ) : (
            <>
              <CheckCircleIcon className="h-6 w-6" />
              <span>{t('auth.review.submitButton')}</span>
              <SparklesIcon className="h-6 w-6" />
            </>
          )}
        </motion.button>
      </motion.div>

      {/* Validation Error */}
      {!agreeToTerms && (
        <motion.div
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          className="p-4 bg-yellow-50 border border-yellow-200 rounded-lg"
        >
          <div className="flex items-center space-x-2">
            <ExclamationTriangleIcon className="h-5 w-5 text-yellow-600" />
            <h4 className="text-sm font-medium text-yellow-800">
              {t('auth.review.termsError')}
            </h4>
          </div>
        </motion.div>
      )}
    </motion.div>
  )
}