import React, { useState } from 'react'
import { motion } from 'framer-motion'
import { useTranslation } from 'react-i18next'
import {
  CheckIcon,
  ExclamationTriangleIcon,
  ArrowRightIcon,
  UserIcon,
  EyeIcon,
  EyeSlashIcon,
  EnvelopeIcon,
  PhoneIcon
} from '@heroicons/react/24/outline'
import { useStylistRegistration } from '../../../hooks/useStylistRegistration'

export default function StylistStep1Personal() {
  const { t } = useTranslation()
  const {
    formData,
    fieldErrors,
    updateField,
    completeCurrentStepAndNext,
    validateStep
  } = useStylistRegistration()

  const [touchedFields, setTouchedFields] = useState<Record<string, boolean>>({})
  const [showPassword, setShowPassword] = useState(false)

  // Real-time validation for current step
  const currentStepValidation = validateStep(1)
  const canProceed = currentStepValidation.isValid

  const handleFieldChange = (field: string, value: any) => {
    updateField(field as any, value)
  }

  const handleFieldBlur = (field: string) => {
    setTouchedFields(prev => ({ ...prev, [field]: true }))
  }

  const handleNext = () => {
    // Mark all fields as touched to show validation errors
    setTouchedFields({
      firstName: true,
      lastName: true,
      email: true,
      phone: true,
      password: true,
    })

    if (canProceed) {
      completeCurrentStepAndNext()
    }
  }

  const getFieldError = (field: string): string => {
    return fieldErrors[field] || ''
  }

  const getFieldStatus = (field: string, value: any): 'error' | 'success' | 'default' => {
    if (!touchedFields[field]) return 'default'
    if (getFieldError(field)) return 'error'
    if (value && (typeof value === 'string' ? value.length > 0 : true)) return 'success'
    return 'default'
  }

  const getFieldClassName = (field: string, value: any) => {
    const status = getFieldStatus(field, value)
    const baseClass = 'w-full p-3 pl-10 border rounded-lg focus:ring-2 transition-all duration-200'

    switch (status) {
      case 'error':
        return `${baseClass} border-red-300 bg-red-50 focus:ring-red-500 focus:border-red-500`
      case 'success':
        return `${baseClass} border-green-300 bg-green-50 focus:ring-green-500 focus:border-green-500`
      default:
        return `${baseClass} border-gray-300 focus:ring-primary-500 focus:border-primary-500`
    }
  }

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
      className="space-y-6"
    >
      {/* Introduction */}
      <div className="text-center mb-8">
        <div className="flex justify-center mb-4">
          <UserIcon className="h-12 w-12 text-primary-500" />
        </div>
        <h3 className="text-2xl font-semibold text-gray-900 mb-2">
          {t('auth.stylistSteps.step1.heading')}
        </h3>
        <p className="text-gray-600">
          {t('auth.stylistSteps.step1.subheading')}
        </p>
      </div>

      {/* Form Fields */}
      <div className="space-y-6">
        {/* Name Fields */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          {/* First Name */}
          <div className="space-y-2">
            <label className="block text-sm font-medium text-gray-700">
              {t('auth.fields.firstName')} <span className="text-red-500">*</span>
            </label>
            <div className="relative">
              <UserIcon className="absolute left-3 top-3 h-5 w-5 text-gray-400" />
              <input
                type="text"
                value={formData.firstName}
                onChange={(e) => handleFieldChange('firstName', e.target.value)}
                onBlur={() => handleFieldBlur('firstName')}
                className={getFieldClassName('firstName', formData.firstName)}
                placeholder={t('auth.placeholders.firstName')}
                autoComplete="given-name"
              />
              {getFieldStatus('firstName', formData.firstName) === 'success' && (
                <CheckIcon className="absolute right-3 top-3 h-5 w-5 text-green-500" />
              )}
            </div>
            {touchedFields.firstName && getFieldError('firstName') && (
              <motion.p
                initial={{ opacity: 0, y: -5 }}
                animate={{ opacity: 1, y: 0 }}
                className="text-red-600 text-sm flex items-center space-x-1"
              >
                <ExclamationTriangleIcon className="h-4 w-4" />
                <span>{getFieldError('firstName')}</span>
              </motion.p>
            )}
          </div>

          {/* Last Name */}
          <div className="space-y-2">
            <label className="block text-sm font-medium text-gray-700">
              {t('auth.fields.lastName')} <span className="text-red-500">*</span>
            </label>
            <div className="relative">
              <UserIcon className="absolute left-3 top-3 h-5 w-5 text-gray-400" />
              <input
                type="text"
                value={formData.lastName}
                onChange={(e) => handleFieldChange('lastName', e.target.value)}
                onBlur={() => handleFieldBlur('lastName')}
                className={getFieldClassName('lastName', formData.lastName)}
                placeholder={t('auth.placeholders.lastName')}
                autoComplete="family-name"
              />
              {getFieldStatus('lastName', formData.lastName) === 'success' && (
                <CheckIcon className="absolute right-3 top-3 h-5 w-5 text-green-500" />
              )}
            </div>
            {touchedFields.lastName && getFieldError('lastName') && (
              <motion.p
                initial={{ opacity: 0, y: -5 }}
                animate={{ opacity: 1, y: 0 }}
                className="text-red-600 text-sm flex items-center space-x-1"
              >
                <ExclamationTriangleIcon className="h-4 w-4" />
                <span>{getFieldError('lastName')}</span>
              </motion.p>
            )}
          </div>
        </div>

        {/* Email */}
        <div className="space-y-2">
          <label className="block text-sm font-medium text-gray-700">
            {t('auth.fields.email')} <span className="text-red-500">*</span>
          </label>
          <div className="relative">
            <EnvelopeIcon className="absolute left-3 top-3 h-5 w-5 text-gray-400" />
            <input
              type="email"
              value={formData.email}
              onChange={(e) => handleFieldChange('email', e.target.value)}
              onBlur={() => handleFieldBlur('email')}
              className={getFieldClassName('email', formData.email)}
              placeholder={t('auth.placeholders.email')}
              autoComplete="email"
            />
            {getFieldStatus('email', formData.email) === 'success' && (
              <CheckIcon className="absolute right-3 top-3 h-5 w-5 text-green-500" />
            )}
          </div>
          {touchedFields.email && getFieldError('email') && (
            <motion.p
              initial={{ opacity: 0, y: -5 }}
              animate={{ opacity: 1, y: 0 }}
              className="text-red-600 text-sm flex items-center space-x-1"
            >
              <ExclamationTriangleIcon className="h-4 w-4" />
              <span>{getFieldError('email')}</span>
            </motion.p>
          )}
        </div>

        {/* Phone */}
        <div className="space-y-2">
          <label className="block text-sm font-medium text-gray-700">
            {t('auth.fields.phone')} <span className="text-red-500">*</span>
          </label>
          <div className="relative">
            <PhoneIcon className="absolute left-3 top-3 h-5 w-5 text-gray-400" />
            <input
              type="tel"
              value={formData.phone}
              onChange={(e) => handleFieldChange('phone', e.target.value)}
              onBlur={() => handleFieldBlur('phone')}
              className={getFieldClassName('phone', formData.phone)}
              placeholder={t('auth.placeholders.phone')}
              autoComplete="tel"
            />
            {getFieldStatus('phone', formData.phone) === 'success' && (
              <CheckIcon className="absolute right-3 top-3 h-5 w-5 text-green-500" />
            )}
          </div>
          {touchedFields.phone && getFieldError('phone') && (
            <motion.p
              initial={{ opacity: 0, y: -5 }}
              animate={{ opacity: 1, y: 0 }}
              className="text-red-600 text-sm flex items-center space-x-1"
            >
              <ExclamationTriangleIcon className="h-4 w-4" />
              <span>{getFieldError('phone')}</span>
            </motion.p>
          )}
          <p className="text-xs text-gray-500">
            {t('auth.stylistSteps.step1.phoneHint')}
          </p>
        </div>

        {/* Password */}
        <div className="space-y-2">
          <label className="block text-sm font-medium text-gray-700">
            {t('auth.fields.password')} <span className="text-red-500">*</span>
          </label>
          <div className="relative">
            <input
              type={showPassword ? 'text' : 'password'}
              value={formData.password}
              onChange={(e) => handleFieldChange('password', e.target.value)}
              onBlur={() => handleFieldBlur('password')}
              className={getFieldClassName('password', formData.password)}
              placeholder={t('auth.placeholders.password')}
              autoComplete="new-password"
            />
            <button
              type="button"
              onClick={() => setShowPassword(!showPassword)}
              className="absolute right-3 top-3 text-gray-400 hover:text-gray-600"
            >
              {showPassword ? (
                <EyeSlashIcon className="h-5 w-5" />
              ) : (
                <EyeIcon className="h-5 w-5" />
              )}
            </button>
          </div>
          {touchedFields.password && getFieldError('password') && (
            <motion.p
              initial={{ opacity: 0, y: -5 }}
              animate={{ opacity: 1, y: 0 }}
              className="text-red-600 text-sm flex items-center space-x-1"
            >
              <ExclamationTriangleIcon className="h-4 w-4" />
              <span>{getFieldError('password')}</span>
            </motion.p>
          )}
          <p className="text-xs text-gray-500">
            {t('auth.stylistSteps.step1.passwordHint')}
          </p>
        </div>
      </div>

      {/* Privacy Notice */}
      <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
        <p className="text-sm text-blue-700">
           {t('auth.stylistSteps.step1.privacyNotice')}
        </p>
      </div>

      {/* Next Button */}
      <div className="flex justify-end pt-6 border-t border-gray-200">
        <motion.button
          onClick={handleNext}
          disabled={!canProceed}
          className={`flex items-center space-x-2 px-6 py-3 rounded-lg font-medium transition-all duration-200 ${
            canProceed
              ? 'bg-gradient-to-r from-primary-500 to-secondary-500 text-white hover:shadow-lg transform hover:scale-105'
              : 'bg-gray-300 text-gray-500 cursor-not-allowed'
          }`}
          whileHover={canProceed ? { scale: 1.05 } : {}}
          whileTap={canProceed ? { scale: 0.95 } : {}}
        >
          <span>{t('auth.stylistSteps.step1.nextButton')}</span>
          <ArrowRightIcon className="h-4 w-4" />
        </motion.button>
      </div>

      {/* Validation Summary */}
      {!canProceed && Object.keys(fieldErrors).length > 0 && (
        <motion.div
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          className="mt-4 p-4 bg-yellow-50 border border-yellow-200 rounded-lg"
        >
          <div className="flex items-center space-x-2 mb-2">
            <ExclamationTriangleIcon className="h-5 w-5 text-yellow-600" />
            <h4 className="text-sm font-medium text-yellow-800">
              {t('auth.stylistSteps.step1.validationTitle')}
            </h4>
          </div>
          <ul className="text-sm text-yellow-700 space-y-1">
            {Object.entries(fieldErrors).map(([field, error]) => (
              <li key={field}> {error}</li>
            ))}
          </ul>
        </motion.div>
      )}

      {/* Success Message */}
      {canProceed && (
        <motion.div
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          className="mt-4 p-4 bg-green-50 border border-green-200 rounded-lg"
        >
          <div className="flex items-center space-x-2">
            <CheckIcon className="h-5 w-5 text-green-600" />
            <h4 className="text-sm font-medium text-green-800">
              {t('auth.stylistSteps.step1.successMessage')}
            </h4>
          </div>
        </motion.div>
      )}
    </motion.div>
  )
}
