import { useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import { motion } from 'framer-motion'
import { useForm } from 'react-hook-form'
import { useTranslation } from 'react-i18next'
import {
  SparklesIcon,
  ArrowRightIcon,
  CheckCircleIcon,
  PhotoIcon,
  XMarkIcon,
  EyeIcon,
  EyeSlashIcon,
  ScissorsIcon,
  StarIcon,
  MapPinIcon
} from '@heroicons/react/24/outline'
import { authService } from '../../services/authService'
import GoogleSignInButton from '../../components/auth/GoogleSignInButton'
import type { StylistRegisterForm } from '../../types'
import toast from 'react-hot-toast'

export default function StylistRegisterPage() {
  const navigate = useNavigate()
  const { t, i18n } = useTranslation()

  const [isLoading, setIsLoading] = useState(false)
  const [showPassword, setShowPassword] = useState(false)
  const [portfolioFiles, setPortfolioFiles] = useState<File[]>([])
  const [portfolioPreviews, setPortfolioPreviews] = useState<string[]>([])

  const specialtyOptions = t('auth.specialties', { returnObjects: true }) as string[]

  const {
    register,
    handleSubmit,
    watch,
    setValue,
    getValues,
    formState: { errors },
  } = useForm<StylistRegisterForm>({
    defaultValues: {
      firstName: '',
      lastName: '',
      email: '',
      phone: '',
      password: '',
      businessName: '',
      experience: 0,
      specialties: [],
      bio: '',
      locationAddress: '',
      locationCity: '',
      locationState: '',
      latitude: 0,
      longitude: 0,
      instagramUrl: '',
      portfolioImages: [],
      acceptTerms: false
    }
  })

  const watchedFields = watch()

  const handleFileUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
    const files = Array.from(event.target.files || [])
    if (files.length + portfolioFiles.length > 6) {
      toast.error(t('auth.portfolio.maxImages'))
      return
    }

    setPortfolioFiles(prev => [...prev, ...files])

    // Create previews
    files.forEach(file => {
      const reader = new FileReader()
      reader.onload = (e) => {
        if (e.target?.result) {
          setPortfolioPreviews(prev => [...prev, e.target!.result as string])
        }
      }
      reader.readAsDataURL(file)
    })
  }

  const removePortfolioImage = (index: number) => {
    setPortfolioFiles(prev => prev.filter((_, i) => i !== index))
    setPortfolioPreviews(prev => prev.filter((_, i) => i !== index))
  }

  const handleSpecialtyToggle = (specialty: string) => {
    const currentSpecialties = getValues('specialties') || []
    const newSpecialties = currentSpecialties.includes(specialty)
      ? currentSpecialties.filter(s => s !== specialty)
      : [...currentSpecialties, specialty]

    setValue('specialties', newSpecialties)
  }

  const onSubmit = async (data: StylistRegisterForm) => {
    setIsLoading(true)

    try {
      // Create FormData for file upload
      const formData = new FormData()

      // Add text fields
      Object.entries(data).forEach(([key, value]) => {
        if (key !== 'portfolioImages' && value !== undefined && value !== '') {
          if (Array.isArray(value)) {
            formData.append(key, JSON.stringify(value))
          } else {
            formData.append(key, value.toString())
          }
        }
      })

      // Set placeholder values for location if not provided
      if (!data.locationCity || data.locationCity === '') {
        formData.append('locationAddress', 'To be completed')
        formData.append('locationCity', 'TBD')
        formData.append('locationState', 'TBD')
        formData.append('latitude', '0')
        formData.append('longitude', '0')
      } else {
        // Use city as address if only city is provided
        formData.set('locationAddress', data.locationCity)
        formData.set('locationState', 'TBD')
        formData.set('latitude', '0')
        formData.set('longitude', '0')
      }

      // Add portfolio images
      portfolioFiles.forEach((file, index) => {
        formData.append(`portfolioImages`, file)
      })

      const response = await authService.registerStylist(formData)

      if (response.success) {
        toast.success(t('auth.messages.stylistRegisterSuccess'))
        navigate('/login', {
          state: { message: t('auth.messages.stylistRegisterSuccess') }
        })
      } else {
        throw new Error(response.error || t('auth.messages.registerError'))
      }
    } catch (error: any) {
      toast.error(error.message || t('auth.messages.registerError'))
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-primary-50 to-secondary-50 flex items-center justify-center py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-2xl w-full space-y-8">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
          className="text-center"
        >
          {/* Logo */}
          <Link to="/" className="inline-flex items-center space-x-2 mb-8">
            <SparklesIcon className="h-10 w-10 text-primary-600" />
            <span className="text-2xl font-serif font-bold gradient-text">
              BeautyCita
            </span>
          </Link>

          {/* Title */}
          <h2 className="text-3xl font-serif font-bold text-gray-900 mb-2">
            {t('auth.stylistRegisterTitle')}
          </h2>
          <p className="text-gray-600">
            {t('auth.stylistRegisterSubtitle')}
          </p>
        </motion.div>

        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, delay: 0.1 }}
          className="card mobile-overflow-visible"
        >
          <div className="card-body">
            {/* Google Sign In */}
            <div className="space-y-4 mb-6">
              <GoogleSignInButton role="stylist" text={t('auth.social.signUpWithGoogle')} />

              {/* Divider */}
              <div className="relative">
                <div className="absolute inset-0 flex items-center">
                  <div className="w-full border-t border-gray-300"></div>
                </div>
                <div className="relative flex justify-center text-sm">
                  <span className="px-2 bg-white text-gray-500">{t('auth.social.orDivider')}</span>
                </div>
              </div>
            </div>

            <form onSubmit={handleSubmit(onSubmit)} className="space-y-6" style={{ overflow: 'visible' }}>
              {/* Personal Information Section */}
              <div className="border-b border-gray-200 pb-6">
                <h3 className="text-lg font-semibold text-gray-900 mb-4 flex items-center">
                  <StarIcon className="h-5 w-5 text-primary-600 mr-2" />
                  {t('auth.sections.personalInfo')}
                </h3>

                {/* Name fields */}
                <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 mb-6">
                  <div className="form-group">
                    <label htmlFor="firstName" className="label">
                      {t('auth.fields.firstName')} *
                    </label>
                    <input
                      {...register('firstName', {
                        required: t('auth.validation.firstNameRequired'),
                        minLength: {
                          value: 2,
                          message: t('auth.validation.firstNameMinLength')
                        }
                      })}
                      type="text"
                      id="firstName"
                      className={`input ${errors.firstName ? 'input-error' : ''}`}
                      placeholder={t('auth.placeholders.firstName')}
                      autoComplete="given-name"
                    />
                    {errors.firstName && (
                      <p className="form-error">{errors.firstName.message}</p>
                    )}
                  </div>

                  <div className="form-group">
                    <label htmlFor="lastName" className="label">
                      {t('auth.fields.lastName')} *
                    </label>
                    <input
                      {...register('lastName', {
                        required: t('auth.validation.lastNameRequired'),
                        minLength: {
                          value: 2,
                          message: t('auth.validation.lastNameMinLength')
                        }
                      })}
                      type="text"
                      id="lastName"
                      className={`input ${errors.lastName ? 'input-error' : ''}`}
                      placeholder={t('auth.placeholders.lastName')}
                      autoComplete="family-name"
                    />
                    {errors.lastName && (
                      <p className="form-error">{errors.lastName.message}</p>
                    )}
                  </div>
                </div>

                {/* Email */}
                <div className="form-group mb-6">
                  <label htmlFor="email" className="label">
                    {t('auth.fields.email')} *
                  </label>
                  <input
                    {...register('email', {
                      required: t('auth.validation.emailRequired'),
                      pattern: {
                        value: /^\S+@\S+$/i,
                        message: t('auth.validation.emailInvalid')
                      }
                    })}
                    type="email"
                    id="email"
                    className={`input ${errors.email ? 'input-error' : ''}`}
                    placeholder={t('auth.placeholders.email')}
                    autoComplete="email"
                  />
                  {errors.email && (
                    <p className="form-error">{errors.email.message}</p>
                  )}
                </div>

                {/* Phone */}
                <div className="form-group mb-6">
                  <label htmlFor="phone" className="label">
                    {t('auth.fields.phone')} *
                  </label>
                  <input
                    {...register('phone', {
                      required: t('auth.validation.phoneRequired'),
                      pattern: {
                        value: /^[\d\s\-\+\(\)]+$/,
                        message: t('auth.validation.phoneInvalid')
                      }
                    })}
                    type="tel"
                    id="phone"
                    className={`input ${errors.phone ? 'input-error' : ''}`}
                    placeholder={t('auth.placeholders.phone')}
                    autoComplete="tel"
                  />
                  {errors.phone && (
                    <p className="form-error">{errors.phone.message}</p>
                  )}
                </div>

                {/* Password */}
                <div className="form-group">
                  <label htmlFor="password" className="label">
                    {t('auth.fields.password')} *
                  </label>
                  <div className="relative">
                    <input
                      {...register('password', {
                        required: t('auth.validation.passwordRequired'),
                        minLength: {
                          value: 8,
                          message: t('auth.validation.passwordMinLength')
                        },
                        pattern: {
                          value: /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)/,
                          message: t('auth.validation.passwordStrong')
                        }
                      })}
                      type={showPassword ? 'text' : 'password'}
                      id="password"
                      className={`input pr-10 ${errors.password ? 'input-error' : ''}`}
                      placeholder={t('auth.placeholders.password')}
                      autoComplete="new-password"
                    />
                    <button
                      type="button"
                      onClick={() => setShowPassword(!showPassword)}
                      className="absolute inset-y-0 right-0 pr-3 flex items-center text-gray-400 hover:text-gray-600"
                    >
                      {showPassword ? (
                        <EyeSlashIcon className="h-5 w-5" />
                      ) : (
                        <EyeIcon className="h-5 w-5" />
                      )}
                    </button>
                  </div>
                  {errors.password && (
                    <p className="form-error">{errors.password.message}</p>
                  )}
                </div>
              </div>

              {/* Professional Information Section */}
              <div className="border-b border-gray-200 pb-6">
                <h3 className="text-lg font-semibold text-gray-900 mb-4 flex items-center">
                  <ScissorsIcon className="h-5 w-5 text-primary-600 mr-2" />
                  {t('auth.sections.professionalInfo')}
                </h3>

                {/* Business Name */}
                <div className="form-group mb-6">
                  <label htmlFor="businessName" className="label">
                    {t('auth.fields.businessName')} *
                  </label>
                  <input
                    {...register('businessName', {
                      required: t('auth.validation.businessNameRequired')
                    })}
                    type="text"
                    id="businessName"
                    className={`input ${errors.businessName ? 'input-error' : ''}`}
                    placeholder={t('auth.placeholders.businessName')}
                  />
                  {errors.businessName && (
                    <p className="form-error">{errors.businessName.message}</p>
                  )}
                </div>

                {/* Experience */}
                <div className="form-group mb-6">
                  <label htmlFor="experience" className="label">
                    {t('auth.fields.experience')} *
                  </label>
                  <select
                    {...register('experience', {
                      required: t('auth.validation.experienceRequired')
                    })}
                    id="experience"
                    className={`input ${errors.experience ? 'input-error' : ''}`}
                  >
                    <option value="">{t('auth.options.selectExp')}</option>
                    <option value="1">{t('auth.options.1year')}</option>
                    <option value="2">{t('auth.options.2years')}</option>
                    <option value="3">{t('auth.options.3years')}</option>
                    <option value="4">{t('auth.options.4years')}</option>
                    <option value="5">{t('auth.options.5years')}</option>
                    <option value="10">{t('auth.options.10years')}</option>
                  </select>
                  {errors.experience && (
                    <p className="form-error">{errors.experience.message}</p>
                  )}
                </div>

                {/* Specialties */}
                <div className="form-group mb-6">
                  <label className="label">{t('auth.fields.specialties')} *</label>
                  <p className="text-sm text-gray-600 mb-3">{t('auth.options.selectSpecialties')}</p>
                  <div className="grid grid-cols-1 sm:grid-cols-2 gap-2">
                    {specialtyOptions.map(specialty => (
                      <label key={specialty} className="flex items-center space-x-2">
                        <input
                          type="checkbox"
                          checked={watchedFields.specialties?.includes(specialty) || false}
                          onChange={() => handleSpecialtyToggle(specialty)}
                          className="rounded border-gray-300 text-primary-600 focus:ring-primary-500"
                        />
                        <span className="text-sm">{specialty}</span>
                      </label>
                    ))}
                  </div>
                  {errors.specialties && (
                    <p className="form-error">{errors.specialties.message}</p>
                  )}
                </div>

                {/* Bio */}
                <div className="form-group">
                  <label htmlFor="bio" className="label">
                    {t('auth.fields.bio')}
                  </label>
                  <textarea
                    {...register('bio')}
                    id="bio"
                    rows={3}
                    className="input"
                    placeholder={t('auth.placeholders.bio')}
                  />
                  <p className="text-xs text-gray-500 mt-1">
                    {t('auth.hints.bioOptional')}
                  </p>
                </div>
              </div>

              {/* Location Section - Optional during registration, required for approval */}
              <div className="border-b border-gray-200 pb-6">
                <h3 className="text-lg font-semibold text-gray-900 mb-4 flex items-center">
                  <MapPinIcon className="h-5 w-5 text-primary-600 mr-2" />
                  {t('auth.sections.location')}
                  <span className="ml-2 text-sm text-gray-500 font-normal">(Can be added later)</span>
                </h3>

                <div className="form-group">
                  <label className="label">{t('auth.fields.workLocation')}</label>
                  <p className="text-sm text-gray-600 mb-3">
                    You can skip this step and add your business location later from your dashboard. This is required before you can receive bookings.
                  </p>

                  {/* Simple text input for city/area instead of full address */}
                  <input
                    type="text"
                    {...register('locationCity')}
                    className="input"
                    placeholder="City or area (e.g., Mexico City, CDMX)"
                  />
                  {errors.locationCity && (
                    <p className="form-error">{errors.locationCity.message}</p>
                  )}
                </div>
              </div>

              {/* Portfolio Section */}
              <div className="border-b border-gray-200 pb-6">
                <h3 className="text-lg font-semibold text-gray-900 mb-4 flex items-center">
                  <PhotoIcon className="h-5 w-5 text-primary-600 mr-2" />
                  {t('auth.sections.portfolio')}
                </h3>

                {/* Instagram URL */}
                <div className="form-group mb-6">
                  <label htmlFor="instagramUrl" className="label">
                    {t('auth.fields.instagramUrl')}
                  </label>
                  <input
                    {...register('instagramUrl')}
                    type="url"
                    id="instagramUrl"
                    className="input"
                    placeholder={t('auth.placeholders.instagramUrl')}
                  />
                  <p className="text-xs text-gray-500 mt-1">
                    {t('auth.hints.instagramOptional')}
                  </p>
                </div>

                {/* Portfolio Upload */}
                <div className="form-group">
                  <label className="label">{t('auth.fields.portfolioImages')}</label>
                  <div className="border-2 border-dashed border-gray-300 rounded-lg p-6">
                    <input
                      type="file"
                      multiple
                      accept="image/*"
                      onChange={handleFileUpload}
                      className="hidden"
                      id="portfolio-upload"
                    />
                    <label
                      htmlFor="portfolio-upload"
                      className="cursor-pointer flex flex-col items-center"
                    >
                      <PhotoIcon className="h-8 w-8 text-gray-400 mb-2" />
                      <span className="text-sm font-medium text-gray-700">
                        {t('auth.portfolio.uploadText')}
                      </span>
                      <span className="text-xs text-gray-500">
                        {t('auth.portfolio.uploadHint')}
                      </span>
                    </label>
                  </div>

                  {portfolioPreviews.length > 0 && (
                    <div className="grid grid-cols-2 md:grid-cols-3 gap-4 mt-4">
                      {portfolioPreviews.map((preview, index) => (
                        <div key={index} className="relative group">
                          <img
                            src={preview}
                            alt={`Portfolio ${index + 1}`}
                            className="w-full h-32 object-cover rounded-lg"
                          />
                          <button
                            type="button"
                            onClick={() => removePortfolioImage(index)}
                            className="absolute -top-2 -right-2 bg-red-500 text-white rounded-full p-1 opacity-0 group-hover:opacity-100 transition-opacity"
                          >
                            <XMarkIcon className="h-4 w-4" />
                          </button>
                        </div>
                      ))}
                    </div>
                  )}
                </div>
              </div>

              {/* Terms acceptance */}
              <div className="flex items-start space-x-3">
                <input
                  {...register('acceptTerms', {
                    required: t('auth.validation.termsRequired')
                  })}
                  id="acceptTerms"
                  type="checkbox"
                  className="h-4 w-4 text-primary-600 focus:ring-primary-500 border-gray-300 rounded mt-1"
                />
                <div className="text-sm">
                  <label htmlFor="acceptTerms" className="text-gray-700">
                    {t('auth.options.acceptStylistTerms')}{' '}
                    <Link to="/terms/stylist" className="text-primary-600 hover:text-primary-700 font-medium">
                      {t('auth.links.stylistTerms')}
                    </Link>{' '}
                    {t('auth.legal.and')}{' '}
                    <Link to="/privacy" className="text-primary-600 hover:text-primary-700 font-medium">
                      {t('auth.links.privacyPolicy')}
                    </Link>
                  </label>
                  {errors.acceptTerms && (
                    <p className="form-error mt-1">{errors.acceptTerms.message}</p>
                  )}
                </div>
              </div>

              {/* Submit button */}
              <button
                type="submit"
                disabled={isLoading}
                className="btn btn-primary btn-lg w-full flex items-center justify-center space-x-2"
              >
                {isLoading ? (
                  <div className="loading-spinner" />
                ) : (
                  <>
                    <span>{t('auth.actions.createStylistAccount')}</span>
                    <ArrowRightIcon className="h-4 w-4" />
                  </>
                )}
              </button>
            </form>

            {/* Divider */}
            <div className="mt-6">
              <div className="relative">
                <div className="absolute inset-0 flex items-center">
                  <div className="w-full border-t border-gray-300" />
                </div>
                <div className="relative flex justify-center text-sm">
                  <span className="px-2 bg-white text-gray-500">{t('common.or')}</span>
                </div>
              </div>
            </div>

            {/* Login link */}
            <div className="mt-6 text-center">
              <p className="text-sm text-gray-600">
                {t('auth.links.hasAccount')}{' '}
                <Link
                  to="/login"
                  className="font-medium text-primary-600 hover:text-primary-700"
                >
                  {t('auth.links.signInHere')}
                </Link>
              </p>
            </div>

            {/* Client register link */}
            <div className="mt-4 text-center">
              <p className="text-xs text-gray-500">
                {t('auth.links.notAProfessional')}{' '}
                <Link
                  to="/register"
                  className="font-medium text-secondary-600 hover:text-secondary-700"
                >
                  {t('auth.links.signUpAsClient')}
                </Link>
              </p>
            </div>
          </div>
        </motion.div>

        {/* Benefits */}
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.6, delay: 0.3 }}
          className="bg-white rounded-lg p-4 border border-gray-100"
        >
          <h3 className="text-sm font-semibold text-gray-900 mb-3">
            {t('auth.stylistBenefits.title')}
          </h3>
          <ul className="space-y-2">
            <li className="flex items-center space-x-2 text-xs text-gray-600">
              <CheckCircleIcon className="h-4 w-4 text-green-500 flex-shrink-0" />
              <span>{t('auth.stylistBenefits.manageBookings')}</span>
            </li>
            <li className="flex items-center space-x-2 text-xs text-gray-600">
              <CheckCircleIcon className="h-4 w-4 text-green-500 flex-shrink-0" />
              <span>{t('auth.stylistBenefits.showcaseWork')}</span>
            </li>
            <li className="flex items-center space-x-2 text-xs text-gray-600">
              <CheckCircleIcon className="h-4 w-4 text-green-500 flex-shrink-0" />
              <span>{t('auth.stylistBenefits.growBusiness')}</span>
            </li>
            <li className="flex items-center space-x-2 text-xs text-gray-600">
              <CheckCircleIcon className="h-4 w-4 text-green-500 flex-shrink-0" />
              <span>{t('auth.stylistBenefits.securePayments')}</span>
            </li>
          </ul>
        </motion.div>
      </div>
    </div>
  )
}