            <p className="text-xl md:text-2xl mb-8 max-w-2xl mx-auto">
              Watch your transformation come to life with BeautyCita's verified professionals
            </p>
            <AnimatedButton
              to="/portfolio"
              variant="ghost"
              size="lg"
              className="inline-flex items-center"
