import { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { motion } from 'framer-motion'
import {
  UserGroupIcon,
  ChartBarIcon,
  CurrencyDollarIcon,
  CalendarDaysIcon,
  StarIcon,
  ShieldCheckIcon,
  CogIcon,
  ExclamationTriangleIcon,
  CheckCircleIcon,
  ClockIcon,
  HeartIcon,
  MagnifyingGlassIcon,
  PlusIcon
} from '@heroicons/react/24/outline'
import { useAuthStore } from '../../store/authStore'

interface DashboardStats {
  totalBookings?: number
  upcomingBookings?: number
  totalRevenue?: number
  averageRating?: number
  totalUsers?: number
  totalStylists?: number
  totalClients?: number
  pendingPayouts?: number
  favoriteStylists?: number
  totalSpent?: number
  completedBookings?: number
}

export default function UnifiedDashboard() {
  const { user, stylist, client } = useAuthStore()
  const [stats, setStats] = useState<DashboardStats>({})
  const [loading, setLoading] = useState(true)

  const userRole = user?.role?.toUpperCase() || 'CLIENT'
  const isAdmin = ['ADMIN', 'SUPERADMIN'].includes(userRole)
  const isSuperAdmin = userRole === 'SUPERADMIN'
  const isStylist = userRole === 'STYLIST'
  const isClient = userRole === 'CLIENT'

  useEffect(() => {
    const fetchDashboardData = async () => {
      try {
        setLoading(true)
        // TODO: Fetch role-specific stats from API
        setStats({
          totalBookings: 42,
          upcomingBookings: 8,
          totalRevenue: 12500,
          averageRating: 4.8,
          totalUsers: 156,
          totalStylists: 45,
          totalClients: 111,
          pendingPayouts: 3,
          favoriteStylists: 5,
          totalSpent: 850,
          completedBookings: 12
        })
      } catch (error) {
        console.error('Failed to fetch dashboard data:', error)
      } finally {
        setLoading(false)
      }
    }

    fetchDashboardData()
  }, [userRole])

  if (!user) {
    return (
      <div className="min-h-screen bg-gray-50 py-8">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <p className="text-gray-600">Loading user information...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900">
            {isAdmin && ' Admin Dashboard'}
            {isStylist && ' Stylist Dashboard'}
            {isClient && ' My Dashboard'}
          </h1>
          <p className="mt-2 text-gray-600">
            Welcome back, {user.name || user.email}
          </p>
          <div className="mt-1 flex items-center gap-2">
            <span className="px-2 py-1 text-xs font-medium rounded-full bg-purple-100 text-purple-800">
              {userRole}
            </span>
            {user.user_status && (
              <span className={`px-2 py-1 text-xs font-medium rounded-full ${
                user.user_status === 'APPROVED' ? 'bg-green-100 text-green-800' :
                user.user_status === 'SUSPENDED' ? 'bg-yellow-100 text-yellow-800' :
                'bg-red-100 text-red-800'
              }`}>
                {user.user_status}
              </span>
            )}
          </div>
        </div>

        {/* Suspended Warning */}
        {user.user_status === 'SUSPENDED' && isStylist && (
          <motion.div
            initial={{ opacity: 0, y: -10 }}
            animate={{ opacity: 1, y: 0 }}
            className="mb-6 bg-yellow-50 border-l-4 border-yellow-400 p-4"
          >
            <div className="flex">
              <ExclamationTriangleIcon className="h-5 w-5 text-yellow-400" />
              <div className="ml-3">
                <p className="text-sm text-yellow-700">
                  Your account is suspended. You can only access payout requests and bank details.
                </p>
              </div>
            </div>
          </motion.div>
        )}

        {/* Blocked Warning */}
        {user.user_status === 'BLOCKED' && (
          <motion.div
            initial={{ opacity: 0, y: -10 }}
            animate={{ opacity: 1, y: 0 }}
            className="mb-6 bg-red-50 border-l-4 border-red-400 p-4"
          >
            <div className="flex">
              <ExclamationTriangleIcon className="h-5 w-5 text-red-400" />
              <div className="ml-3">
                <p className="text-sm text-red-700">
                  Your account has been blocked. Please contact support for assistance.
                </p>
              </div>
            </div>
          </motion.div>
        )}

        {/* SUPERADMIN / ADMIN Stats */}
        {isAdmin && (
          <>
            <div className="grid grid-cols-1 gap-6 sm:grid-cols-2 lg:grid-cols-4 mb-8">
              <StatsCard
                icon={UserGroupIcon}
                title="Total Users"
                value={stats.totalUsers || 0}
                color="purple"
                link="/admin/users"
              />
              <StatsCard
                icon={UserGroupIcon}
                title="Stylists"
                value={stats.totalStylists || 0}
                color="blue"
                link="/admin/stylists"
              />
              <StatsCard
                icon={UserGroupIcon}
                title="Clients"
                value={stats.totalClients || 0}
                color="green"
                link="/admin/clients"
              />
              <StatsCard
                icon={CurrencyDollarIcon}
                title="Pending Payouts"
                value={stats.pendingPayouts || 0}
                color="yellow"
                link="/admin/payouts"
              />
            </div>

            {/* Admin Quick Actions */}
            <div className="bg-white rounded-lg shadow p-6 mb-8">
              <h2 className="text-lg font-semibold text-gray-900 mb-4">Quick Actions</h2>
              <div className="grid grid-cols-1 gap-4 sm:grid-cols-2 lg:grid-cols-4">
                <QuickActionButton
                  icon={UserGroupIcon}
                  label="Manage Users"
                  href="/admin/users"
                />
                <QuickActionButton
                  icon={CurrencyDollarIcon}
                  label="Payout Requests"
                  href="/admin/payouts"
                />
                <QuickActionButton
                  icon={ChartBarIcon}
                  label="Analytics"
                  href="/admin/analytics"
                />
                {isSuperAdmin && (
                  <QuickActionButton
                    icon={CogIcon}
                    label="System Settings"
                    href="/admin/settings"
                  />
                )}
              </div>
            </div>
          </>
        )}

        {/* STYLIST Stats */}
        {isStylist && user.user_status !== 'SUSPENDED' && (
          <>
            <div className="grid grid-cols-1 gap-6 sm:grid-cols-2 lg:grid-cols-4 mb-8">
              <StatsCard
                icon={CalendarDaysIcon}
                title="Total Bookings"
                value={stats.totalBookings || 0}
                color="blue"
              />
              <StatsCard
                icon={ClockIcon}
                title="Upcoming"
                value={stats.upcomingBookings || 0}
                color="purple"
              />
              <StatsCard
                icon={CurrencyDollarIcon}
                title="Total Revenue"
                value={`$${stats.totalRevenue || 0}`}
                color="green"
              />
              <StatsCard
                icon={StarIcon}
                title="Rating"
                value={stats.averageRating || 0}
                color="yellow"
              />
            </div>

            {/* Stylist Quick Actions */}
            <div className="bg-white rounded-lg shadow p-6 mb-8">
              <h2 className="text-lg font-semibold text-gray-900 mb-4">Quick Actions</h2>
              <div className="grid grid-cols-1 gap-4 sm:grid-cols-2 lg:grid-cols-4">
                <QuickActionButton
                  icon={CalendarDaysIcon}
                  label="View Calendar"
                  href="/dashboard/calendar"
                />
                <QuickActionButton
                  icon={PlusIcon}
                  label="Manage Services"
                  href="/dashboard/services"
                />
                <QuickActionButton
                  icon={ChartBarIcon}
                  label="Revenue"
                  href="/dashboard/revenue"
                />
                <QuickActionButton
                  icon={CurrencyDollarIcon}
                  label="Payout Request"
                  href="/dashboard/payouts"
                />
              </div>
            </div>
          </>
        )}

        {/* STYLIST - Suspended Access (Payout Only) */}
        {isStylist && user.user_status === 'SUSPENDED' && (
          <div className="bg-white rounded-lg shadow p-6 mb-8">
            <h2 className="text-lg font-semibold text-gray-900 mb-4">Available Actions</h2>
            <p className="text-sm text-gray-600 mb-4">
              While your account is suspended, you can only access payout-related features.
            </p>
            <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
              <QuickActionButton
                icon={CurrencyDollarIcon}
                label="Payout Requests"
                href="/dashboard/payouts"
              />
              <QuickActionButton
                icon={CogIcon}
                label="Bank Details"
                href="/dashboard/bank-details"
              />
            </div>
          </div>
        )}

        {/* CLIENT Stats */}
        {isClient && (
          <>
            <div className="grid grid-cols-1 gap-6 sm:grid-cols-2 lg:grid-cols-4 mb-8">
              <StatsCard
                icon={CalendarDaysIcon}
                title="Total Bookings"
                value={stats.totalBookings || 0}
                color="blue"
              />
              <StatsCard
                icon={ClockIcon}
                title="Upcoming"
                value={stats.upcomingBookings || 0}
                color="purple"
              />
              <StatsCard
                icon={CurrencyDollarIcon}
                title="Total Spent"
                value={`$${stats.totalSpent || 0}`}
                color="green"
              />
              <StatsCard
                icon={HeartIcon}
                title="Favorites"
                value={stats.favoriteStylists || 0}
                color="pink"
              />
            </div>

            {/* Client Quick Actions */}
            <div className="bg-white rounded-lg shadow p-6 mb-8">
              <h2 className="text-lg font-semibold text-gray-900 mb-4">Quick Actions</h2>
              <div className="grid grid-cols-1 gap-4 sm:grid-cols-2 lg:grid-cols-4">
                <QuickActionButton
                  icon={MagnifyingGlassIcon}
                  label="Find Stylist"
                  href="/stylists"
                />
                <QuickActionButton
                  icon={CalendarDaysIcon}
                  label="My Bookings"
                  href="/bookings"
                />
                <QuickActionButton
                  icon={HeartIcon}
                  label="Favorites"
                  href="/favorites"
                />
                <QuickActionButton
                  icon={CogIcon}
                  label="Profile"
                  href="/profile"
                />
              </div>
            </div>
          </>
        )}

        {/* Recent Activity Section */}
        <div className="bg-white rounded-lg shadow">
          <div className="px-6 py-4 border-b border-gray-200">
            <h2 className="text-lg font-semibold text-gray-900">Recent Activity</h2>
          </div>
          <div className="p-6">
            <p className="text-sm text-gray-500">Loading recent activity...</p>
          </div>
        </div>
      </div>
    </div>
  )
}

// Stats Card Component
interface StatsCardProps {
  icon: React.ComponentType<React.SVGProps<SVGSVGElement>>
  title: string
  value: string | number
  color: 'blue' | 'purple' | 'green' | 'yellow' | 'pink'
  link?: string
}

function StatsCard({ icon: Icon, title, value, color, link }: StatsCardProps) {
  const colorClasses = {
    blue: 'bg-blue-500',
    purple: 'bg-purple-500',
    green: 'bg-green-500',
    yellow: 'bg-yellow-500',
    pink: 'bg-pink-500'
  }

  const card = (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      className={`bg-white rounded-lg shadow p-6 ${link ? 'hover:shadow-lg transition-shadow cursor-pointer' : ''}`}
    >
      <div className="flex items-center">
        <div className={`flex-shrink-0 rounded-md p-3 ${colorClasses[color]}`}>
          <Icon className="h-6 w-6 text-white" />
        </div>
        <div className="ml-5">
          <p className="text-sm font-medium text-gray-500">{title}</p>
          <p className="text-2xl font-semibold text-gray-900">{value}</p>
        </div>
      </div>
    </motion.div>
  )

  return link ? <Link to={link}>{card}</Link> : card
}

// Quick Action Button Component
interface QuickActionButtonProps {
  icon: React.ComponentType<React.SVGProps<SVGSVGElement>>
  label: string
  href: string
}

function QuickActionButton({ icon: Icon, label, href }: QuickActionButtonProps) {
  return (
    <Link
      to={href}
      className="flex items-center p-4 bg-gray-50 rounded-lg hover:bg-gray-100 transition-colors"
    >
      <Icon className="h-6 w-6 text-purple-600 flex-shrink-0" />
      <span className="ml-3 text-sm font-medium text-gray-900">{label}</span>
    </Link>
  )
}
