import { useState, useEffect } from 'react'
import { motion } from 'framer-motion'
import { useNavigate, useLocation } from 'react-router-dom'
import { useTranslation } from 'react-i18next'
import { Elements } from '@stripe/react-stripe-js'
import {
  MagnifyingGlassIcon,
  MapPinIcon,
  StarIcon,
  ClockIcon,
  CurrencyDollarIcon,
  ChevronRightIcon,
  HeartIcon,
  UserCircleIcon,
  CalendarDaysIcon,
  CreditCardIcon,
  CheckCircleIcon,
  ArrowLeftIcon
} from '@heroicons/react/24/outline'
import { HeartIcon as HeartSolidIcon } from '@heroicons/react/24/solid'
import { bookingService, type Stylist, type StylistService, type BookingRequest } from '../services/bookingService'
import { useAuthStore } from '../store/authStore'
import { stripePromise, paymentService } from '../lib/stripe'
import PaymentForm from '../components/PaymentForm'
import toast from 'react-hot-toast'

type BookingStep = 'stylist-selection' | 'service-selection' | 'datetime-selection' | 'payment' | 'confirmation'

export default function BookingPage() {
  const { t } = useTranslation()
  const navigate = useNavigate()
  const location = useLocation()
  const { user, isAuthenticated } = useAuthStore()
  const [currentStep, setCurrentStep] = useState<BookingStep>('stylist-selection')
  const [stylists, setStylists] = useState<Stylist[]>([])
  const [services, setServices] = useState<StylistService[]>([])
  const [loading, setLoading] = useState(true)
  const [searchQuery, setSearchQuery] = useState('')
  const [selectedStylist, setSelectedStylist] = useState<Stylist | null>(null)
  const [selectedService, setSelectedService] = useState<StylistService | null>(null)
  const [selectedDate, setSelectedDate] = useState('')
  const [selectedTime, setSelectedTime] = useState('')
  const [availableSlots, setAvailableSlots] = useState<string[]>([])
  const [bookingNotes, setBookingNotes] = useState('')
  const [favorites, setFavorites] = useState<Set<string>>(new Set())
  const [isProcessing, setIsProcessing] = useState(false)
  const [paymentIntentId, setPaymentIntentId] = useState<string | null>(null)
  const [clientSecret, setClientSecret] = useState<string | null>(null)
  const [finalBooking, setFinalBooking] = useState(null)

  useEffect(() => {
    if (!isAuthenticated) {
      navigate('/login')
      return
    }

    // Check if we came from a stylist profile page
    if (location.state?.stylistId) {
      loadStylistForBooking(location.state.stylistId)
      if (location.state.serviceId) {
        setCurrentStep('datetime-selection')
      } else {
        setCurrentStep('service-selection')
      }
    } else {
      loadStylists()
    }
  }, [isAuthenticated, navigate, location.state])

  const loadStylists = async () => {
    try {
      setLoading(true)
      const response = await bookingService.getStylists()

      if (response.success && response.data) {
        setStylists(response.data)
      } else {
        toast.error(t('booking.page.loadStylistsError'))
      }
    } catch (error) {
      console.error('Error loading stylists:', error)
      toast.error(t('booking.page.loadStylistsError'))
    } finally {
      setLoading(false)
    }
  }

  const filteredStylists = stylists.filter(stylist => {
    const searchTerm = searchQuery.toLowerCase()
    return (
      stylist.business_name.toLowerCase().includes(searchTerm) ||
      stylist.location_city.toLowerCase().includes(searchTerm) ||
      stylist.specialties.some(specialty => specialty.toLowerCase().includes(searchTerm))
    )
  })

  const toggleFavorite = (stylistId: string) => {
    setFavorites(prev => {
      const newFavorites = new Set(prev)
      if (newFavorites.has(stylistId)) {
        newFavorites.delete(stylistId)
        toast.success('Eliminado de favoritos')
      } else {
        newFavorites.add(stylistId)
        toast.success('Agregado a favoritos')
      }
      return newFavorites
    })
  }

  const loadStylistForBooking = async (stylistId: string) => {
    try {
      setLoading(true)
      const [stylistResponse, servicesResponse] = await Promise.all([
        bookingService.getStylist(stylistId),
        bookingService.getStylistServices(stylistId)
      ])

      if (stylistResponse.success && stylistResponse.data) {
        setSelectedStylist(stylistResponse.data)
      }

      if (servicesResponse.success && servicesResponse.data) {
        setServices(servicesResponse.data)
      }
    } catch (error) {
      console.error('Error loading stylist for booking:', error)
      toast.error(t('booking.page.loadStylistError'))
    } finally {
      setLoading(false)
    }
  }

  const selectStylist = async (stylist: Stylist) => {
    try {
      setSelectedStylist(stylist)
      setLoading(true)

      const response = await bookingService.getStylistServices(stylist.id)
      if (response.success && response.data) {
        setServices(response.data)
        setCurrentStep('service-selection')
      } else {
        toast.error('Error al cargar servicios del estilista')
      }
    } catch (error) {
      console.error('Error loading stylist services:', error)
      toast.error('Error al cargar servicios')
    } finally {
      setLoading(false)
    }
  }

  const selectService = (service: StylistService) => {
    setSelectedService(service)
    setCurrentStep('datetime-selection')
  }

  const loadAvailableSlots = async (date: string) => {
    if (!selectedStylist) return

    try {
      const response = await bookingService.getAvailableSlots(selectedStylist.id, date)
      if (response.success && response.data) {
        setAvailableSlots(response.data)
      } else {
        setAvailableSlots([])
        toast.error('No hay horarios disponibles para esta fecha')
      }
    } catch (error) {
      console.error('Error loading available slots:', error)
      setAvailableSlots([])
      toast.error('Error al cargar horarios disponibles')
    }
  }

  const selectDateTime = (date: string, time: string) => {
    setSelectedDate(date)
    setSelectedTime(time)
    setCurrentStep('payment')
  }

  const createPaymentIntent = async () => {
    if (!selectedStylist || !selectedService || !selectedDate || !selectedTime) {
      toast.error(t('booking.page.incompleteInfo'))
      return
    }

    try {
      const totalPrice = selectedService.custom_price || selectedService.service.price
      const platformFee = Math.round(totalPrice * 0.029 * 100) / 100
      const finalTotal = totalPrice + platformFee

      const paymentData = await paymentService.createPaymentIntent(finalTotal, {
        stylistId: selectedStylist.id,
        serviceId: selectedService.id,
        appointmentDate: selectedDate,
        appointmentTime: selectedTime
      })

      setPaymentIntentId(paymentData.paymentIntentId)
      setClientSecret(paymentData.clientSecret)
    } catch (error) {
      console.error('Error creating payment intent:', error)
      toast.error('Error al preparar el pago')
    }
  }

  const handlePaymentSuccess = async (paymentIntent: any) => {
    setIsProcessing(true)
    try {
      const bookingData = {
        stylist_id: selectedStylist!.id,
        service_id: selectedService!.id,
        appointment_date: selectedDate,
        appointment_time: selectedTime,
        notes: bookingNotes
      }

      const result = await paymentService.confirmPayment(paymentIntent.id, bookingData)

      if (result.success) {
        setFinalBooking(result.booking)
        setCurrentStep('confirmation')
        toast.success(t('booking.page.paymentSuccess'))
      } else {
        toast.error('Error al confirmar la reserva')
      }
    } catch (error) {
      console.error('Error confirming booking:', error)
      toast.error('Error al procesar la confirmacin')
    } finally {
      setIsProcessing(false)
    }
  }

  const handlePaymentError = (error: string) => {
    toast.error(error)
    setIsProcessing(false)
  }

  // Create payment intent when entering payment step
  useEffect(() => {
    if (currentStep === 'payment' && !clientSecret) {
      createPaymentIntent()
    }
  }, [currentStep])

  const renderStepIndicator = () => {
    const steps = [
      { key: 'stylist-selection', label: t('booking.page.stylistLabel'), number: 1 },
      { key: 'service-selection', label: 'Servicio', number: 2 },
      { key: 'datetime-selection', label: 'Fecha y Hora', number: 3 },
      { key: 'payment', label: 'Pago', number: 4 },
      { key: 'confirmation', label: 'Confirmacin', number: 5 }
    ]

    return (
      <div className="flex items-center justify-center mb-8">
        {steps.map((step, index) => {
          const isActive = currentStep === step.key
          const isCompleted = steps.findIndex(s => s.key === currentStep) > index

          return (
            <div key={step.key} className="flex items-center">
              <div className={`flex items-center justify-center w-8 h-8 rounded-full text-sm font-medium ${
                isActive
                  ? 'bg-primary-600 text-white'
                  : isCompleted
                  ? 'bg-green-500 text-white'
                  : 'bg-gray-200 text-gray-600'
              }`}>
                {step.number}
              </div>
              <span className={`ml-2 text-sm font-medium ${
                isActive ? 'text-primary-600' : 'text-gray-500'
              }`}>
                {step.label}
              </span>
              {index < steps.length - 1 && (
                <ChevronRightIcon className="w-4 h-4 text-gray-400 mx-3" />
              )}
            </div>
          )
        })}
      </div>
    )
  }

  const renderStylistSelection = () => {
    if (loading) {
      return (
        <div className="flex items-center justify-center py-12">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-600"></div>
        </div>
      )
    }

    return (
      <div className="space-y-6">
        {/* Search Bar */}
        <div className="relative">
          <MagnifyingGlassIcon className="absolute left-3 top-1/2 transform -translate-y-1/2 h-5 w-5 text-gray-400" />
          <input
            type="text"
            placeholder="Buscar por nombre, ciudad o especialidad..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="w-full pl-10 pr-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
          />
        </div>

        {/* Stylists Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {filteredStylists.map((stylist) => (
            <motion.div
              key={stylist.id}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              className="card hover:shadow-lg transition-shadow duration-200 cursor-pointer"
              onClick={() => selectStylist(stylist)}
            >
              <div className="card-body">
                {/* Header */}
                <div className="flex items-start justify-between mb-4">
                  <div className="flex items-center space-x-3">
                    {stylist.profile_picture_url ? (
                      <img
                        src={stylist.profile_picture_url}
                        alt={stylist.business_name}
                        className="w-12 h-12 rounded-full object-cover"
                      />
                    ) : (
                      <div className="w-12 h-12 bg-primary-100 rounded-full flex items-center justify-center">
                        <UserCircleIcon className="w-8 h-8 text-primary-600" />
                      </div>
                    )}
                    <div>
                      <h3 className="font-semibold text-gray-900">{stylist.business_name}</h3>
                      <div className="flex items-center space-x-2 text-sm">
                        <div className="flex items-center">
                          <StarIcon className="w-4 h-4 text-yellow-400 fill-current" />
                          <span className="text-gray-600 ml-1">{parseFloat(stylist.rating_average.toString()).toFixed(1)}</span>
                        </div>
                        <span className="text-gray-400"></span>
                        <span className="text-gray-600">{stylist.rating_count} reseas</span>
                      </div>
                    </div>
                  </div>
                  <button
                    onClick={(e) => {
                      e.stopPropagation()
                      toggleFavorite(stylist.id)
                    }}
                    className="p-1 rounded-full hover:bg-gray-100 transition-colors"
                  >
                    {favorites.has(stylist.id) ? (
                      <HeartSolidIcon className="w-5 h-5 text-red-500" />
                    ) : (
                      <HeartIcon className="w-5 h-5 text-gray-400" />
                    )}
                  </button>
                </div>

                {/* Location */}
                <div className="flex items-center text-sm text-gray-600 mb-3">
                  <MapPinIcon className="w-4 h-4 mr-1" />
                  <span>{stylist.location_city}, {stylist.location_state}</span>
                </div>

                {/* Specialties */}
                <div className="flex flex-wrap gap-1 mb-4">
                  {stylist.specialties.slice(0, 3).map((specialty) => (
                    <span
                      key={specialty}
                      className="inline-flex px-2 py-1 text-xs font-medium bg-primary-100 text-primary-800 rounded-full"
                    >
                      {specialty}
                    </span>
                  ))}
                  {stylist.specialties.length > 3 && (
                    <span className="inline-flex px-2 py-1 text-xs font-medium bg-gray-100 text-gray-600 rounded-full">
                      +{stylist.specialties.length - 3} ms
                    </span>
                  )}
                </div>

                {/* Experience */}
                <div className="flex items-center justify-between text-sm">
                  <div className="flex items-center text-gray-600">
                    <ClockIcon className="w-4 h-4 mr-1" />
                    <span>{stylist.experience_years} aos de experiencia</span>
                  </div>
                  <div className="text-primary-600 font-medium">
                    Ver servicios 
                  </div>
                </div>
              </div>
            </motion.div>
          ))}
        </div>

        {filteredStylists.length === 0 && (
          <div className="text-center py-12">
            <MagnifyingGlassIcon className="w-12 h-12 text-gray-400 mx-auto mb-3" />
            <p className="text-gray-500 mb-2">No se encontraron estilistas</p>
            <p className="text-sm text-gray-400">
              Intenta con otros trminos de bsqueda
            </p>
          </div>
        )}
      </div>
    )
  }

  const renderServiceSelection = () => {
    if (loading) {
      return (
        <div className="flex items-center justify-center py-12">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-600"></div>
        </div>
      )
    }

    return (
      <div className="space-y-6">
        <div className="flex items-center justify-between">
          <div>
            <h2 className="text-2xl font-semibold text-gray-900 mb-2">
              Servicios de {selectedStylist?.business_name}
            </h2>
            <p className="text-gray-600">
              Selecciona el servicio que deseas reservar
            </p>
          </div>
          <button
            onClick={() => setCurrentStep('stylist-selection')}
            className="btn btn-outline-primary"
          >
            <ArrowLeftIcon className="w-4 h-4 mr-2" />
            Cambiar estilista
          </button>
        </div>

        {services.length > 0 ? (
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            {services.map((service) => (
              <motion.div
                key={service.id}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                className="card hover:shadow-lg transition-shadow duration-200 cursor-pointer"
                onClick={() => selectService(service)}
              >
                <div className="card-body">
                  <div className="flex justify-between items-start mb-4">
                    <div>
                      <h4 className="font-semibold text-gray-900 text-lg mb-2">
                        {service.custom_name || service.service.name}
                      </h4>
                      <p className="text-gray-600 text-sm mb-3">
                        {service.custom_description || service.service.description}
                      </p>
                    </div>
                  </div>
                  <div className="flex items-center justify-between">
                    <div className="flex items-center space-x-4 text-sm text-gray-500">
                      <div className="flex items-center">
                        <ClockIcon className="w-4 h-4 mr-1" />
                        <span>{service.service.duration} min</span>
                      </div>
                      <div className="flex items-center">
                        <CurrencyDollarIcon className="w-4 h-4 mr-1" />
                        <span>${service.custom_price || service.service.price}</span>
                      </div>
                    </div>
                    <div className="text-primary-600 font-medium">
                      Seleccionar 
                    </div>
                  </div>
                </div>
              </motion.div>
            ))}
          </div>
        ) : (
          <div className="text-center py-12">
            <CurrencyDollarIcon className="w-12 h-12 text-gray-400 mx-auto mb-3" />
            <p className="text-gray-500 mb-2">No hay servicios disponibles</p>
            <p className="text-sm text-gray-400 mb-4">
              Este estilista an no ha publicado sus servicios
            </p>
            <button
              onClick={() => setCurrentStep('stylist-selection')}
              className="btn btn-primary"
            >
              Volver a estilistas
            </button>
          </div>
        )}
      </div>
    )
  }

  const renderDateTimeSelection = () => {
    const today = new Date()
    const dates = []

    // Generate next 14 days
    for (let i = 0; i < 14; i++) {
      const date = new Date(today)
      date.setDate(today.getDate() + i)
      dates.push(date)
    }

    return (
      <div className="space-y-6">
        <div className="flex items-center justify-between">
          <div>
            <h2 className="text-2xl font-semibold text-gray-900 mb-2">
              Selecciona Fecha y Hora
            </h2>
            <p className="text-gray-600">
              {selectedService?.custom_name || selectedService?.service.name} con {selectedStylist?.business_name}
            </p>
          </div>
          <button
            onClick={() => setCurrentStep('service-selection')}
            className="btn btn-outline-primary"
          >
            <ArrowLeftIcon className="w-4 h-4 mr-2" />
            Cambiar servicio
          </button>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
          {/* Date Selection */}
          <div>
            <h3 className="font-semibold text-gray-900 mb-4">Selecciona una fecha</h3>
            <div className="grid grid-cols-2 gap-3">
              {dates.map((date) => {
                const dateStr = date.toISOString().split('T')[0]
                const isSelected = selectedDate === dateStr
                const isToday = date.toDateString() === today.toDateString()

                return (
                  <button
                    key={dateStr}
                    onClick={() => {
                      setSelectedDate(dateStr)
                      setSelectedTime('')
                      loadAvailableSlots(dateStr)
                    }}
                    className={`p-3 text-left rounded-lg border transition-colors ${
                      isSelected
                        ? 'border-primary-500 bg-primary-50 text-primary-700'
                        : 'border-gray-300 hover:border-gray-400 hover:bg-gray-50'
                    }`}
                  >
                    <div className="font-medium">
                      {isToday ? 'Hoy' : date.toLocaleDateString('es-MX', { weekday: 'short' })}
                    </div>
                    <div className="text-sm text-gray-600">
                      {date.toLocaleDateString('es-MX', { month: 'short', day: 'numeric' })}
                    </div>
                  </button>
                )
              })}
            </div>
          </div>

          {/* Time Selection */}
          <div>
            <h3 className="font-semibold text-gray-900 mb-4">
              {selectedDate ? 'Horarios disponibles' : 'Primero selecciona una fecha'}
            </h3>
            {selectedDate ? (
              <div className="grid grid-cols-3 gap-2">
                {availableSlots.length > 0 ? (
                  availableSlots.map((time) => (
                    <button
                      key={time}
                      onClick={() => setSelectedTime(time)}
                      className={`p-2 text-sm rounded-lg border transition-colors ${
                        selectedTime === time
                          ? 'border-primary-500 bg-primary-500 text-white'
                          : 'border-gray-300 hover:border-primary-300 hover:bg-primary-50'
                      }`}
                    >
                      {time}
                    </button>
                  ))
                ) : (
                  <div className="col-span-3 text-center py-8 text-gray-500">
                    No hay horarios disponibles para esta fecha
                  </div>
                )}
              </div>
            ) : (
              <div className="text-center py-8 text-gray-400">
                <CalendarDaysIcon className="w-12 h-12 mx-auto mb-2" />
                <p>Selecciona una fecha para ver horarios</p>
              </div>
            )}
          </div>
        </div>

        {/* Booking Notes */}
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">
            Notas adicionales (opcional)
          </label>
          <textarea
            value={bookingNotes}
            onChange={(e) => setBookingNotes(e.target.value)}
            placeholder={t('booking.page.specialNotes')}
            rows={3}
            className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
          />
        </div>

        {/* Continue Button */}
        {selectedDate && selectedTime && (
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            className="flex justify-end"
          >
            <button
              onClick={() => setCurrentStep('payment')}
              className="btn btn-primary btn-lg"
            >
              Continuar a Pago
              <ChevronRightIcon className="w-4 h-4 ml-2" />
            </button>
          </motion.div>
        )}
      </div>
    )
  }

  const renderPayment = () => {
    const totalPrice = selectedService?.custom_price || selectedService?.service.price || 0
    const platformFee = Math.round(totalPrice * 0.029 * 100) / 100
    const finalTotal = totalPrice + platformFee

    return (
      <div className="space-y-6">
        <div className="flex items-center justify-between">
          <div>
            <h2 className="text-2xl font-semibold text-gray-900 mb-2">
              Confirmar y Pagar
            </h2>
            <p className="text-gray-600">
              Revisa los detalles de tu reserva antes de pagar
            </p>
          </div>
          <button
            onClick={() => setCurrentStep('datetime-selection')}
            className="btn btn-outline-primary"
          >
            <ArrowLeftIcon className="w-4 h-4 mr-2" />
            Cambiar horario
          </button>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Booking Summary */}
          <div className="lg:col-span-2">
            <div className="card">
              <div className="card-body">
                <h3 className="font-semibold text-gray-900 mb-4">Resumen de la Reserva</h3>

                <div className="space-y-4">
                  <div className="flex items-start space-x-4">
                    {selectedStylist?.profile_picture_url ? (
                      <img
                        src={selectedStylist.profile_picture_url}
                        alt={selectedStylist.business_name}
                        className="w-16 h-16 rounded-lg object-cover"
                      />
                    ) : (
                      <div className="w-16 h-16 bg-primary-100 rounded-lg flex items-center justify-center">
                        <UserCircleIcon className="w-10 h-10 text-primary-600" />
                      </div>
                    )}
                    <div>
                      <h4 className="font-medium text-gray-900">{selectedStylist?.business_name}</h4>
                      <p className="text-sm text-gray-600">{selectedStylist?.name}</p>
                      <div className="flex items-center mt-1">
                        <MapPinIcon className="w-4 h-4 text-gray-400 mr-1" />
                        <span className="text-sm text-gray-600">
                          {selectedStylist?.location_city}, {selectedStylist?.location_state}
                        </span>
                      </div>
                    </div>
                  </div>

                  <div className="border-t pt-4">
                    <div className="flex justify-between items-start">
                      <div>
                        <h4 className="font-medium text-gray-900">
                          {selectedService?.custom_name || selectedService?.service.name}
                        </h4>
                        <p className="text-sm text-gray-600 mt-1">
                          {selectedService?.custom_description || selectedService?.service.description}
                        </p>
                        <div className="flex items-center mt-2 space-x-4 text-sm text-gray-500">
                          <div className="flex items-center">
                            <ClockIcon className="w-4 h-4 mr-1" />
                            <span>{selectedService?.service.duration} min</span>
                          </div>
                          <div className="flex items-center">
                            <CalendarDaysIcon className="w-4 h-4 mr-1" />
                            <span>{selectedDate} a las {selectedTime}</span>
                          </div>
                        </div>
                      </div>
                      <div className="text-right">
                        <div className="font-semibold text-gray-900">${totalPrice}</div>
                      </div>
                    </div>
                  </div>

                  {bookingNotes && (
                    <div className="border-t pt-4">
                      <h5 className="font-medium text-gray-700 mb-2">Notas</h5>
                      <p className="text-sm text-gray-600">{bookingNotes}</p>
                    </div>
                  )}
                </div>
              </div>
            </div>

            {/* Stripe Payment Form */}
            <div className="mt-6">
              {clientSecret ? (
                <Elements stripe={stripePromise} options={{ clientSecret }}>
                  <PaymentForm
                    totalAmount={finalTotal}
                    onPaymentSuccess={handlePaymentSuccess}
                    onPaymentError={handlePaymentError}
                    isProcessing={isProcessing}
                  />
                </Elements>
              ) : (
                <div className="card">
                  <div className="card-body">
                    <div className="flex items-center justify-center py-8">
                      <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary-600 mr-3" />
                      <span className="text-gray-600">Preparando formulario de pago...</span>
                    </div>
                  </div>
                </div>
              )}
            </div>
          </div>

          {/* Price Summary */}
          <div>
            <div className="card sticky top-4">
              <div className="card-body">
                <h3 className="font-semibold text-gray-900 mb-4">Resumen de Precio</h3>

                <div className="space-y-3">
                  <div className="flex justify-between">
                    <span className="text-gray-600">Servicio</span>
                    <span className="font-medium">${totalPrice}</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-gray-600">Tarifa de plataforma</span>
                    <span className="font-medium">${platformFee}</span>
                  </div>
                  <div className="border-t pt-3">
                    <div className="flex justify-between">
                      <span className="font-semibold text-gray-900">Total</span>
                      <span className="font-semibold text-primary-600">${finalTotal}</span>
                    </div>
                  </div>
                </div>

                <div className="mt-6">
                  <p className="text-xs text-gray-500 text-center">
                    Al completar tu reserva, aceptas nuestros trminos y condiciones
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }

  const renderConfirmation = () => {
    return (
      <div className="max-w-2xl mx-auto text-center">
        <motion.div
          initial={{ scale: 0.8, opacity: 0 }}
          animate={{ scale: 1, opacity: 1 }}
          transition={{ duration: 0.5 }}
        >
          <div className="w-20 h-20 bg-green-100 rounded-full flex items-center justify-center mx-auto mb-6">
            <CheckCircleIcon className="w-12 h-12 text-green-600" />
          </div>

          <h1 className="text-3xl font-bold text-gray-900 mb-4">
            Reserva Confirmada!
          </h1>

          <p className="text-gray-600 mb-8">
            Tu cita ha sido reservada exitosamente. Recibirs una confirmacin por SMS y email.
          </p>

          <div className="bg-gray-50 rounded-lg p-6 mb-8">
            <h3 className="font-semibold text-gray-900 mb-4">Detalles de tu cita</h3>
            <div className="space-y-2 text-left">
              <div className="flex justify-between">
                <span className="text-gray-600">Estilista:</span>
                <span className="font-medium">{finalBooking?.business_name || selectedStylist?.business_name}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-600">Servicio:</span>
                <span className="font-medium">{finalBooking?.service_name || selectedService?.custom_name || selectedService?.service.name}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-600">Fecha:</span>
                <span className="font-medium">{finalBooking?.appointment_date || selectedDate}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-600">Hora:</span>
                <span className="font-medium">{finalBooking?.appointment_time || selectedTime}</span>
              </div>
              {paymentIntentId && (
                <div className="flex justify-between">
                  <span className="text-gray-600">ID de Pago:</span>
                  <span className="font-mono text-sm">{paymentIntentId}</span>
                </div>
              )}
            </div>
          </div>

          <div className="flex flex-col sm:flex-row gap-4 justify-center">
            <button
              onClick={() => navigate('/dashboard')}
              className="btn btn-primary"
            >
              Ver mis reservas
            </button>
            <button
              onClick={() => {
                setCurrentStep('stylist-selection')
                setSelectedStylist(null)
                setSelectedService(null)
                setSelectedDate('')
                setSelectedTime('')
                setBookingNotes('')
              }}
              className="btn btn-outline-primary"
            >
              Hacer otra reserva
            </button>
          </div>
        </motion.div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="container-responsive py-8">
        {/* Header */}
        <div className="mb-8">
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5 }}
          >
            <h1 className="text-3xl font-serif font-bold text-gray-900 mb-2">
              Reservar Cita
            </h1>
            <p className="text-gray-600">
              Encuentra y reserva con los mejores estilistas de Puerto Vallarta
            </p>
          </motion.div>
        </div>

        {/* Step Indicator */}
        {renderStepIndicator()}

        {/* Content */}
        <motion.div
          key={currentStep}
          initial={{ opacity: 0, x: 20 }}
          animate={{ opacity: 1, x: 0 }}
          transition={{ duration: 0.3 }}
        >
          {currentStep === 'stylist-selection' && renderStylistSelection()}
          {currentStep === 'service-selection' && renderServiceSelection()}
          {currentStep === 'datetime-selection' && renderDateTimeSelection()}
          {currentStep === 'payment' && renderPayment()}
          {currentStep === 'confirmation' && renderConfirmation()}
        </motion.div>
      </div>
    </div>
  )
}