import { useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import { motion, AnimatePresence } from 'framer-motion'
import { useForm } from 'react-hook-form'
import { useTranslation } from 'react-i18next'
import {
  EyeIcon,
  EyeSlashIcon,
  SparklesIcon,
  ArrowRightIcon,
  ArrowLeftIcon,
  CheckCircleIcon,
  MapPinIcon,
  BellIcon,
  HeartIcon,
  CurrencyDollarIcon
} from '@heroicons/react/24/outline'
import { useAuthStore } from '../../store/authStore'
import PhoneInput from '../../components/ui/PhoneInput'
import GoogleSignInButton from '../../components/auth/GoogleSignInButton'
import LocationPicker from '../../components/ui/LocationPicker'
import type { RegisterForm } from '../../types'

interface ClientRegistrationData extends RegisterForm {
  // Booking Preferences
  favoriteServices?: string[]
  priceRangeMin?: number
  priceRangeMax?: number
  preferredDistance?: number
  locationAddress?: string
  locationCity?: string
  locationState?: string
  locationZip?: string
  // Notification Preferences
  emailNotifications?: boolean
  smsNotifications?: boolean
  appointmentReminders?: boolean
  promotionalEmails?: boolean
}

const serviceCategories = [
  { id: 'hair', name: 'Hair', emoji: '' },
  { id: 'nails', name: 'Nails', emoji: '' },
  { id: 'skincare', name: 'Skincare', emoji: '' },
  { id: 'makeup', name: 'Makeup', emoji: '' },
  { id: 'lashes-brows', name: 'Lashes & Brows', emoji: '' },
  { id: 'massages', name: 'Massages', emoji: '' },
  { id: 'waxing', name: 'Waxing', emoji: '' },
  { id: 'special-occasions', name: 'Special Occasions', emoji: '' },
]

export default function ClientRegisterPage() {
  const [step, setStep] = useState(1)
  const [showPassword, setShowPassword] = useState(false)
  const [phoneValue, setPhoneValue] = useState('')
  const [countryCode, setCountryCode] = useState('US')
  const [selectedServices, setSelectedServices] = useState<string[]>([])
  const [priceRange, setPriceRange] = useState({ min: 0, max: 500 })
  const [preferredDistance, setPreferredDistance] = useState(20)
  const { register: registerUser, isLoading } = useAuthStore()
  const navigate = useNavigate()
  const { t } = useTranslation()

  const {
    register,
    handleSubmit,
    setValue,
    getValues,
    formState: { errors },
  } = useForm<ClientRegistrationData>({
    defaultValues: {
      emailNotifications: true,
      smsNotifications: true,
      appointmentReminders: true,
      promotionalEmails: false,
    }
  })

  const handlePhoneChange = (phone: string, country: string) => {
    setPhoneValue(phone)
    setCountryCode(country)
    setValue('phone', phone)
    setValue('countryCode', country)
  }

  const handleLocationSelect = (location: any) => {
    setValue('locationAddress', location.address)
    setValue('locationCity', location.city)
    setValue('locationState', location.state)
    setValue('locationZip', location.zipCode)
  }

  const toggleService = (serviceId: string) => {
    setSelectedServices(prev =>
      prev.includes(serviceId)
        ? prev.filter(id => id !== serviceId)
        : [...prev, serviceId]
    )
  }

  const onSubmit = async (data: ClientRegistrationData) => {
    const registrationData = {
      ...data,
      role: 'CLIENT',
      favoriteServices: selectedServices,
      priceRangeMin: priceRange.min,
      priceRangeMax: priceRange.max,
      preferredDistance: preferredDistance
    }
    const success = await registerUser(registrationData)
    if (success) {
      navigate('/login', {
        state: { message: t('auth.messages.registerSuccess') }
      })
    }
  }

  const nextStep = () => {
    setStep(prev => Math.min(prev + 1, 3))
  }

  const prevStep = () => {
    setStep(prev => Math.max(prev - 1, 1))
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-pink-50 via-purple-50 to-blue-50 flex items-center justify-center py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-2xl w-full space-y-8">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
          className="text-center"
        >
          <Link to="/" className="inline-flex items-center space-x-2 mb-8">
            <SparklesIcon className="h-10 w-10 text-primary-600" />
            <span className="text-2xl font-serif font-bold gradient-text">
              BeautyCita
            </span>
          </Link>

          <h2 className="text-3xl font-serif font-bold text-gray-900 mb-2">
            {t('auth.registerTitle')}
          </h2>
          <p className="text-gray-600">
            Step {step} of 3: {step === 1 ? 'Account Info' : step === 2 ? 'Preferences' : 'Notifications'}
          </p>

          {/* Progress Bar */}
          <div className="mt-6 max-w-md mx-auto">
            <div className="flex items-center justify-between mb-2">
              {[1, 2, 3].map(i => (
                <div
                  key={i}
                  className={`w-8 h-8 rounded-full flex items-center justify-center text-sm font-bold ${
                    step >= i ? 'bg-primary-600 text-white' : 'bg-gray-200 text-gray-500'
                  }`}
                >
                  {i}
                </div>
              ))}
            </div>
            <div className="h-2 bg-gray-200 rounded-full overflow-hidden">
              <motion.div
                className="h-full bg-gradient-to-r from-primary-600 to-secondary-600"
                initial={{ width: '0%' }}
                animate={{ width: `${(step / 3) * 100}%` }}
                transition={{ duration: 0.3 }}
              />
            </div>
          </div>
        </motion.div>

        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, delay: 0.1 }}
          className="card"
        >
          <div className="card-body">
            <AnimatePresence mode="wait">
              {/* Step 1: Account Information */}
              {step === 1 && (
                <motion.div
                  key="step1"
                  initial={{ opacity: 0, x: 20 }}
                  animate={{ opacity: 1, x: 0 }}
                  exit={{ opacity: 0, x: -20 }}
                >
                  <GoogleSignInButton role="client" text={t('auth.social.signUpWithGoogle')} />

                  <div className="relative my-6">
                    <div className="absolute inset-0 flex items-center">
                      <div className="w-full border-t border-gray-300"></div>
                    </div>
                    <div className="relative flex justify-center text-sm">
                      <span className="px-2 bg-white text-gray-500">{t('auth.social.orDivider')}</span>
                    </div>
                  </div>

                  <div className="space-y-4">
                    <div className="grid grid-cols-2 gap-4">
                      <div className="form-group">
                        <label className="label">First Name *</label>
                        <input
                          {...register('firstName', { required: 'First name is required' })}
                          className={`input ${errors.firstName ? 'input-error' : ''}`}
                          placeholder="John"
                        />
                        {errors.firstName && <p className="form-error">{errors.firstName.message}</p>}
                      </div>

                      <div className="form-group">
                        <label className="label">Last Name *</label>
                        <input
                          {...register('lastName', { required: 'Last name is required' })}
                          className={`input ${errors.lastName ? 'input-error' : ''}`}
                          placeholder="Doe"
                        />
                        {errors.lastName && <p className="form-error">{errors.lastName.message}</p>}
                      </div>
                    </div>

                    <div className="form-group">
                      <label className="label">Email *</label>
                      <input
                        {...register('email', {
                          required: 'Email is required',
                          pattern: { value: /^\S+@\S+$/i, message: 'Invalid email' }
                        })}
                        type="email"
                        className={`input ${errors.email ? 'input-error' : ''}`}
                        placeholder="john@example.com"
                      />
                      {errors.email && <p className="form-error">{errors.email.message}</p>}
                    </div>

                    <div className="form-group">
                      <label className="label">Phone</label>
                      <PhoneInput
                        value={phoneValue}
                        onChange={handlePhoneChange}
                        placeholder="+1 (555) 123-4567"
                      />
                    </div>

                    <div className="form-group">
                      <label className="label">Password *</label>
                      <div className="relative">
                        <input
                          {...register('password', {
                            required: 'Password is required',
                            minLength: { value: 8, message: 'At least 8 characters' }
                          })}
                          type={showPassword ? 'text' : 'password'}
                          className={`input pr-10 ${errors.password ? 'input-error' : ''}`}
                          placeholder=""
                        />
                        <button
                          type="button"
                          onClick={() => setShowPassword(!showPassword)}
                          className="absolute inset-y-0 right-0 pr-3 flex items-center"
                        >
                          {showPassword ? <EyeSlashIcon className="h-5 w-5" /> : <EyeIcon className="h-5 w-5" />}
                        </button>
                      </div>
                      {errors.password && <p className="form-error">{errors.password.message}</p>}
                    </div>

                    <div className="flex items-start space-x-3">
                      <input
                        {...register('acceptTerms', { required: 'You must accept terms' })}
                        type="checkbox"
                        className="h-4 w-4 text-primary-600 rounded mt-1"
                      />
                      <label className="text-sm text-gray-700">
                        I accept the{' '}
                        <Link to="/terms" className="text-primary-600 hover:underline">Terms</Link>
                        {' '}and{' '}
                        <Link to="/privacy" className="text-primary-600 hover:underline">Privacy Policy</Link>
                      </label>
                    </div>
                    {errors.acceptTerms && <p className="form-error">{errors.acceptTerms.message}</p>}
                  </div>

                  <button
                    type="button"
                    onClick={nextStep}
                    className="btn btn-primary w-full mt-6 flex items-center justify-center space-x-2"
                  >
                    <span>Next: Your Preferences</span>
                    <ArrowRightIcon className="h-4 w-4" />
                  </button>
                </motion.div>
              )}

              {/* Step 2: Booking Preferences */}
              {step === 2 && (
                <motion.div
                  key="step2"
                  initial={{ opacity: 0, x: 20 }}
                  animate={{ opacity: 1, x: 0 }}
                  exit={{ opacity: 0, x: -20 }}
                  className="space-y-6"
                >
                  <div>
                    <h3 className="font-bold text-lg mb-3 flex items-center space-x-2">
                      <HeartIcon className="h-6 w-6 text-primary-600" />
                      <span>Favorite Services</span>
                    </h3>
                    <p className="text-sm text-gray-600 mb-4">Select services you're interested in</p>
                    <div className="grid grid-cols-2 md:grid-cols-3 gap-3">
                      {serviceCategories.map(service => (
                        <button
                          key={service.id}
                          type="button"
                          onClick={() => toggleService(service.id)}
                          className={`p-3 rounded-xl border-2 transition-all ${
                            selectedServices.includes(service.id)
                              ? 'border-primary-600 bg-primary-50'
                              : 'border-gray-200 hover:border-primary-300'
                          }`}
                        >
                          <div className="text-2xl mb-1">{service.emoji}</div>
                          <div className="text-xs font-medium">{service.name}</div>
                        </button>
                      ))}
                    </div>
                  </div>

                  <div>
                    <h3 className="font-bold text-lg mb-3 flex items-center space-x-2">
                      <CurrencyDollarIcon className="h-6 w-6 text-primary-600" />
                      <span>Price Range</span>
                    </h3>
                    <div className="space-y-3">
                      <div className="flex items-center space-x-4">
                        <input
                          type="range"
                          min="0"
                          max="1000"
                          value={priceRange.max}
                          onChange={(e) => setPriceRange({ ...priceRange, max: parseInt(e.target.value) })}
                          className="flex-1"
                        />
                        <span className="font-bold text-primary-600">${priceRange.max}</span>
                      </div>
                      <p className="text-sm text-gray-600">Maximum price per service</p>
                    </div>
                  </div>

                  <div>
                    <h3 className="font-bold text-lg mb-3 flex items-center space-x-2">
                      <MapPinIcon className="h-6 w-6 text-primary-600" />
                      <span>Preferred Location</span>
                    </h3>
                    <LocationPicker onLocationSelect={handleLocationSelect} />
                    <div className="mt-3">
                      <label className="text-sm text-gray-700">Maximum distance (miles)</label>
                      <div className="flex items-center space-x-4 mt-2">
                        <input
                          type="range"
                          min="5"
                          max="50"
                          value={preferredDistance}
                          onChange={(e) => setPreferredDistance(parseInt(e.target.value))}
                          className="flex-1"
                        />
                        <span className="font-bold text-primary-600">{preferredDistance} mi</span>
                      </div>
                    </div>
                  </div>

                  <div className="flex space-x-3">
                    <button
                      type="button"
                      onClick={prevStep}
                      className="btn btn-outline flex-1 flex items-center justify-center space-x-2"
                    >
                      <ArrowLeftIcon className="h-4 w-4" />
                      <span>Back</span>
                    </button>
                    <button
                      type="button"
                      onClick={nextStep}
                      className="btn btn-primary flex-1 flex items-center justify-center space-x-2"
                    >
                      <span>Next: Notifications</span>
                      <ArrowRightIcon className="h-4 w-4" />
                    </button>
                  </div>
                </motion.div>
              )}

              {/* Step 3: Notification Preferences */}
              {step === 3 && (
                <motion.div
                  key="step3"
                  initial={{ opacity: 0, x: 20 }}
                  animate={{ opacity: 1, x: 0 }}
                  exit={{ opacity: 0, x: -20 }}
                  className="space-y-6"
                >
                  <div>
                    <h3 className="font-bold text-lg mb-3 flex items-center space-x-2">
                      <BellIcon className="h-6 w-6 text-primary-600" />
                      <span>Notification Preferences</span>
                    </h3>
                    <p className="text-sm text-gray-600 mb-4">Choose how you want to stay updated</p>

                    <div className="space-y-4">
                      <label className="flex items-center justify-between p-4 border rounded-xl cursor-pointer hover:bg-gray-50">
                        <div>
                          <div className="font-medium">Email Notifications</div>
                          <div className="text-sm text-gray-600">Booking confirmations and updates</div>
                        </div>
                        <input
                          {...register('emailNotifications')}
                          type="checkbox"
                          className="h-5 w-5 text-primary-600 rounded"
                        />
                      </label>

                      <label className="flex items-center justify-between p-4 border rounded-xl cursor-pointer hover:bg-gray-50">
                        <div>
                          <div className="font-medium">SMS Notifications</div>
                          <div className="text-sm text-gray-600">Text reminders for appointments</div>
                        </div>
                        <input
                          {...register('smsNotifications')}
                          type="checkbox"
                          className="h-5 w-5 text-primary-600 rounded"
                        />
                      </label>

                      <label className="flex items-center justify-between p-4 border rounded-xl cursor-pointer hover:bg-gray-50">
                        <div>
                          <div className="font-medium">Appointment Reminders</div>
                          <div className="text-sm text-gray-600">24hr before appointments</div>
                        </div>
                        <input
                          {...register('appointmentReminders')}
                          type="checkbox"
                          className="h-5 w-5 text-primary-600 rounded"
                        />
                      </label>

                      <label className="flex items-center justify-between p-4 border rounded-xl cursor-pointer hover:bg-gray-50">
                        <div>
                          <div className="font-medium">Promotional Emails</div>
                          <div className="text-sm text-gray-600">Special offers and deals</div>
                        </div>
                        <input
                          {...register('promotionalEmails')}
                          type="checkbox"
                          className="h-5 w-5 text-primary-600 rounded"
                        />
                      </label>
                    </div>
                  </div>

                  <div className="flex space-x-3">
                    <button
                      type="button"
                      onClick={prevStep}
                      className="btn btn-outline flex-1 flex items-center justify-center space-x-2"
                    >
                      <ArrowLeftIcon className="h-4 w-4" />
                      <span>Back</span>
                    </button>
                    <button
                      onClick={handleSubmit(onSubmit)}
                      disabled={isLoading}
                      className="btn btn-primary flex-1 flex items-center justify-center space-x-2"
                    >
                      {isLoading ? (
                        <div className="loading-spinner" />
                      ) : (
                        <>
                          <CheckCircleIcon className="h-5 w-5" />
                          <span>Create Account</span>
                        </>
                      )}
                    </button>
                  </div>
                </motion.div>
              )}
            </AnimatePresence>

            <div className="mt-6 text-center text-sm text-gray-600">
              Already have an account?{' '}
              <Link to="/login" className="font-medium text-primary-600 hover:text-primary-700">
                Sign in
              </Link>
            </div>
          </div>
        </motion.div>
      </div>
    </div>
  )
}
