import { useState, useEffect } from 'react'
import { motion } from 'framer-motion'
import { Link } from 'react-router-dom'
import {
  MagnifyingGlassIcon,
  MapPinIcon,
  StarIcon,
  ClockIcon,
  HeartIcon,
  UserCircleIcon,
  FunnelIcon,
  AdjustmentsHorizontalIcon
} from '@heroicons/react/24/outline'
import { HeartIcon as HeartSolidIcon } from '@heroicons/react/24/solid'
import { bookingService, type Stylist } from '../services/bookingService'
import toast from 'react-hot-toast'
import LocationPermission from '../components/LocationPermission'
import useGeolocation from '../hooks/useGeolocation'
import { sortByDistance, formatDistance, getDistanceStyle, isValidCoordinates } from '../utils/distance'

const specialtyOptions = [
  'Corte de cabello',
  'Coloracin',
  'Peinados',
  'Manicure',
  'Pedicure',
  'Maquillaje',
  'Cejas',
  'Extensiones',
  'Tratamientos capilares'
]

const sortOptions = [
  { value: 'distance', label: 'Ms cercanos' },
  { value: 'rating', label: 'Mejor calificados' },
  { value: 'reviews', label: 'Ms reseas' },
  { value: 'experience', label: 'Ms experiencia' },
  { value: 'name', label: 'Nombre A-Z' }
]

export default function StylistsPage() {
  const [stylists, setStylists] = useState<Stylist[]>([])
  const [loading, setLoading] = useState(true)
  const [searchQuery, setSearchQuery] = useState('')
  const [selectedSpecialties, setSelectedSpecialties] = useState<string[]>([])
  const [sortBy, setSortBy] = useState('rating')
  const [showFilters, setShowFilters] = useState(false)
  const [favorites, setFavorites] = useState<Set<string>>(new Set())
  const [userLocation, setUserLocation] = useState<{ latitude: number; longitude: number } | null>(null)
  const [showLocationPrompt, setShowLocationPrompt] = useState(true)
  const [isMobile, setIsMobile] = useState(false)

  const { latitude, longitude, permission } = useGeolocation()

  // Check if we're on mobile
  useEffect(() => {
    const checkIsMobile = () => {
      setIsMobile(window.innerWidth < 768 || 'ontouchstart' in window)
    }

    checkIsMobile()
    window.addEventListener('resize', checkIsMobile)

    return () => window.removeEventListener('resize', checkIsMobile)
  }, [])

  // Handle location updates
  useEffect(() => {
    if (isValidCoordinates(latitude, longitude)) {
      setUserLocation({ latitude: latitude!, longitude: longitude! })
      setShowLocationPrompt(false)

      // Auto-sort by distance when location is available on mobile
      if (isMobile && sortBy === 'rating') {
        setSortBy('distance')
      }
    }
  }, [latitude, longitude, isMobile, sortBy])

  useEffect(() => {
    loadStylists()
  }, [])

  const loadStylists = async () => {
    try {
      setLoading(true)
      const response = await bookingService.getStylists()

      if (response.success && response.data) {
        setStylists(response.data)
      } else {
        toast.error('Error al cargar estilistas')
      }
    } catch (error) {
      console.error('Error loading stylists:', error)
      toast.error('Error al cargar estilistas')
    } finally {
      setLoading(false)
    }
  }

  const filteredAndSortedStylists = (() => {
    let filtered = stylists.filter(stylist => {
      const searchTerm = searchQuery.toLowerCase()
      const matchesSearch =
        stylist.business_name.toLowerCase().includes(searchTerm) ||
        stylist.location_city.toLowerCase().includes(searchTerm) ||
        stylist.specialties.some(specialty => specialty.toLowerCase().includes(searchTerm))

      const matchesSpecialties =
        selectedSpecialties.length === 0 ||
        selectedSpecialties.some(specialty => stylist.specialties.includes(specialty))

      return matchesSearch && matchesSpecialties
    })

    // Apply distance sorting if location is available and distance sort is selected
    if (sortBy === 'distance' && userLocation) {
      // Enhance stylists with distance data and sort
      return sortByDistance(filtered, userLocation)
    }

    // Apply regular sorting
    return filtered.sort((a, b) => {
      switch (sortBy) {
        case 'rating':
          return parseFloat(b.rating_average.toString()) - parseFloat(a.rating_average.toString())
        case 'reviews':
          return b.rating_count - a.rating_count
        case 'experience':
          return b.experience_years - a.experience_years
        case 'name':
          return a.business_name.localeCompare(b.business_name)
        default:
          return 0
      }
    })
  })()

  const toggleFavorite = (stylistId: string) => {
    setFavorites(prev => {
      const newFavorites = new Set(prev)
      if (newFavorites.has(stylistId)) {
        newFavorites.delete(stylistId)
        toast.success('Eliminado de favoritos')
      } else {
        newFavorites.add(stylistId)
        toast.success('Agregado a favoritos')
      }
      return newFavorites
    })
  }

  const toggleSpecialty = (specialty: string) => {
    setSelectedSpecialties(prev =>
      prev.includes(specialty)
        ? prev.filter(s => s !== specialty)
        : [...prev, specialty]
    )
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-600"></div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="container-responsive py-8">
        {/* Header */}
        <div className="mb-8">
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5 }}
          >
            <h1 className="text-3xl font-serif font-bold text-gray-900 mb-2">
              Estilistas en Puerto Vallarta
            </h1>
            <p className="text-gray-600">
              Descubre los mejores profesionales de la belleza en tu rea
            </p>
          </motion.div>
        </div>

        {/* Search and Filters */}
        <div className="mb-8 space-y-4">
          {/* Search Bar */}
          <div className="relative">
            <MagnifyingGlassIcon className="absolute left-3 top-1/2 transform -translate-y-1/2 h-5 w-5 text-gray-400" />
            <input
              type="text"
              placeholder="Buscar por nombre, ciudad o especialidad..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="w-full pl-10 pr-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
            />
          </div>

          {/* Filter Bar */}
          <div className="flex items-center justify-between">
            <button
              onClick={() => setShowFilters(!showFilters)}
              className="flex items-center space-x-2 px-4 py-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors"
            >
              <FunnelIcon className="w-4 h-4" />
              <span>Filtros</span>
              {selectedSpecialties.length > 0 && (
                <span className="bg-primary-100 text-primary-800 text-xs px-2 py-1 rounded-full">
                  {selectedSpecialties.length}
                </span>
              )}
            </button>

            <div className="flex items-center space-x-4">
              <span className="text-sm text-gray-600">
                {filteredAndSortedStylists.length} estilistas encontrados
              </span>
              <select
                value={sortBy}
                onChange={(e) => setSortBy(e.target.value)}
                className="border border-gray-300 rounded-lg px-3 py-2 text-sm focus:ring-2 focus:ring-primary-500 focus:border-transparent"
              >
                {sortOptions.map((option) => {
                  // Only show distance option if location is available
                  if (option.value === 'distance' && !userLocation) {
                    return null
                  }
                  return (
                    <option key={option.value} value={option.value}>
                      {option.label}
                    </option>
                  )
                })}
              </select>
            </div>
          </div>

          {/* Filters Panel */}
          {showFilters && (
            <motion.div
              initial={{ opacity: 0, height: 0 }}
              animate={{ opacity: 1, height: 'auto' }}
              exit={{ opacity: 0, height: 0 }}
              className="bg-white border border-gray-200 rounded-lg p-4"
            >
              <h3 className="font-medium text-gray-900 mb-3">Especialidades</h3>
              <div className="flex flex-wrap gap-2">
                {specialtyOptions.map((specialty) => (
                  <button
                    key={specialty}
                    onClick={() => toggleSpecialty(specialty)}
                    className={`px-3 py-1 text-sm rounded-full border transition-colors ${
                      selectedSpecialties.includes(specialty)
                        ? 'bg-primary-100 text-primary-800 border-primary-300'
                        : 'bg-gray-50 text-gray-600 border-gray-300 hover:bg-gray-100'
                    }`}
                  >
                    {specialty}
                  </button>
                ))}
              </div>
              {selectedSpecialties.length > 0 && (
                <button
                  onClick={() => setSelectedSpecialties([])}
                  className="mt-3 text-sm text-primary-600 hover:text-primary-700"
                >
                  Limpiar filtros
                </button>
              )}
            </motion.div>
          )}
        </div>

        {/* Stylists Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {filteredAndSortedStylists.map((stylist) => (
            <motion.div
              key={stylist.id}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              className="card hover:shadow-lg transition-shadow duration-200"
            >
              <div className="card-body">
                {/* Header */}
                <div className="flex items-start justify-between mb-4">
                  <div className="flex items-center space-x-3">
                    {stylist.profile_picture_url ? (
                      <img
                        src={stylist.profile_picture_url}
                        alt={stylist.business_name}
                        className="w-12 h-12 rounded-full object-cover"
                      />
                    ) : (
                      <div className="w-12 h-12 bg-primary-100 rounded-full flex items-center justify-center">
                        <UserCircleIcon className="w-8 h-8 text-primary-600" />
                      </div>
                    )}
                    <div>
                      <h3 className="font-semibold text-gray-900">{stylist.business_name}</h3>
                      <div className="flex items-center space-x-2 text-sm">
                        <div className="flex items-center">
                          <StarIcon className="w-4 h-4 text-yellow-400 fill-current" />
                          <span className="text-gray-600 ml-1">{parseFloat(stylist.rating_average.toString()).toFixed(1)}</span>
                        </div>
                        <span className="text-gray-400"></span>
                        <span className="text-gray-600">{stylist.rating_count} reseas</span>
                      </div>
                    </div>
                  </div>
                  <button
                    onClick={() => toggleFavorite(stylist.id)}
                    className="p-1 rounded-full hover:bg-gray-100 transition-colors"
                  >
                    {favorites.has(stylist.id) ? (
                      <HeartSolidIcon className="w-5 h-5 text-red-500" />
                    ) : (
                      <HeartIcon className="w-5 h-5 text-gray-400" />
                    )}
                  </button>
                </div>

                {/* Location with Distance */}
                <div className="flex items-center justify-between text-sm text-gray-600 mb-3">
                  <div className="flex items-center">
                    <MapPinIcon className="w-4 h-4 mr-1" />
                    <span>{stylist.location_city}, {stylist.location_state}</span>
                  </div>

                  {/* Distance Badge for Mobile */}
                  {isMobile && userLocation && (stylist as any).distance !== undefined && (stylist as any).distance !== Infinity && (
                    <div className={`px-2 py-1 rounded-full text-xs font-medium ${getDistanceStyle((stylist as any).distance).bgColor} ${getDistanceStyle((stylist as any).distance).textColor}`}>
                      <span className="mr-1">{getDistanceStyle((stylist as any).distance).icon}</span>
                      {(stylist as any).formattedDistance || formatDistance((stylist as any).distance)}
                    </div>
                  )}
                </div>

                {/* Bio */}
                {stylist.bio && (
                  <p className="text-sm text-gray-600 mb-3 line-clamp-2">
                    {stylist.bio}
                  </p>
                )}

                {/* Specialties */}
                <div className="flex flex-wrap gap-1 mb-4">
                  {stylist.specialties.slice(0, 3).map((specialty) => (
                    <span
                      key={specialty}
                      className="inline-flex px-2 py-1 text-xs font-medium bg-primary-100 text-primary-800 rounded-full"
                    >
                      {specialty}
                    </span>
                  ))}
                  {stylist.specialties.length > 3 && (
                    <span className="inline-flex px-2 py-1 text-xs font-medium bg-gray-100 text-gray-600 rounded-full">
                      +{stylist.specialties.length - 3} ms
                    </span>
                  )}
                </div>

                {/* Experience & Actions */}
                <div className="flex items-center justify-between">
                  <div className="flex items-center text-sm text-gray-600">
                    <ClockIcon className="w-4 h-4 mr-1" />
                    <span>{stylist.experience_years} aos</span>
                  </div>
                  <div className="flex space-x-2">
                    <Link
                      to={`/stylist/${stylist.id}`}
                      className="btn btn-outline-primary btn-sm"
                    >
                      Ver perfil
                    </Link>
                    <Link
                      to={`/booking?stylist=${stylist.id}`}
                      className="btn btn-primary btn-sm"
                    >
                      Reservar
                    </Link>
                  </div>
                </div>
              </div>
            </motion.div>
          ))}
        </div>

        {/* Empty State */}
        {filteredAndSortedStylists.length === 0 && (
          <div className="text-center py-12">
            <MagnifyingGlassIcon className="w-12 h-12 text-gray-400 mx-auto mb-3" />
            <p className="text-gray-500 mb-2">No se encontraron estilistas</p>
            <p className="text-sm text-gray-400 mb-4">
              Intenta ajustar los filtros de bsqueda
            </p>
            <button
              onClick={() => {
                setSearchQuery('')
                setSelectedSpecialties([])
              }}
              className="btn btn-primary"
            >
              Limpiar filtros
            </button>
          </div>
        )}
      </div>

      {/* Location Permission Component */}
      {isMobile && showLocationPrompt && permission !== 'granted' && (
        <LocationPermission
          onLocationGranted={(location) => {
            setUserLocation(location)
            setShowLocationPrompt(false)

            // Auto-switch to distance sorting when location is granted
            if (sortBy === 'rating') {
              setSortBy('distance')
              toast.success('Ahora puedes ver las distancias a los estilistas')
            }
          }}
          onLocationDenied={() => {
            setShowLocationPrompt(false)
            setUserLocation(null)
          }}
          showOnlyOnMobile={true}
        />
      )}
    </div>
  )
}